<template>
<div>

  <form ref="form" @submit.stop.prevent="handleSubmit">
      <div class="form-group row m-0 mt-3">
          <label class="mx-3" for="projectName">{{$t("service.PRJ_LST_PRJ_NAME")}}:</label>{{projectName}}

          <label class="mx-3" for="textarea-default">{{$t("service.PRJ_LST_DESC")}}:</label>
          <b-form-input class="col-md-5" id="textarea-default" v-model="projectText" :placeholder="$t('service.PRJ_DIAL_NPRJ_HINT_IPT_PRJ_DESC')" required></b-form-input>
      </div>
      <div class="form-group row m-2 mt-3">
          <label class="switch">
              <input type="checkbox" v-model="open" name="switchPort"/>
              <span class="ml-2"></span>
              <h6 class="ml-2 mt-1">{{$t('service.PRJ_DIAL_NPRJ_LBL_ENABLE_PRJ_RSRC_LIMIT')}}</h6>
              <small class="ml-2" style="color:red;">{{$t('service.PRJ_DIAL_NPRJ_HINT_ENABLE_PRJ_RSRC_LIMIT')}}</small>
            </label>
      </div>
      <div v-if="open" class="m-2" >
          <table id="tableResource" class="table">
              <thead class="thead-dark">
                  <tr>
                    <th scope="col">{{$t('service.SVCM_DIAL_NEWNAMESPACE_TABLE_RSRC_TYPE')}}</th>
                    <th scope="col">{{$t('service.SVCM_DIAL_NEWNAMESPACE_TABLE_PRJ_RSRC_LIMIT')}}</th>
                    <th scope="col">{{$t('service.SVCM_DIAL_NEWNAMESPACE_TABLE_NAMESPACE_RSRC_LIMIT')}}</th>
                    <th scope="col">{{$t('service.SVCM_DIAL_NEWNAMESPACE_TABLE_CONTAINER_RSRC_LIMIT')}}</th>
                  </tr>
              </thead>
              <tbody>
                  <tr>
                      <th scope="row">CPU Limit</th>
                      <td>
                          <input v-model="resourceQuota.limit.limitsCpu" type="number" :class="{'form-control':true}" required min="0"/>
                          <label class="mx-2">m</label>
                      </td>
                      <td>
                          <input v-model="namespaceDefaultResourceQuota.limit.limitsCpu" type="number" :class="{'form-control':true}" required min="0"/>
                          <label class="mx-2">m</label>
                      </td>
                      <td>
                          <input v-model="containerDefaultResourceLimit.limitsCpu" type="number" :class="{'form-control':true}" required  min="0"/>
                          <label class="mx-2">m</label>
                      </td>
                  </tr>
                  <tr>
                      <th scope="row">CPU Reservation</th>
                      <td>
                          <input v-model="resourceQuota.limit.requestsCpu" type="number" :class="{'form-control':true}" required min="0"/>
                          <label class="mx-2">m</label>
                      </td>
                      <td>
                          <input v-model="namespaceDefaultResourceQuota.limit.requestsCpu" type="number" :class="{'form-control':true}" required min="0"/>
                          <label class="mx-2">m</label>
                      </td>
                      <td>
                          <input v-model="containerDefaultResourceLimit.requestsCpu" type="number" :class="{'form-control':true}" required  min="0"/>
                          <label class="mx-2">m</label>
                      </td>
                  </tr>
                  <tr>
                      <th scope="row">Memory Limit</th>
                      <td>
                          <input v-model="resourceQuota.limit.limitsMemory" type="number" :class="{'form-control':true}" required min="0"/>
                          <label class="mx-2">Mi</label>
                      </td>
                      <td>
                          <input v-model="namespaceDefaultResourceQuota.limit.limitsMemory" type="number" :class="{'form-control':true}" required min="0"/>
                          <label class="mx-2">Mi</label>
                      </td>
                      <td>
                          <input v-model="containerDefaultResourceLimit.limitsMemory" type="number" :class="{'form-control':true}" required  min="0"/>
                          <label class="mx-2">Mi</label>
                      </td>
                  </tr>
                  <tr>
                      <th scope="row">Memory Reservation</th>
                      <td>
                          <input v-model="resourceQuota.limit.requestsMemory" type="number" :class="{'form-control':true}" required min="0"/>
                          <label class="mx-2">Mi</label>
                      </td>
                      <td>
                          <input v-model="namespaceDefaultResourceQuota.limit.requestsMemory" type="number" :class="{'form-control':true}" required min="0"/>
                          <label class="mx-2">Mi</label>
                      </td>
                      <td>
                          <input v-model="containerDefaultResourceLimit.requestsMemory" type="number" :class="{'form-control':true}" required  min="0"/>
                          <label class="mx-2">Mi</label>
                      </td>
                  </tr>
              </tbody>
          </table>
      </div>
  </form>   

  <div class="modal-footer mt-4">
    <b-button size="sm" variant="success" @click="okk()">{{$t("service.PRJ_DIAL_NPRJ_BTN_SUBMIT")}}</b-button>
    <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.PRJ_DIAL_NPRJ_BTN_CANCEL")}}</b-button>
  </div> 

</div>
</template> 

<script>
import Vue from 'vue';
export default {
  props:['strProjectName'],
  data(){
    return{

      projectName: "",
      projectNameState: null,
      projectText: "",
      projectTextState: null,

      open: false,

      resourceQuota: {
        limit: {
          limitsCpu: 1024,
          requestsCpu: 1024,
          limitsMemory: 2048,
          requestsMemory: 2048
        }
      },

      namespaceDefaultResourceQuota: {
        limit: {
          limitsCpu: 512,
          requestsCpu: 512,
          limitsMemory: 1024,
          requestsMemory: 1024
        }
      },

      containerDefaultResourceLimit: {
        limitsCpu: 200,
        requestsCpu: 200,
        limitsMemory: 256,
        requestsMemory: 256
      },
    }
  },

  computed: {

  },

  created(){
    backendapi.get(`/project/${this.strProjectName}`)
      .then(response => {
        this.projectName = response.data.name
        this.projectText = response.data.description
        if(response.data.containerDefaultResourceLimit !== null && response.data.namespaceDefaultResourceQuota !== null && response.data.resourceQuota !== null){
          this.open = true
          this.resourceQuota.limit.limitsCpu = parseInt(response.data.resourceQuota.limit.limitsCpu.replace("m",""))
          this.resourceQuota.limit.requestsCpu = parseInt(response.data.resourceQuota.limit.requestsCpu.replace("m",""))
          this.resourceQuota.limit.limitsMemory = parseInt(response.data.resourceQuota.limit.limitsMemory.replace("Mi",""))
          this.resourceQuota.limit.requestsMemory = parseInt(response.data.resourceQuota.limit.requestsMemory.replace("Mi",""))
      
          this.namespaceDefaultResourceQuota.limit.limitsCpu = parseInt(response.data.namespaceDefaultResourceQuota.limit.limitsCpu.replace("m",""))
          this.namespaceDefaultResourceQuota.limit.requestsCpu = parseInt(response.data.namespaceDefaultResourceQuota.limit.requestsCpu.replace("m",""))
          this.namespaceDefaultResourceQuota.limit.limitsMemory = parseInt(response.data.namespaceDefaultResourceQuota.limit.limitsMemory.replace("Mi",""))
          this.namespaceDefaultResourceQuota.limit.requestsMemory = parseInt(response.data.namespaceDefaultResourceQuota.limit.requestsMemory.replace("Mi",""))
          
          this.containerDefaultResourceLimit.limitsCpu = parseInt(response.data.containerDefaultResourceLimit.limitsCpu.replace("m",""))
          this.containerDefaultResourceLimit.requestsCpu = parseInt(response.data.containerDefaultResourceLimit.requestsCpu.replace("m",""))
          this.containerDefaultResourceLimit.limitsMemory = parseInt(response.data.containerDefaultResourceLimit.limitsMemory.replace("Mi",""))
          this.containerDefaultResourceLimit.requestsMemory = parseInt(response.data.containerDefaultResourceLimit.requestsMemory.replace("Mi",""))
        }else{
          this.open = false
        }
      }).catch(error => {
        alert(error.message);
    });
    this.$emit('stopLoadTable')
  },

  methods:{
    okk(){
      if (this.open == false) {
        this.resourceQuota = null;
        this.namespaceDefaultResourceQuota = null;
        this.containerDefaultResourceLimit = null;
      }
      if (this.open == true) {
        this.resourceQuota.limit.limitsCpu =
          this.resourceQuota.limit.limitsCpu.toString() + "m";
        this.namespaceDefaultResourceQuota.limit.limitsCpu =
          this.namespaceDefaultResourceQuota.limit.limitsCpu.toString() + "m";
        this.containerDefaultResourceLimit.limitsCpu =
          this.containerDefaultResourceLimit.limitsCpu.toString() + "m";
        this.resourceQuota.limit.requestsCpu =
          this.resourceQuota.limit.requestsCpu.toString() + "m";
        this.namespaceDefaultResourceQuota.limit.requestsCpu =
          this.namespaceDefaultResourceQuota.limit.requestsCpu.toString() + "m";
        this.containerDefaultResourceLimit.requestsCpu =
          this.containerDefaultResourceLimit.requestsCpu.toString() + "m";
        this.resourceQuota.limit.limitsMemory =
          this.resourceQuota.limit.limitsMemory.toString() + "Mi";
        this.namespaceDefaultResourceQuota.limit.limitsMemory =
          this.namespaceDefaultResourceQuota.limit.limitsMemory.toString() +
          "Mi";
        this.containerDefaultResourceLimit.limitsMemory =
          this.containerDefaultResourceLimit.limitsMemory.toString() + "Mi";
        this.resourceQuota.limit.requestsMemory =
          this.resourceQuota.limit.requestsMemory.toString() + "Mi";
        this.namespaceDefaultResourceQuota.limit.requestsMemory =
          this.namespaceDefaultResourceQuota.limit.requestsMemory.toString() +
          "Mi";
        this.containerDefaultResourceLimit.requestsMemory =
          this.containerDefaultResourceLimit.requestsMemory.toString() + "Mi";
      }
      this.go2();
    },
    go2(){
      backendapi.put(`/project/${this.strProjectName}`, {
          name: this.projectName,
          description: this.projectText,
          resourceQuota: this.resourceQuota,
          namespaceDefaultResourceQuota: this.namespaceDefaultResourceQuota,
          containerDefaultResourceLimit: this.containerDefaultResourceLimit
        })
        .then(response => {
          this.$emit('loadTable')
        })
        .catch(error => {
          this.$emit('loadTable')
          alert(error.message);
        });
    },
  },
}
</script>
    
<style>
.button {
  margin-top: 35px;
}
.flip-list-move {
  transition: transform 0.5s;
}
.no-move {
  transition: transform 0s;
}
.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}
.list-group {
  min-height: 20px;
}
.list-group-item {
  cursor: move;
}
.list-group-item i {
  cursor: pointer;
}
.list-group .list-group-item{
  cursor: pointer;

}
.list-group .list-group-item:hover{
  cursor: pointer;
  color:#fff;
  background-color: #3f51b5
}
#member .nav-tabs>.nav-item>.nav-link {
  border: none;
}
#member .nav-tabs>.nav-item>.nav-link.active {
  background-color: #3f51b5;
}
#member .nav-tabs>.nav-item>.nav-link.active, #member .nav-tabs>.nav-item>.nav-link:hover, #member .nav-tabs>.nav-item>.nav-link.active:focus {
    color: #fff;
    background-color: #3f51b5;
}
</style>



