<template>
  <div>
    <!-- 讀取畫面 -->
    <loading :active.sync="isLoading"></loading>

    <div class="content-heading">{{$t("sidebar.SVC_MEM")}}</div>

    <!-- select選項開始 -->
    <b-form class="was-validated">
      <div class="form-group row">
        <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_PRJ")}}:</label>
        <div class="col-md-4">
          <select id="projectlist" class="custom-select custom-select-md mb-3" @change="selectedproject()" v-model="strProject">
            <option value disabled selected>{{$t("option.PROJECT")}}</option>
            <option v-for="(item) in projectlist" :value="item.name" :key="item.name">{{item.name}}</option>
          </select>
        </div>
        <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_NAMESPACE")}}:</label>
        <div class="col-md-4">
          <select id="namespacelist" class="custom-select custom-select-md mb-3" @change="selectednamespace()" :disabled="disabled">
            <option id="myselected" value disabled selected>{{$t("option.NAMESPACE")}}</option>
            <option v-for="item in namespacelist" :value="item.name" :key="item.name">{{item.name}}</option>
          </select>
        </div>
      </div>
    </b-form>
    <!-- select選項結束 -->

    <!-- <b-modal class="modal fade" id="linklink" size="xl" :title="$t('service.SVCM_DIAL_TRACEING_TITLE')" ref="modal" :hide-footer="true">
      <embed width="1120" height="650" src="http://kiali.10.62.164.150.xip.io/console/overview?kiosk=true" frameborder="0" allowfullscreen></embed> 
    </b-modal> -->

    <!-- 新建按鈕開始 -->
    <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" v-show="showNamespace" v-b-modal.createNamespace>{{$t("service.SVCM_BTN_NEW_NAMESPACE")}}</b-btn>
    <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" v-show="showService" v-b-modal.createService>{{$t("service.SVCM_BTN_BTN_NEWSVC")}}</b-btn>
    <a class="mb-1 btn btn-primary" v-show="showService" :href="'http://kiali.10.62.164.150.xip.io/console/graph/namespaces/?edges=requestsPerSecond&graphType=versionedApp&namespaces='+this.strNamespace+'&unusedNodes=true&injectServiceNodes=false&pi=15000&duration=60&layout=dagre'" target="blank" style="padding: .375rem 1rem;">{{$t('common.ICON_TIP_TRACE')}}</a> 

    <br />
    <br />
    <!-- 新建按鈕結束 -->
    
    <ProjectTable :parentName="'Service'" :strProject="strProject" @getPjname="getPjname"  v-show="showProject"/>

    <!-- 新建Namespace開始 -->
    <b-modal class="modal fade" id="createNamespace" size="xl" :title="$t('service.SVCM_DIAL_NEWNAMESPACE_TITLE')" ref="modal" :hide-footer="true">
      <NewNamespace :strProject="strProject" :strNamespace="strNamespace" v-on:childMethod="loadProject()"/>
    </b-modal>
    <!-- 新建Namespace結束 -->

    <!-- 編輯Namespace開始 -->
    <b-modal class="modal fade" id="editNamespace" size="xl" :title="$t('service.SVCM_DIAL_MODIFY_NEWNAMESPACE_TITLE')" ref="modal" :hide-footer="true">
      <EditNamespace :strProject="strProject" :strNamespace="strNamespace" v-on:childMethod="loadProject()"/>
    </b-modal>
    <!-- 編輯Namespace結束 -->

    <!-- 新建服務開始 -->
    <b-modal class="modal fade" id="createService" size="xl" :title="$t('service.SVCM_DIAL_NEWSVC_BASICINFO_TITLE')" ref="modal" :hide-footer="true">
      <NewService :strProject="strProject" :strNamespace="strNamespace" v-on:childMethod="parentMethod"/>
    </b-modal>
    <!-- 新建服務結束 -->

    <!-- Namespace表單開始 -->
    <v-client-table :data="tableData1" :columns="columns1" :options="options1" v-show="showNamespace">
      <template slot="name" slot-scope="props">
        <div>
          <div class="text-left">
            <a href="javascript:void(0)" @click.prevent="goNamespace(props.row.name)">{{props.row.name}}</a>
          </div>  
        </div>
      </template>
      <template slot="state" slot-scope="props">
        <div>
          <div class="p-2" :class="[unactive, props.row.state=='active'? active : '']">
            {{props.row.state}}
          </div>  
        </div>
      </template>
      <template slot="action" slot-scope="props">
        <div>
          <b-btn variant="outline-info" class="btn-xs" v-b-tooltip.hover :title="$t('common.ICON_TIP_TRACE')" @click.prevent="goTrace(props.row.name)">
            <i class="fas fa-link"></i>
          </b-btn>
          <!-- <b-btn variant="outline-info" class="btn-xs" v-b-modal.linklink v-b-tooltip.hover :title="$t('common.ICON_TIP_TRACE')">
            <i class="fas fa-link"></i>
          </b-btn> -->
          <!-- <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" >
            <a :href="props.row.traceroute_link" target="blank">
              <i class="fas fa-link"></i>
            </a>
          </b-btn> -->
          <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="editpop(props.row)" v-b-modal.editNamespace v-b-tooltip.hover :title="$t('common.ICON_TIP_EDIT')">
              <i class="fas fa-edit"></i>
          </b-btn>
          <b-btn variant="outline-danger" class="btn-xs" v-if="props.row.state!=='removing'" @click.prevent="checkCancel(props.row)" v-b-tooltip.hover :title="$t('common.ICON_TIP_DEL')">
            <i class="fas fa-trash-alt"></i>
          </b-btn>
        </div>
      </template>
    </v-client-table>
    <!-- Namespace表單結束 -->

    <!-- 服務表單開始 -->
    <v-client-table :data="tableData2" :columns="columns2" :options="options2" v-show="showService">
      <template slot="state" slot-scope="props">
        <div>
          <div class="p-2" :class="[unactive, props.row.state=='active'? active : '']">
            {{props.row.state}}
          </div>  
        </div>
      </template>
      <template slot="action" slot-scope="props">
          <div>
              <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="viewlog(props.row)" v-b-modal.containerLog v-b-tooltip.hover :title="$t('service.SVCM_DIAL_CONTAINER_LOG_TITLE')">
                  <em class="fa-x fas fa-eye"></em>
              </b-btn>
              <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="rollback(props.row)" v-b-modal.createRollback v-b-tooltip.hover :title="$t('service.SVCM_DIAL_SVCRBK_TITLE')">
                  <!--i class="fas fa-redo-alt"></i-->
                  <i class="fas fa-history"></i>
              </b-btn>
              <!-- <b-btn variant="outline-success" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="authorize(props.row)" v-b-modal.createAuthorize v-b-tooltip.hover :title="$t('service.SVCM_DIAL_SVCMODIFY_TITLE')">
                  <i class="fas fa-draw-polygon"></i>
              </b-btn> -->
              <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="upgrade(props.row)" v-b-modal.createUpgrade v-b-tooltip.hover :title="$t('common.ICON_TIP_SVC_UPGRADE')">
                  <!-- i class="fas fa-chevron-up"></i -->
                  <i class="fas fa-arrow-circle-up"></i>
              </b-btn>

              <b-btn variant="outline-success" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="Info(props.row)" v-b-modal.createInfo v-b-tooltip.hover :title="$t('common.ICON_TIP_SVC_INFO')">
                  <i class="fas fa-info-circle"></i>
              </b-btn>

              <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="Clone(props.row)" v-b-modal.createClone v-b-tooltip.hover :title="$t('common.ICON_TIP_SVC_CLONE')">
                  <i class="far fa-clone"></i>
              </b-btn>

              <b-btn variant="outline-danger" class="btn-xs" v-if="props.row.state!=='removing'" @click.prevent="checkCancel2(props.row)" v-b-tooltip.hover :title="$t('common.ICON_TIP_DEL')">
                  <i class="fas fa-trash-alt"></i>
              </b-btn>
          </div>
      </template>
    </v-client-table>
    <!-- 服務表單結束 -->

    <!-- 服務返回開始 -->
    <b-modal class="modal fade" id="createRollback" :title="$t('service.SVCM_DIAL_SVCRBK_TITLE')" ref="modal" :hide-footer="true" >
        <RollbackService :strProject="strProject" :strNamespace="strNamespace" :strservicename="strservicename" v-on:childMethod="parentMethod" />
    </b-modal>
    <!-- 服務返回結束 -->

    <!-- 服務鑑權開始 -->
    <!-- <b-modal class="modal fade" id="createAuthorize" size="xl" :title="$t('service.SVCM_DIAL_SVCMODIFY_TITLE')" ref="modal" :hide-footer="true">
        <Authorize :strProject="strProject" :strNamespace="strNamespace" :strservicename="strservicename" v-on:childMethod="parentMethod" />
    </b-modal> -->
    <!-- 服務鑑權結束 -->

    <!-- 升級服務開始 -->
    <b-modal class="modal fade" id="createUpgrade" size="xl" :title="$t('service.SVCM_DIAL_SVCUPGRADE_TITLE')" ref="modal" :hide-footer="true">
        <Upgrade :strProject="strProject" :strNamespace="strNamespace" :strservicename="strservicename" v-on:childMethod="parentMethod" />
    </b-modal>
    <!-- 升級服務結束 -->

    <!-- 服務資訊開始 -->
    <b-modal class="modal fade" id="createInfo" size="xl" :title="$t('service.SVCM_DIAL_SVCINFO_TITLE')" ref="modal" :hide-footer="true">
        <EditService :strProject="strProject" :strNamespace="strNamespace" :strservicename="strservicename" v-on:childMethod="parentMethod" />
    </b-modal>
    <!-- 服務資訊結束 -->

    <!-- 服務複製開始 -->
    <b-modal class="modal fade" id="createClone" size="xl" :title="$t('service.SVCM_DIAL_SVCCLONE_TITLE')" ref="modal" :hide-footer="true">
        <CloneService :strProject="strProject" :strNamespace="strNamespace" :strservicename="strservicename" v-on:childMethod="parentMethod" />
    </b-modal>
    <!-- 服務複製結束 -->

    <!-- 容器日誌開始 -->
    <b-modal class="modal fade" id="containerLog" :title="$t('service.SVCM_DIAL_CONTAINER_LOG_TITLE')" ref="modal" size="xl" :hide-footer="true">
        <div class="row">
            <div class="col-lg-3">
                <div class="card mb-3 border-primary">
                    <div class="card-header text-white bg-primary">{{$t("service.SVCM_DIAL_CONTAINER_LOG_OPT_REPLICA")}}</div>
                    <div class="card-body">
                        <div class="d-flex align-items-center flex-nowrap" v-for="replica in replicas" :key="replica.id">
                            <input type="radio" v-model="selectedReplica" name="replicas" :value="replica.name" />
                            <span>{{replica.displayname}}({{replica.created}})</span>
                        </div>
                    </div>
                </div>
                <div class="card mb-3 border-primary">
                    <div class="card-header text-white bg-primary">{{$t("service.SVCM_DIAL_CONTAINER_LOG_OPT_CONTAINER")}}</div>
                    <div class="card-body">
                        <div class="d-flex align-items-center flex-nowrap" v-for="container in containers" :key="container.name">
                            <input type="radio" v-model="selectedContainer" name="containers" :value="container.name" /> 
                            <span>{{container.name}}</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-lg-9">
                <div class="form-group row mx-0 justify-content-start">
                    <div class="col-md-auto">
                        <input class="form-check-input" id="inlineRadio1" type="radio" name="inlineRadioOptions" v-model="viewAllLog" :value="true" />
                        <label class="form-check-label" for="inlineRadio1">{{$t("service.SVCM_DIAL_CONTAINER_LOG_OPT_SHOW_ALL")}}</label>
                    </div>
                    <div class="col-md-auto">
                        <input class="form-check-input" id="inlineRadio2" type="radio" name="inlineRadioOptions" v-model="viewAllLog" :value="false" />
                        <label class="form-check-label" for="inlineRadio2">{{$t("service.SVCM_DIAL_CONTAINER_LOG_OPT_SHOW_LAST")}}</label>
                        <input id="number" type="number" min="0" step="50" v-model="logLineNumber" /> {{$t("service.SVCM_DIAL_CONTAINER_LOG_OPT_RECORDS")}}
                    </div>
                    <div class="col-md-auto">
                        <button class="btn btn-primary btn-xs mr-1" type="button" @click="query()">{{$t("service.SVCM_DIAL_CONTAINER_LOG_BTN_QUERY")}}</button>
                    </div>
                </div>
                <div id="logContent" class="logContent col-12" v-html="logContent">

                </div>
            </div>
        </div>
    </b-modal>
    <!-- 容器日誌結束 -->

  </div>
</template>

<script>
import Vue from "vue";
import { ClientTable, Event } from "vue-tables-2";
import swal from "sweetalert2";
import NewNamespace from "./service/NewNamespace";
import EditNamespace from "./service/EditNamespace";
import ProjectTable from "./service/ProjectTable";
import NewService from "./service/NewService";
import RollbackService from "./service/RollbackService";
import Authorize from "./service/Authorize";
import Upgrade from "./service/Upgrade";
import EditService from "./service/EditService";
import CloneService from "./service/CloneService";
Vue.use(ClientTable);

export default {
  name: "Service",
  components: {
    ClientTable,
    ProjectTable,
    NewNamespace,
    EditNamespace,
    NewService,
    RollbackService,
    Authorize,
    Upgrade,
    EditService,
    CloneService
  },
  data() {
    return {
      unactive:'unactive',
      active:'active',
      timer1:'',
      timer2:'',
      scrollOptions: {
        height: "600px",
        width: "700px",
        size: 0
      },

      logLineNumber: 50,
      selectedReplica: null,
      selectedContainer: null,
      logContent: "",
      viewAllLog: true,
      replicas: [],
      containers: [],

      spacename: "",
      spacenameState: null,
      spacetext: "",
      spacetextState: null,

      submittedNames: [],
      selectedIndex: "",
      selectedProject: "",
      alldata: [],
      projectlist: [],
      strProject: "",
      namespacelist: [],
      strNamespace: "",
      strspacename: "",
      strservicename: "",
      tableData1: [],
      tableData2: [],
      tempData:[],
      showProject:true,
      showNamespace: false,
      showService: false,
      disabled: true,

      columns1: ["name", "state", "description", "createdTS", "action", "traceroute_link"],
      options1: {
        sortable: ["name", "state", "description", "createdTS"],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder: this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count: this.$t('common.LBL_TOTAL')  + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults:this.$t('common.LBL_NO_RECORD')
        },
        headings: {
          name: () => this.$t('service.SVCM_LST_NAMESPACE_NAME'),
          state: () => this.$t('service.SVCM_LST_NAMESPACE_STATUS'),
          description: () => this.$t('service.SVCM_LST_NAMESPACE_DESC'),
          createdTS: () => this.$t('service.SVCM_LST_NAMESPACE_CREATE_TIME'),
          action: () => this.$t('service.SVCM_LST_NAMESPACE_OPT')
        },
        columnsClasses:{
          name:"name-table",
          state: "state-table",
          description:"description-table",
          action:"action-table",
          createdTS: "date-table",
          traceroute_link: "link"
        },
        pagination: { chunk: 5 },
        sortIcon: {
          is: "fa-sort",
          base: "fas",
          up: "fa-sort-up",
          down: "fa-sort-down"
        },
        dateColumns:['createdTS'],
        dateFormat: 'YYYY-MM-DD HH:mm:ss',
        orderBy:{
          column:"createdTS",
          ascending:false
        }, 
      },

      columns2: ["name", "state", "image", "createdTS", "action"],
      options2: {
        sortable: ["name", "state", "image", "createdTS"],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder: this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count: this.$t('common.LBL_TOTAL') + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults: this.$t('common.LBL_NO_RECORD')
        },
        headings: {
          name: () => this.$t('service.SVCM_LST_SVC_NAME'),
          state: () => this.$t('service.SVCM_LST_SVC_STATUS'),
          image: () => this.$t('service.SVCM_LST_CONTAINER_IMG'),
          createdTS: () => this.$t('service.SVCM_LST_CRTE_TIME'),
          action: () => this.$t('service.SVCM_LST_OPT')
        },
        columnsClasses:{
          state: "state",
        },
        pagination: { chunk: 5 },
        sortIcon: {
          is: "fa-sort",
          base: "fas",
          up: "fa-sort-up",
          down: "fa-sort-down"
        },
        dateColumns:['createdTS'],
        dateFormat: 'YYYY-MM-DD HH:mm:ss',
        orderBy:{
          column:"createdTS",
          ascending:false
        }, 
      },
      isNew: false,
      isLoading: false
    };
  },
  created() {
    this.isLoading = true;
    backendapi
      .get("/project")
      .then(response => {
        this.isLoading = false;
        this.alldata = response.data;
        this.projectlist = response.data;
      })
      .catch(error => {
        console.log(error);
    });
  },

  methods: {

    getPjname(PJname){
      this.strProject = PJname
      this.isLoading = true;
      clearInterval(this.timer1);
      this.timer1 = setInterval(()=> {this.reloadd1()}, 3000);
      backendapi.get(`/project/${this.strProject}/namespace`)
        .then(response => {
          this.namespacelist = response.data;
          this.selected = true;
          this.tableData1 = response.data.map((item, index) => {
            item["id"] = index;
            item['createdTS'] = moment(item.createdTS);
            return item;
          });
          this.showProject = false
          this.showNamespace = true
          this.showService = false
          this.isLoading = false
          document.getElementById("myselected").selected = true;
          if (this.namespacelist.length >= 1) {
            this.disabled = false;
          } else {
            this.disabled = true;
          }
            backendapi.get(`/project/${this.strProject}/namespace/traceroute`)
            .then(response => {
              this.tableData1 = this.tableData1.map((item, i) => 
                Object.assign({}, item, response.data[i]
              ));
              this.tableData1.map((item, index) => {
                item['createdTS'] = moment(item.createdTS);
                return item;
              })
            })
            .catch(error => {
              console.log(error);
            });
        })
        .catch(error => {
          console.log(error);
        });
    },
    reloadd1(){
      backendapi.get(`/project/${this.strProject}/namespace`)
        .then(response => {
          this.tempData = response.data.map((item, index) => {
            item["id"] = index;
            item['createdTS'] = moment(item.createdTS);
            return item;
          });
            backendapi.get(`/project/${this.strProject}/namespace/traceroute`)
            .then(response => {
              this.tableData1 = this.tempData.map((item, i) => 
                Object.assign({}, item, response.data[i]
              ));
              this.tableData1.map((item, index) => {
                item['createdTS'] = moment(item.createdTS);
                return item;
              });
            })
        })
    },
    reloadd2(){
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/service`)
        .then(response => {
          // console.log("aaaaaaaaaaaaaa");
          this.tableData2 = response.data.map((item, index) => {
            item["serviceId"] = item.id;
            item["id"] = index;
            item['createdTS'] = moment(item.createdTS);
            return item;
        });
      });
    },
    selectedproject() {
      var projectlist = document.getElementById("projectlist");
      this.strProject = projectlist.options[projectlist.selectedIndex].value;
      this.isLoading = true;
      clearInterval(this.timer1);
      this.timer1 = setInterval(()=> {this.reloadd1()}, 3000);
      backendapi.get(`/project/${this.strProject}/namespace`)
        .then(response => {
          this.namespacelist = response.data;
          this.selected = true;
          this.tableData1 = response.data.map((item, index) => {
            item["id"] = index;
            item['createdTS'] = moment(item.createdTS);
            return item;
          });
          this.showProject = false
          this.showNamespace = true
          this.showService = false
          this.isLoading = false
          document.getElementById("myselected").selected = true;
          if (this.namespacelist.length >= 1) {
            this.disabled = false;
          } else {
            this.disabled = true;
          }
            backendapi.get(`/project/${this.strProject}/namespace/traceroute`)
            .then(response => {
              this.tableData1 = this.tableData1.map((item, i) => 
                Object.assign({}, item, response.data[i]
              ));
              this.tableData1.map((item, index) => {
                item['createdTS'] = moment(item.createdTS);
                return item;
              })
            })
            .catch(error => {
              console.log(error);
            });
        })
        .catch(error => {
          console.log(error);
        });
    },
    selectednamespace() {
      var namespacelist = document.getElementById("namespacelist");
      this.strNamespace = namespacelist.options[namespacelist.selectedIndex].value;
      this.isLoading = true;
      clearInterval(this.timer2);
      this.timer2 = setInterval(()=> {this.reloadd2()}, 3000);
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/service`)
        .then(response => {
          this.tableData2 = response.data.map((item, index) => {
            console.log("service", item);
            item["serviceId"] = item.id;
            item["id"] = index;
            item['createdTS'] = moment(item.createdTS);
            return item;
          });
          this.showNamespace = false;
          this.showService = true;
          this.isLoading = false;
        })
        .catch(error => {
          console.log(error);
          this.isLoading = false;
        });
    },

    goNamespace(name){
      this.strNamespace = name;
      this.isLoading = true;
      backendapi
      .get(
        `/project/${this.strProject}/namespace/${this.strNamespace}/service`
      )
      .then(response => {
        this.tableData2 = response.data.map((item, index) => {
          item["serviceId"] = item.id;
          item["id"] = index;
          item['createdTS'] = moment(item.createdTS);
          return item;
        });
        document.getElementById("namespacelist").value = this.strNamespace
        this.showNamespace = false;
        this.showService = true;
        this.isLoading = false;
      })
      .catch(error => {
        this.isLoading = false;
        console.log(error);
      });
    },

    loadTable() {
        this.isLoading = true;
        backendapi
          .get(`/project/${this.strProject}/namespace`)
          .then(response => {
            this.namespacelist = response.data;
            this.selected = true;
            this.tableData1 = response.data.map((item, index) => {
              item["id"] = index;
              item['createdTS'] = moment(item.createdTS);
              return item;
            });
            (this.showService = false),
            (this.showNamespace = true),
            (this.isLoading = false);
            document.getElementById("myselected").selected = true;
            if (this.namespacelist.length >= 1) {
              this.disabled = false;
            } else {
              this.disabled = true;
            }
          })
          .catch(error => {
            console.log(error);
          });
    },
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity();
      this.spacenameState = valid ? "valid" : "invalid";
      this.spacetextState = valid ? "valid" : "invalid";
      return valid;
    },
    handleOk() {
      this.handleSubmit();
    },
    resetModal() {
      this.spacename = "";
      this.spacetext = "";
      this.spacenameState = null;
      this.spacetextState = null;
    },
    handleSubmit() {
      if (!this.checkFormValidity()) {
        return;
      }
      console.log("post", this.strProject);
      backendapi
        .post(`/project/${this.strProject}/namespace`, {
          name: this.spacename,
          description: this.spacetext
        })
        .then(response => {
          this.$nextTick(() => {
            this.$refs.modal.hide();
          });
        })
        .catch(error => {
          console.log(error);
        });
    },
    remove(row) {
      this.isLoading = true;
      this.tableData1.filter(item => row.id !== item.id);
      this.strspacename = this.tableData1[row.id].name;
      backendapi
        .delete(`/project/${this.strProject}/namespace/${this.strspacename}`)
        .then(response => {
          this.loadTable();
          this.isLoading = false;
        })
        .catch(error => {
          console.log(error);
        });
    },
    loadProject() {
      this.$nextTick(() => {
        this.$refs.modal.hide();
      });
      clearInterval(this.timer1);
      this.timer1 = setInterval(()=> {this.reloadd1()}, 3000);
    },
    parentMethod() {
      this.$nextTick(() => {
        this.$refs.modal.hide();
      });
      this.selectednamespace();
    },
    // 新建服務表單刪除 //
    remove2(row) {
      this.isLoading = true;
      this.tableData2.filter(item => row.id !== item.id);
      this.strservicename = this.tableData2[row.id].name;
      backendapi
        .delete(
          `/project/${this.strProject}/namespace/${this.strNamespace}/service/${this.strservicename}`
        )
        .then(response => {
          this.selectednamespace();
          this.isLoading = false;
        })
        .catch(error => {
          alert(error.message);
          this.isLoading = false;
        });
    },
    editpop(row) {
      this.tableData1.filter(item => row.id !== item.id);
      this.strNamespace = this.tableData1[row.id].name;
    },
    rollback(row) {
      this.tableData2.filter(item => row.id !== item.id);
      this.strservicename = this.tableData2[row.id].name;
    },
    authorize(row) {
      this.tableData2.filter(item => row.id !== item.id);
      this.strservicename = this.tableData2[row.id].name;
    },
    upgrade(row) {
      this.tableData2.filter(item => row.id !== item.id);
      this.strservicename = this.tableData2[row.id].name;
    },
    Info(row) {
      this.tableData2.filter(item => row.id !== item.id);
      this.strservicename = this.tableData2[row.id].name;
    },
    Clone(row) {
      this.tableData2.filter(item => row.id !== item.id);
      this.strservicename = this.tableData2[row.id].name;
    },
    goTrace(name){
      window.open('http://kiali.10.62.164.150.xip.io/console/graph/namespaces/?edges=requestsPerSecond&graphType=versionedApp&namespaces='+ name + '&unusedNodes=true&injectServiceNodes=false&pi=15000&duration=60&layout=dagre', "_blank");
    },
    checkCancel(row) {
      swal({
        title: this.$t('service.SVCM_DIAL_DEL_NAMESPACE_MSG'),
        text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#DD6B55",
        confirmButtonText: this.$t('common.BTN_CONFIRM'),
        cancelButtonText:  this.$t('common.BTN_CANCEL'),
        heightAuto: false
      }).then(result => {
        if (result.value) {
          this.remove(row);
          swal({
            title: this.$t('service.SVCM_DIAL_DEL_NAMESPACE_SUCCESS'),
            confirmButtonText: this.$t('common.BTN_OK'),
            type: "success",
            heightAuto: false
          });
        }
      });
    },
    checkCancel2(row) {
      swal({
        title: this.$t('service.SVCM_DIAL_DEL_SVC_CHECKING_MSG'),
        text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#DD6B55",
        confirmButtonText: this.$t('common.BTN_CONFIRM'),
        cancelButtonText: this.$t('common.BTN_CANCEL'),
        heightAuto: false
      }).then(result => {
        if (result.value) {
          this.remove2(row);
          swal({
            title: this.$t('common.MSG_DEL_SUCCESS'),
            confirmButtonText: this.$t('common.BTN_OK'),
            type: "success",
            heightAuto: false
          });
        }
      });
    },
    viewlog(row) {
      this.logLineNumber = 50;
      this.selectedReplica = null;
      this.selectedContainer = null;
      this.logContent = "";
      this.viewAllLog = true;

      console.log("loggg", row);
      console.log("row serviceId", row.serviceId);
      console.log("strProject", this.strProject);
      console.log("strNamespace", this.strNamespace);
      backendapi
        .get(
          `/project/${this.strProject}/namespace/${this.strNamespace}/workload/${row.serviceId}/info`
        )
        .then(res => {
          this.replicas = res.data.map((item, index) => {
           item.created = moment(item.created).format('YYYY-MM-DD HH:mm:ss')
            return item
          });;
          console.log("replicas", this.replicas);
          this.containers = res.data[0].containers.filter((item, index) => {
            if(!item["name"].includes("istio")){
              return item;
            }
          });
          console.log("containers", this.containers);
        });
    },
    query() {
      if (this.selectedReplica == null) {
        alert(this.$t('service.SVCM_ALERT_CONTAINER_LOG_NO_REPLICA_BE_SELECTED'));
      } else if (this.selectedContainer == null) {
        alert(this.$t('service.SVCM_ALERT_CONTAINER_LOG_NO_CONTAINER_BE_SELECTED'));
      } else {
        //
        var url = `/project/${this.strNamespace}/${this.selectedReplica}/${this.selectedContainer}/logs`;
        if (!this.viewAllLog) {
          url = url + `?tailLines=${this.logLineNumber}`;
        }

        backendapi
          .get(url)
          .then(res => {
            console.log(res.data);
            this.logContent = res.data.log_info.replace(/\n/g, "<br>");
          })
          .catch(err => {
            console.error(err);
          });
      }
    },

  },
  destroyed: function() {
    clearInterval(this.timer1);
    clearInterval(this.timer2);
  }
};
</script>

<style>
.VueTables tbody tr td {
  font-size: 14px;
}

#logContent{
  background: #000;
  color: #fff;
  padding-left: 10px;
  font-size: 13px;
  min-height:460px!important;
  max-height:460px!important;
  overflow: auto;
  overflow-y: scroll;
  overflow-x: hidden;
}

/* .slimScrollDiv{
  width: 100%!important;
}
.slimScrollDiv #logContent{
  width: 100%!important;
} */
table .link{
  display: none!important;
}
.name-table{
  width: 15%;
}
.state-table{
  width: 10%;
}
.date-table{
  width: 17%;
}
.action-table{
  width: 15%;
}

</style>



