<template>
<div>

  <form ref="form">
    <div class="form-group row m-0 my-3">
      <label class="mx-3" for="addspacename">{{$t("service.SVCM_DIAL_NEWNAMESPACE_IPTLBL_NAMESPACE")}} {{strNamespace}}</label>
      <!-- <b-form-input id="addspacename" class="col-md-4" type="text" :placeholder="$t('service.SVCM_DIAL_NEWNAMESPACE_HINT_INPUT_NAMESPACE')" v-model="spacename" required></b-form-input> -->
      <label class="mx-3" for="textarea-default">{{$t("service.SVCM_DIAL_NEWNAMESPACE_IPTLBL_DESC")}}</label>
      <b-form-input id="textarea-default" class="col-md-5" :placeholder="$t('service.SVCM_DIAL_NEWNAMESPACE_HINT_INPUT_NAMESPACE_DESC')" v-model="spacetext" required></b-form-input>
    </div>

    <div class="m-3" v-if="open">

      <h6>CPU Limit: &nbsp&nbsp&nbsp<span>{{resourceQuota.limit.limitsCpu}}</span></h6>

      <b-progress class="mt-2" :max="resourceQuota.limit.limitsCpu" show-value>
        <b-progress-bar :value="resourceQuota.usedLimit.limitsCpu-namespaceDefaultResourceQuota.limit.limitsCpu" variant="secondary"></b-progress-bar>
        <b-progress-bar :value="namespaceDefaultResourceQuota.limit.limitsCpu" variant="warning"></b-progress-bar>
      </b-progress>

      <table id="tableResource" class="table" style="background: #daf1e45c;">
        <tbody>
          <tr>
            <th scope="row" class="pr-1 text-warning">{{$t("service.SVCM_DIAL_NEWNAMESPACE_IPTLBL_NAMESPACE_RSRC_LIMIT")}}</th>
            <td><input v-model="namespaceDefaultResourceQuota.limit.limitsCpu" type="number" :class="{'form-control':true}" required min="0" @change="changeValue()"/><label class="mx-2">m</label></td>
            <th scope="row" class="pr-1">{{$t("service.SVCM_DIAL_NEWNAMESPACE_IPTLBL_CONTAINER_RSRC_LIMIT")}}</th>
            <td><input v-model="containerDefaultResourceLimit.limitsCpu" type="number" :class="{'form-control':true}" required min="0" @change="changeValue()"/><label class="mx-2">m</label></td>
          </tr>
        </tbody>  
      </table> 

    </div>

    <div class="m-3" v-if="open">

      <h6>CPU Reservation: &nbsp&nbsp&nbsp<span>{{resourceQuota.limit.requestsCpu}}</span></h6>

      <b-progress class="mt-2" :max="resourceQuota.limit.requestsCpu" show-value>
        <b-progress-bar :value="resourceQuota.usedLimit.requestsCpu-namespaceDefaultResourceQuota.limit.requestsCpu" variant="secondary"></b-progress-bar>
        <b-progress-bar :value="namespaceDefaultResourceQuota.limit.requestsCpu" variant="warning"></b-progress-bar>
      </b-progress>

      <table id="tableResource" class="table"  style="background: #daf1e45c;">
        <tbody>
          <tr>
            <th scope="row" class="pr-1 text-warning">{{$t("service.SVCM_DIAL_NEWNAMESPACE_IPTLBL_NAMESPACE_RSRC_LIMIT")}}</th>
            <td><input v-model="namespaceDefaultResourceQuota.limit.requestsCpu" type="number" :class="{'form-control':true}" required  min="0" @change="changeValue()"/><label class="mx-2">m</label></td>
            <th scope="row" class="pr-1">{{$t("service.SVCM_DIAL_NEWNAMESPACE_IPTLBL_CONTAINER_RSRC_LIMIT")}}</th>
            <td><input v-model="containerDefaultResourceLimit.requestsCpu" type="number" :class="{'form-control':true}" required min="0" @change="changeValue()"/><label class="mx-2">m</label></td>
          </tr>
        </tbody>  
      </table>

    </div>

    <div class="m-3"  v-if="open">

      <h6>Memory Limit: &nbsp&nbsp&nbsp<span>{{resourceQuota.limit.limitsMemory}}</span></h6>

      <b-progress class="mt-2" :max="resourceQuota.limit.limitsMemory" show-value>
        <b-progress-bar :value="resourceQuota.usedLimit.limitsMemory-namespaceDefaultResourceQuota.limit.limitsMemory" variant="secondary"></b-progress-bar>
        <b-progress-bar :value="namespaceDefaultResourceQuota.limit.limitsMemory" variant="warning"></b-progress-bar>
      </b-progress>

      <table id="tableResource" class="table" style="background: #daf1e45c;">
        <tbody>
          <tr>
            <th scope="row" class="pr-1 text-warning">{{$t("service.SVCM_DIAL_NEWNAMESPACE_IPTLBL_NAMESPACE_RSRC_LIMIT")}}</th>
            <td><input v-model="namespaceDefaultResourceQuota.limit.limitsMemory" type="number" :class="{'form-control':true}" required  min="0" @change="changeValue()"/><label class="mx-2">Mi</label></td>
            <th scope="row" class="pr-1">{{$t("service.SVCM_DIAL_NEWNAMESPACE_IPTLBL_CONTAINER_RSRC_LIMIT")}}</th>
            <td><input v-model="containerDefaultResourceLimit.limitsMemory" type="number" :class="{'form-control':true}" required min="0" @change="changeValue()"/><label class="mx-2">Mi</label></td>
          </tr>
        </tbody>  
      </table>  

    </div>

    <div class="m-3"  v-if="open">

      <h6>Memory Reservation: &nbsp&nbsp&nbsp<span>{{resourceQuota.limit.requestsMemory}}</span></h6>

      <b-progress class="mt-2" :max="resourceQuota.limit.requestsMemory" show-value>
        <b-progress-bar :value="resourceQuota.usedLimit.requestsMemory-namespaceDefaultResourceQuota.limit.requestsMemory" variant="secondary"></b-progress-bar>
        <b-progress-bar :value="namespaceDefaultResourceQuota.limit.requestsMemory" variant="warning"></b-progress-bar>
      </b-progress>

      <table id="tableResource" class="table" style="background: #daf1e45c;">
        <tbody>
          <tr>
            <th scope="row" class="pr-1 text-warning">{{$t("service.SVCM_DIAL_NEWNAMESPACE_IPTLBL_NAMESPACE_RSRC_LIMIT")}}</th>
            <td><input v-model="namespaceDefaultResourceQuota.limit.requestsMemory" type="number" :class="{'form-control':true}" required min="0" @change="changeValue()"/><label class="mx-2">Mi</label></td>
            <th scope="row" class="pr-1">{{$t("service.SVCM_DIAL_NEWNAMESPACE_IPTLBL_CONTAINER_RSRC_LIMIT")}}</th>
            <td><input v-model="containerDefaultResourceLimit.requestsMemory" type="number" :class="{'form-control':true}" required min="0" @change="changeValue()"/><label class="mx-2">Mi</label></td>
          </tr>
        </tbody>
      </table>

    </div>

    <!-- <div v-if="open" class="m-3">
      <table id="tableResource" class="table">
        <thead class="thead-dark">
          <tr>
            <th scope="col">{{$t("service.SVCM_DIAL_NEWNAMESPACE_TABLE_RSRC_TYPE")}}</th>
            <th scope="col">{{$t("service.SVCM_DIAL_NEWNAMESPACE_TABLE_NAMESPACE_RSRC_LIMIT")}}</th>
            <th scope="col">{{$t("service.SVCM_DIAL_NEWNAMESPACE_TABLE_CONTAINER_RSRC_LIMIT")}}</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th scope="row">CPU Limit</th>
            <td><input v-model="namespaceDefaultResourceQuota.limit.limitsCpu" type="number" :class="{'form-control':true}" required min="0" @change="changeValue()"/><label class="mx-2">m</label></td>
            <td><input v-model="containerDefaultResourceLimit.limitsCpu" type="number" :class="{'form-control':true}" required min="0" @change="changeValue()"/><label class="mx-2">m</label></td>
          </tr>
          <tr>
            <th scope="row">CPU Reservation</th>
            <td><input v-model="namespaceDefaultResourceQuota.limit.requestsCpu" type="number" :class="{'form-control':true}" required  min="0" @change="changeValue()"/><label class="mx-2">m</label></td>
            <td><input v-model="containerDefaultResourceLimit.requestsCpu" type="number" :class="{'form-control':true}" required min="0" @change="changeValue()"/><label class="mx-2">m</label></td>
          </tr>
          <tr>
            <th scope="row">Memory Limit</th>
            <td><input v-model="namespaceDefaultResourceQuota.limit.limitsMemory" type="number" :class="{'form-control':true}" required  min="0" @change="changeValue()"/><label class="mx-2">Mi</label></td>
            <td><input v-model="containerDefaultResourceLimit.limitsMemory" type="number" :class="{'form-control':true}" required min="0" @change="changeValue()"/><label class="mx-2">Mi</label></td>
          </tr>
          <tr>
            <th scope="row">Memory Reservation</th>
            <td><input v-model="namespaceDefaultResourceQuota.limit.requestsMemory" type="number" :class="{'form-control':true}" required min="0" @change="changeValue()"/><label class="mx-2">Mi</label></td>
            <td><input v-model="containerDefaultResourceLimit.requestsMemory" type="number" :class="{'form-control':true}" required min="0" @change="changeValue()"/><label class="mx-2">Mi</label></td>
          </tr>
        </tbody>
      </table>
    </div> -->
  </form>

  <div class="modal-footer mt-3">
    <b-button class="mx-1" size="sm" variant="success" @click="handleOk()">{{$t("service.PRJ_DIAL_NPRJ_BTN_SUBMIT")}}</b-button>
    <b-button class="mx-1" size="sm" variant="danger" @click="cancel()">{{$t("service.PRJ_DIAL_NPRJ_BTN_CANCEL")}}</b-button>
  </div>  

</div>
</template> 

<script>
import Vue from 'vue';

export default {
  props: ['strProject','strNamespace'],
  components: {
  },
  data(){
    return{
      value: 0,
      max: 0,
      open:false,
      spacename:'',
      spacenameState: null,
      spacetext:'',
      spacetextState: null,
      resourceQuota:{
        limit:{
          limitsCpu:0,
          requestsCpu:0,
          limitsMemory:0,
          requestsMemory:0
        },
        usedLimit: {
          limitsCpu: 0,
          requestsCpu: 0,
          limitsMemory: 0,
          requestsMemory: 0
        }
      },

      namespaceDefaultResourceQuota:{
        limit:{
          limitsCpu:0,
          requestsCpu:0,
          limitsMemory:0,
          requestsMemory:0
        }
      },

      containerDefaultResourceLimit:{
        limitsCpu:0,
        requestsCpu:0,
        limitsMemory:0,
        requestsMemory:0
      },

      isLoading: false,
    };
  },
  created(){
    backendapi.get(`/project/${this.strProject}`).then((response) => {
      // this.spacetext = response.data.description
      if(response.data.containerDefaultResourceLimit !== null && response.data.namespaceDefaultResourceQuota !== null && response.data.resourceQuota !== null){
        this.open = true
        this.resourceQuota.limit.limitsCpu = parseInt(response.data.resourceQuota.limit.limitsCpu.replace("m",""))
        this.resourceQuota.limit.requestsCpu = parseInt(response.data.resourceQuota.limit.requestsCpu.replace("m",""))
        this.resourceQuota.limit.limitsMemory = parseInt(response.data.resourceQuota.limit.limitsMemory.replace("Mi",""))
        this.resourceQuota.limit.requestsMemory = parseInt(response.data.resourceQuota.limit.requestsMemory.replace("Mi",""))

        if(!response.data.resourceQuota.usedLimit.limitsCpu){
          this.resourceQuota.usedLimit.limitsCpu = 0
        }
        if(!response.data.resourceQuota.usedLimit.requestsCpu){
          this.resourceQuota.usedLimit.requestsCpu = 0
        }
        if(!response.data.resourceQuota.usedLimit.limitsMemory){
          this.resourceQuota.usedLimit.limitsMemory = 0
        }
        if(!response.data.resourceQuota.usedLimit.requestsMemory){
          this.resourceQuota.usedLimit.requestsMemory = 0
        }else{
          this.resourceQuota.usedLimit.limitsCpu = parseInt(response.data.resourceQuota.usedLimit.limitsCpu.replace("m",""))
          this.resourceQuota.usedLimit.requestsCpu = parseInt(response.data.resourceQuota.usedLimit.requestsCpu.replace("m",""))
          this.resourceQuota.usedLimit.limitsMemory = parseInt(response.data.resourceQuota.usedLimit.limitsMemory.replace("Mi",""))
          this.resourceQuota.usedLimit.requestsMemory = parseInt(response.data.resourceQuota.usedLimit.requestsMemory.replace("Mi",""))
          this.resourceQuota.usedLimit.limitsMemory = parseInt(response.data.resourceQuota.usedLimit.limitsMemory.replace("Gi",""))*1024
          this.resourceQuota.usedLimit.requestsMemory = parseInt(response.data.resourceQuota.usedLimit.requestsMemory.replace("Gi",""))*1024

        }
      }
    })
    .catch(error => {
      alert(error.message)
    });
    backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}`).then((response) => {
      this.spacetext = response.data.description
      if(JSON.stringify(response.data.containerDefaultResourceLimit) !== "{}" && JSON.stringify(response.data.resourceQuota) !== "{}" && response.data.containerDefaultResourceLimit !== null && response.data.resourceQuota !== null ){
        this.open = true

        this.namespaceDefaultResourceQuota.limit.limitsCpu = parseInt(response.data.resourceQuota.limit.limitsCpu.replace("m",""))
        this.namespaceDefaultResourceQuota.limit.requestsCpu = parseInt(response.data.resourceQuota.limit.requestsCpu.replace("m",""))
        this.namespaceDefaultResourceQuota.limit.limitsMemory = parseInt(response.data.resourceQuota.limit.limitsMemory.replace("Mi",""))
        this.namespaceDefaultResourceQuota.limit.requestsMemory = parseInt(response.data.resourceQuota.limit.requestsMemory.replace("Mi",""))
        
        this.containerDefaultResourceLimit.limitsCpu = parseInt(response.data.containerDefaultResourceLimit.limitsCpu.replace("m",""))
        this.containerDefaultResourceLimit.requestsCpu = parseInt(response.data.containerDefaultResourceLimit.requestsCpu.replace("m",""))
        this.containerDefaultResourceLimit.limitsMemory = parseInt(response.data.containerDefaultResourceLimit.limitsMemory.replace("Mi",""))
        this.containerDefaultResourceLimit.requestsMemory = parseInt(response.data.containerDefaultResourceLimit.requestsMemory.replace("Mi",""))

        this.resourceQuota.usedLimit.limitsCpu = this.resourceQuota.usedLimit.limitsCpu - this.namespaceDefaultResourceQuota.limit.limitsCpu
        this.resourceQuota.usedLimit.requestsCpu = this.resourceQuota.usedLimit.requestsCpu - this.namespaceDefaultResourceQuota.limit.requestsCpu
        this.resourceQuota.usedLimit.limitsMemory = this.resourceQuota.usedLimit.limitsMemory - this.namespaceDefaultResourceQuota.limit.limitsMemory
        this.resourceQuota.usedLimit.requestsMemory = this.resourceQuota.usedLimit.requestsMemory - this.namespaceDefaultResourceQuota.limit.requestsMemory

    
      }
    })
    .catch(error => {
      alert(error.message)
    });
  },
  computed:{
  },

  methods:{
    changeValue() {
      this.namespaceDefaultResourceQuota.limit.limitsCpu = this.namespaceDefaultResourceQuota.limit.limitsCpu * 1
      this.namespaceDefaultResourceQuota.limit.requestsCpu = this.namespaceDefaultResourceQuota.limit.requestsCpu * 1
      this.namespaceDefaultResourceQuota.limit.limitsMemory = this.namespaceDefaultResourceQuota.limit.limitsMemory * 1
      this.namespaceDefaultResourceQuota.limit.requestsMemory = this.namespaceDefaultResourceQuota.limit.requestsMemory * 1
      if (this.namespaceDefaultResourceQuota.limit.limitsCpu > this.resourceQuota.limit.limitsCpu) {
        alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_RSRC_LIMIT_EXCEED_QUOTA'))
        this.namespaceDefaultResourceQuota.limit.limitsCpu = 0
      }
      if(this.namespaceDefaultResourceQuota.limit.requestsCpu > this.resourceQuota.limit.requestsCpu){
        alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_RSRC_LIMIT_EXCEED_QUOTA'))
        this.namespaceDefaultResourceQuota.limit.requestsCpu = 0
      }
      if(this.namespaceDefaultResourceQuota.limit.limitsMemory > this.resourceQuota.limit.limitsMemory){
        alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_RSRC_LIMIT_EXCEED_QUOTA'))
        this.namespaceDefaultResourceQuota.limit.limitsMemory = 0
      }
      if(this.namespaceDefaultResourceQuota.limit.requestsMemory > this.resourceQuota.limit.requestsMemory){
        alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_RSRC_LIMIT_EXCEED_QUOTA'))
        this.namespaceDefaultResourceQuota.limit.requestsMemory = 0
      }

      if (this.containerDefaultResourceLimit.limitsCpu > this.namespaceDefaultResourceQuota.limit.limitsCpu) {
        alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_CONTAINER_RSRC_LIMIT_CANNOT_EXCEED_NAMESPACE'))
        this.containerDefaultResourceLimit.limitsCpu = 0
      }
      if(this.containerDefaultResourceLimit.requestsCpu > this.namespaceDefaultResourceQuota.limit.requestsCpu){
        alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_CONTAINER_RSRC_LIMIT_CANNOT_EXCEED_NAMESPACE'))
        this.containerDefaultResourceLimit.requestsCpu = 0
      }
      if(this.containerDefaultResourceLimit.limitsMemory > this.namespaceDefaultResourceQuota.limit.limitsMemory){
        alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_CONTAINER_RSRC_LIMIT_CANNOT_EXCEED_NAMESPACE'))
        this.containerDefaultResourceLimit.limitsMemory = 0
      }
      if(this.containerDefaultResourceLimit.requestsMemory > this.namespaceDefaultResourceQuota.limit.requestsMemory){
        alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_CONTAINER_RSRC_LIMIT_CANNOT_EXCEED_NAMESPACE'))
        this.containerDefaultResourceLimit.requestsMemory = 0
      }

    },
    cancel(){
      this.$emit('childMethod');
    },
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity();
      this.spacenameState = valid ? 'valid' : 'invalid'
      this.spacetextState = valid ? 'valid' : 'invalid'
      return valid
    },
    handleOk() {
      this.handleSubmit()
    },
    resetModal() {
      this.spacename = ''
      this.spacetext = ''
      this.spacenameState = null
      this.spacetextState = null
    },
    handleSubmit() {
      this.spacetext.trim();
      if (this.spacetext=="") {
        alert("請輸入描述")
        return
      }
      if (!this.checkFormValidity()) {
        return
      }
      if(this.open == false){
        this.containerDefaultResourceLimit = null
        this.namespaceDefaultResourceQuota = null
        this.resourceQuota = null
        this.goNull();
      }
      if(this.open == true){

        if (this.namespaceDefaultResourceQuota.limit.limitsCpu > this.resourceQuota.limit.limitsCpu) {
          alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_RSRC_LIMIT_EXCEED_QUOTA'))
          return
        }
        if(this.namespaceDefaultResourceQuota.limit.requestsCpu > this.resourceQuota.limit.requestsCpu){
          alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_RSRC_LIMIT_EXCEED_QUOTA'))
          return
        }
        if(this.namespaceDefaultResourceQuota.limit.limitsMemory > this.resourceQuota.limit.limitsMemory){
          alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_RSRC_LIMIT_EXCEED_QUOTA'))
          return
        }
        if(this.namespaceDefaultResourceQuota.limit.requestsMemory > this.resourceQuota.limit.requestsMemory){
          alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_RSRC_LIMIT_EXCEED_QUOTA'))
          return
        }

        if (this.containerDefaultResourceLimit.limitsCpu > this.namespaceDefaultResourceQuota.limit.limitsCpu) {
          alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_CONTAINER_RSRC_LIMIT_CANNOT_EXCEED_NAMESPACE'))
          return
        }
        if(this.containerDefaultResourceLimit.requestsCpu > this.namespaceDefaultResourceQuota.limit.requestsCpu){
          alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_CONTAINER_RSRC_LIMIT_CANNOT_EXCEED_NAMESPACE'))
          return
        }
        if(this.containerDefaultResourceLimit.limitsMemory > this.namespaceDefaultResourceQuota.limit.limitsMemory){
          alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_CONTAINER_RSRC_LIMIT_CANNOT_EXCEED_NAMESPACE'))
          return
        }
        if(this.containerDefaultResourceLimit.requestsMemory > this.namespaceDefaultResourceQuota.limit.requestsMemory){
          alert(this.$t('service.SVCM_DIAL_NEWNAMESPACE_ALERT_CONTAINER_RSRC_LIMIT_CANNOT_EXCEED_NAMESPACE'))
          return
        }

        this.resourceQuota.limit.limitsCpu = this.resourceQuota.limit.limitsCpu.toString() + "m"
        this.namespaceDefaultResourceQuota.limit.limitsCpu = this.namespaceDefaultResourceQuota.limit.limitsCpu.toString() + "m"
        this.containerDefaultResourceLimit.limitsCpu = this.containerDefaultResourceLimit.limitsCpu.toString() + "m"
        this.resourceQuota.limit.requestsCpu = this.resourceQuota.limit.requestsCpu.toString() + "m"
        this.namespaceDefaultResourceQuota.limit.requestsCpu = this.namespaceDefaultResourceQuota.limit.requestsCpu.toString() + "m"
        this.containerDefaultResourceLimit.requestsCpu = this.containerDefaultResourceLimit.requestsCpu.toString() + "m"
        this.resourceQuota.limit.limitsMemory = this.resourceQuota.limit.limitsMemory.toString() + "Mi"
        this.namespaceDefaultResourceQuota.limit.limitsMemory = this.namespaceDefaultResourceQuota.limit.limitsMemory.toString() + "Mi"
        this.containerDefaultResourceLimit.limitsMemory = this.containerDefaultResourceLimit.limitsMemory.toString() + "Mi"
        this.resourceQuota.limit.requestsMemory = this.resourceQuota.limit.requestsMemory.toString() + "Mi"
        this.namespaceDefaultResourceQuota.limit.requestsMemory = this.namespaceDefaultResourceQuota.limit.requestsMemory.toString() + "Mi"
        this.containerDefaultResourceLimit.requestsMemory = this.containerDefaultResourceLimit.requestsMemory.toString() + "Mi"
        this.goRight();
      }
      
    },
    goNull(){
      backendapi.put(`/project/${this.strProject}/namespace/${this.strNamespace}`,{
        "description": this.spacetext,
        "containerDefaultResourceLimit":{},
        "resourceQuota":{}
      }).then((response) => {
        this.$emit('childMethod');  
      })
      .catch(error => {
        alert(error.message)
      });
    },
    goRight(){
      backendapi.put(`/project/${this.strProject}/namespace/${this.strNamespace}`,{
        "description": this.spacetext,
        "containerDefaultResourceLimit":this.containerDefaultResourceLimit,
        "resourceQuota":this.namespaceDefaultResourceQuota
      }).then((response) => {
        this.$emit('childMethod');  
      })
      .catch(error => {
        alert(error.message)
      });
    }
  }
}
</script>

<style>
#tableResource.table input{
  max-width: 85%;
  display: inline;
}
#tableResource.table .thead-dark th{
  background-color: #3f51b5;
}
</style>


