<template>
<div>

  <div class="card card-default">
      <div class="card-body">         
        <fieldset>
            <div class="form-group row">
                <label class="col-4 col-form-label">{{$t("service.SVCM_DIAL_SVCRBK_SELLBL_VER_RECS")}}</label>
                <div class="col-8">
                  <select class="custom-select custom-select-sm" v-model="replicaSetId" @change="selectrollback($event)">
                    <option v-for="(item, index) in rollback" :value="item.id" >{{item.id}}</option>
                  </select>
                </div>
            </div>
        </fieldset>
        <fieldset>
          <!-- <dl class="row">
            <dt class="col-sm-3">暫存:</dt>
            <dd class="col-sm-9">{{name}}</dd>
          </dl> -->
          <dl class="row">       
            <dt class="col-sm-3">{{$t("service.SVCM_DIAL_SVCRBK_LBL_DEPLOY_TIME")}}</dt>
            <dd class="col-sm-9">{{time}}</dd>
          </dl>
        </fieldset>  
      </div>
  </div>
  <div class="row m-0 justify-content-end">
    <b-button size="sm" variant="success" @click="ok()" >提交</b-button>
  </div>  
</div>
</template>  

<script>
import Vue from 'vue';

export default {
  props: ['strProject','strNamespace','strservicename'],
  components: {

  },
  data(){
    return{
      rollback:[],
      replicaSetId:"",
      name:'',
      time:'',
    }  
  },

  created(){
    backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/service/${this.strservicename}/rollback`).then((response) => {
      response.data.map((item, index) => {
        this.rollback.push(item)
      }); 
      this.isLoading = false;
    })
    .catch(error => {
      alert(error.message);
      this.isLoading = false;
    });
  },

  methods:{
    selectrollback(event){
      var a = event.target.selectedIndex
      this.time = moment(this.rollback[a].created).format('YYYY-MM-DD HH:mm:ss')
      this.name = this.rollback[a].id
    },
    ok(){
    backendapi.put(`/project/${this.strProject}/namespace/${this.strNamespace}/service/${this.strservicename}/rollback`,{
      replicaSetId:this.replicaSetId
    }).then((response) => {
      this.$emit('childMethod');  
    })
    .catch(error => {
      alert(error.message);
      this.isLoading = false;
    });
    }
  }
}
</script>
    
<style src="vue-form-wizard/dist/vue-form-wizard.min.css"></style>

<style>
.vue-form-wizard .wizard-nav-pills>li>a>span {
    margin-left: 0rem;
    text-align: center;
    width: 100%;
}
.vue-form-wizard .wizard-nav-pills>li>a {
    padding: .5rem !important;
    border-radius: 7px;
}
.vue-form-wizard.md .wizard-icon-circle {
    font-size: 15px;
}
.vue-form-wizard.md .wizard-nav-pills>li.active>a .wizard-icon {
    font-size: 15px;
}
.vue-form-wizard .wizard-nav-pills>li>a>span>div {
    font-weight:bold;
}
.cloudDrive, .NFS, .config, .cloudDrive:last-child, .NFS:last-child, .config:last-child{
  padding: 20px;
  border: 1px dashed #eee;
  margin-bottom: 20px;
}
.form-group h4{
  text-align: center;
}
.deployform .form-inline{
  margin: 10px 0;
}
.deployform input{
  margin: 0px 5px;
}
.deployform fieldset{
  padding: 5px 0;
  margin: 0;
}
fieldset:last-child{
  margin-bottom: 0;
}

span.s_comment{
  display: block;
  font-size: 13px;
  margin-top: 10px;
}

.must_write{
  color:red;
  font-size: 13px;
  margin-left: 5px;
}

.input_tooltip{
    padding: 0px;
    box-shadow: none;
    border: none;
}
.input_tooltip i{
    font-size: 24px;
    color:red;
}
</style>



