<template>
<div>

  <!-- 讀取畫面 -->
  <loading :active.sync="isLoading"></loading>

  <div class="content-heading">{{$t("sidebar.VOLUME_SET")}}</div>
 
  <!-- Select選項開始 -->
  <b-form class="was-validated">
    <div class="form-group row justify-content-end">
      <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_PRJ")}}:</label>
      <div class="col-md-4">
        <select id="projectlist" class="custom-select custom-select-md mb-3" @change="selectedproject()" v-model="strProject">
          <option value disabled selected>{{$t("option.PROJECT")}}</option>
          <option v-for="(item) in projectlist" :value="item.name" :key="item.name">{{item.name}}</option>
        </select>
      </div>
      <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_NAMESPACE")}}:</label>
      <div class="col-md-4">
        <select id="namespacelist" class="custom-select custom-select-md mb-3" @change="selectednamespace()" :disabled="disabled" v-model="strNamespace">
          <option id="myselected" value disabled selected v-model="selecttt">{{$t("option.NAMESPACE")}}</option>
          <option v-for="item in namespacelist" :value="item.name" :key="item.name">{{item.name}}</option>
        </select>
      </div>
    </div>
  </b-form>
  <!-- Select選項結束 -->

  <!-- 專案表單開始 -->
  <ProjectTable :strProject="strProject" @getPjname="getPjname" v-show="showProject"/>
  <!-- 專案表單結束 -->


  <!-- 命名空間表單開始 -->
  <NamespaceTable :strProject="strProject" :tableDataNamespace="tableDataNamespace" @getNamespace="getNamespace" v-if="showNamespace"/>
  <!-- 命名空間表單結束 -->


  <!-- 新建雲硬碟開始 -->
  <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" v-b-modal.createVolumes v-show="showVolume">{{$t("service.VOLUME_SELLBL_BTN_CRTE_VOLUME")}}</b-btn>
  <br>
  <br>
  <b-modal class="modal fade" id="createVolumes" :title="$t('service.VOLUME_SELLBL_BTN_CRTE_VOLUME')" ref="modal"  @show="resetModal" @hidden="resetModal" @ok="handleOk">
    <form ref="form" @submit.stop.prevent="handleSubmit">
        <fieldset>
            <div class="form-group row align-items-end">
                <label class="col-5 col-form-label">{{$t("service.VOLUME_DIAL_CRTEVOL_IPTLBL_VOLUME_NAME")}}</label>
                <div class="col-6">
                    <b-form-input v-model="newVolumes.name" :class="{'form-control':true}" type="text" required />
                </div>
            </div>
        </fieldset>
        <fieldset>
            <div class="form-group row align-items-end">
                <label class="col-5 col-form-label">{{$t("service.VOLUME_DIAL_CRTEVOL_SELLBL_VOLUME_QUOTA")}}</label>
                <div class="col-5">
                    <b-form-input v-model="newVolumes.size" :class="{'form-control':true}" type="number" min="1" />
                </div>
                <div class="col-1">
                  <span>Gi</span>
                </div>
            </div>
        </fieldset>
        <fieldset>
            <div class="form-group row align-items-end">
                <label class="col-5 col-form-label">{{$t("service.VOLUME_DIAL_CRTEVOL_SELLBL_VOLUME_TYPE")}}</label>
                <div class="col-6">
                    <select id="storageClasslist" class="custom-select custom-select-sm" @change="selectedstorage()">
                      <option value="" selected disabled>{{$t('service.VOLUME_HINT_SEL_VOLUME_TYPE')}}</option>
                      <option v-for="(item) in newVolumes.storageClassId" :value="item.name" >{{item.name}}</option>
                    </select>
                </div>
            </div>
        </fieldset>             
    </form>
    <template v-slot:modal-footer="{ok, cancel}">
      <b-button size="sm" variant="success" @click="ok()">{{$t("service.VOLUME_DIAL_CRTEVOL_BTN_SUBMIT")}}</b-button>
      <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.REGISTRY_DIAL_CRTECERT_BTN_CANCEL")}}</b-button>
    </template>
  </b-modal>
  <!-- 新建雲硬碟結束 -->
  
  <!-- 分類表單開始 -->
  <v-client-table :data="tableData" :columns="columns" :options="options" v-show="showVolume">
    <template slot="state" slot-scope="props">
      <div>
        <div class="p-2" :class="[unactive, props.row.state=='bound'? active : '']">
          {{props.row.state}}
        </div>  
      </div>
    </template>
    <template slot="action" slot-scope="props">
      <div>
        <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)" v-b-tooltip.hover title="刪除"><i class="fas fa-trash-alt"></i></b-btn>
      </div>
    </template>
  </v-client-table>
  <!-- 分類表單結束 -->

</div>
</template>  

<script>
import Vue from 'vue';
import { ClientTable,  Event } from 'vue-tables-2';
import swal from 'sweetalert2'
import ProjectTable from "./service/ProjectTable";
import NamespaceTable from "./service/NamespaceTable";
Vue.use(ClientTable);

export default {
  components: {
    ClientTable,
    ProjectTable,
    NamespaceTable
  },
  data(){
    return{
      unactive:'unactive',
      active:'active',
      timer1:'',
      selectedIndex: '',
      selectedProject: '',
      alldata:[],
      projectlist:[],
      strProject:'',
      namespacelist:[],
      strNamespace:'',
      strvolumesname:'',
      tableData: [],
      disabled: true,
      tableDataNamespace :[],
      showProject: true,
      showNamespace: false,
      showVolume: false,
      selecttt:'',
      newVolumes:{
        name:'',
        nameState:null,
        size:'1',
        storageClassId:[
          {
            name:'sata'
          },
          {
            name:'ssd-200'
          },
          {
            name:'ssd-400'
          },
          {
            name:'ssd-600'
          }
        ],
        selectedstorage:''
      },

      columns: ['name', 'state', 'size', 'action'],
      options: {
        sortable: ['name', 'state', 'size'],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder:this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count:this.$t('common.LBL_TOTAL') + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults:this.$t('common.LBL_NO_RECORD'),
        },
        headings: {
            name: () => this.$t("service.VOLUME_SELLBL_LST_VOLUME_NAME"),
            state: () => this.$t("service.VOLUME_SELLBL_LST_STATUS"),
            size: () => this.$t("service.VOLUME_SELLBL_LST_QUOTA"),
            action: () => this.$t("service.VOLUME_SELLBL_LST_OPT"),
        },
        columnsClasses:{
          state: "state",
        },
        pagination: { chunk: 5 },
        sortIcon: {
            is: 'fa-sort',
            base: 'fas',
            up: 'fa-sort-up',
            down: 'fa-sort-down'
        },
      },

      isNew: false,
      isLoading: false,
    };
  },
  created(){
    this.isLoading = true;
    backendapi.get("/project").then((response) => {
      this.isLoading = false;
      this.projectlist = response.data;
    })
    .catch(error => {
      alert(error.message)
      this.isLoading = false;
    });
  },
  methods:{

    getPjname(PJname){
      this.strProject = PJname
      this.isLoading = true;
      clearInterval(this.timer1);
      this.timer1 = setInterval(()=> {this.reloadd1()}, 3000);
      backendapi.get(`/project/${this.strProject}/namespace`)
        .then(response => {
          this.namespacelist = response.data;
          this.tableDataNamespace = response.data.map((item, index) => {
            item["id"] = index;
            item['createdTS'] = moment(item.createdTS);
            return item;
          });
          this.selected = true;
          this.showProject = false
          this.showNamespace = true
          this.showVolume = false
          this.isLoading = false
          document.getElementById("myselected").selected = true;
          if (this.namespacelist.length >= 1) {
            this.disabled = false;
          } else {
            this.disabled = true;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },

    getNamespace(namespace){
      this.strNamespace = namespace
      clearInterval(this.timer1);
      this.timer1 = setInterval(()=> {this.reloadd1()}, 3000);
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/persistentvolumeclaim`).then((response) => {
        this.tableData = response.data.map((item, index) => {
            item['id'] = index
            return item
        });
        this.showProject = false
        this.showNamespace = false
        this.showVolume = true
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    },

    reloadd1(){
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/persistentvolumeclaim`).then((response) => {
        this.tableData = response.data.map((item, index) => {
            item['id'] = index
            return item
        });
      })
    },
    // 選取專案 //
    selectedproject() {
      var projectlist = document.getElementById("projectlist");
      this.strProject = projectlist.options[projectlist.selectedIndex].value;
      this.isLoading = true;
      this.tableData = []
      backendapi.get(`/project/${this.strProject}/namespace`).then((response) => {
        this.namespacelist = response.data
        this.tableDataNamespace = response.data.map((item, index) => {
          item["id"] = index;
          item['createdTS'] = moment(item.createdTS);
          return item;
        });
        this.selected = true
        this.isLoading = false
        this.showProject = false
        this.showNamespace = true
        this.showVolume = false
        document.getElementById("myselected").selected = true;
        if (this.namespacelist.length >= 1 ){
          this.disabled = false
        }else{
          this.disabled = true
        }
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    },
    // 選取命名空間 //
    selectednamespace() {
      var namespacelist = document.getElementById("namespacelist");
      this.strNamespace = namespacelist.options[namespacelist.selectedIndex].value;
      clearInterval(this.timer1);
      this.timer1 = setInterval(()=> {this.reloadd1()}, 3000);
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/persistentvolumeclaim`).then((response) => {
        this.tableData = response.data.map((item, index) => {
            item['id'] = index
            return item
        });
        this.showProject = false
        this.showNamespace = false
        this.showVolume = true
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    },
    // 選取雲硬碟類型 //
    selectedstorage(){
      var storageClasslist = document.getElementById("storageClasslist");
      this.newVolumes.selectedstorage = storageClasslist.options[storageClasslist.selectedIndex].value;
    },
    // 驗證 //
    checkFormValidity() {
      const vm = this;
      const valid = vm.$refs.form.checkValidity();
      this.newVolumes.nameState = valid ? 'valid' : 'invalid'
      return valid
    },
    // 驗證 //
    handleOk(e) {
      const vm = this;
      e.preventDefault()
      vm.handleSubmit()
    },
    // 預設值 //
    resetModal() {
      this.newVolumes.name = ''
      this.newVolumes.nameState = null
    },
    // 新建雲硬碟送出 //
    handleSubmit() {
      if (!this.checkFormValidity()) {
        return
      }
      this.isLoading = true;
      backendapi.post(`/project/${this.strProject}/namespace/${this.strNamespace}/persistentvolumeclaim`,{
        name: this.newVolumes.name,
        size: this.newVolumes.size + 'Gi',
        storageClassId: this.newVolumes.selectedstorage
      }).then((response) => {
        this.selectednamespace()
        this.isLoading = false;
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });

      this.$nextTick(() => {
        this.$refs.modal.hide()
      });
    },
    // 分類表單刪除 //
    remove (row) {
      this.tableData.filter(item => row.id !== item.id);
      this.strvolumesname = this.tableData[row.id].name;
      backendapi.delete(`/project/${this.strProject}/namespace/${this.strNamespace}/persistentvolumeclaim/${this.strvolumesname}`).then(response => {
        this.selectednamespace();
        this.isLoading = false;      
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });
    },
    // 刪除步驟 //
    checkCancel(row) { 
      swal({
          title: this.$t('service.VOLUME_DIAL_DEL_VOLUME_MSG'),
          text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: this.$t('common.BTN_CONFIRM'),
          cancelButtonText:this.$t('common.BTN_CANCEL'),
          heightAuto: false
      }).then((result) => {
          if(result.value){
          this.remove(row);
            swal({
                title: this.$t('common.MSG_DEL_SUCCESS'),
                confirmButtonText: this.$t('common.BTN_OK'),
                type: "success",
                heightAuto: false
            });
          } 
      });
    },
  },
 destroyed: function() {
    console.log('volume timer destroyed');
    clearInterval(this.timer1);
  }
}
</script>

<style>
#createVolumes .row{
  margin:0 15px;
}
fieldset:last-child{
  margin-bottom: 0;
}
</style>


