<template>
<div>

  <!-- 讀取畫面 -->
  <loading :active.sync="isLoading"></loading>

  <div class="content-heading">{{$t("sidebar.REGISTRY")}}</div>

  <!-- 鏡像倉庫地址開始 -->
  <dl class="row" v-show="showLink">
    <dt style="margin-left:15px;">{{$t("service.REGISTRY_LBL_REGISTRY_ADDR")}}:</dt>
    <dd class="col-sm-10"><a :href="'http://'+ this.harbor_address" target="_blank">{{this.harbor_address}}</a></dd>
  </dl>
  <!-- 鏡像倉庫地址結束 -->

  <!-- Select選項開始 -->
  <b-form class="was-validated">
    <div class="form-group row justify-content-end">
      <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_PRJ")}}:</label>
      <div class="col-md-4">
        <select id="projectlist" class="custom-select custom-select-md mb-3" @change="selectedproject()" v-model="strProject">
          <option value disabled selected>{{$t("option.PROJECT")}}</option>
          <option v-for="(item) in projectlist" :value="item.name" :key="item.name">{{item.name}}</option>
        </select>
      </div>
    </div>
  </b-form>
  <!-- Select選項結束 -->

  <!-- 新建鏡像庫開始 -->
  <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" v-b-modal.createImages v-show="showImage">{{$t("service.REGISTRY_BTN_CRTE_REG_CERT")}}</b-btn>
  <br>
  <br>
  <b-modal class="modal fade" id="createImages" :title="$t('service.REGISTRY_BTN_CRTE_REG_CERT')" ref="modal"  @show="resetModal" @hidden="resetModal" @ok="handleOk">
    <form ref="form" @submit.stop.prevent="handleSubmit">
        <fieldset>
          <dl class="row">
            <dt class="col-sm-4">{{$t("service.REGISTRY_SELLBL_PRJ_NAME")}}</dt>
            <dd class="col-sm-8">{{strProject}}</dd>
          </dl>
        </fieldset>
        <fieldset>
            <div class="form-group row">
                <label class="col-4 col-form-label">{{$t("service.REGISTRY_DIAL_CRTECERT_IPTLBL_CERT_NAME")}}</label>
                <div class="col-8">
                    <b-form-input v-model="newImages.name" :class="{'form-control':true}" type="text" required/>
                </div>
            </div>
        </fieldset>
        <fieldset>
          <dl class="row">
            <dt class="col-sm-4">{{$t("service.REGISTRY_DIAL_CRTECERT_LBL_REG_ADDR")}}</dt>
            <dd class="col-sm-8">{{harbor_address}}</dd>
          </dl>
        </fieldset>
        <fieldset>
            <div class="form-group row">
                <label class="col-4 col-form-label">{{$t("service.REGISTRY_DIAL_CRTECERT_IPTLBL_ACNT")}}</label>
                <div class="col-8">
                    <b-form-input v-model="newImages.registries.username" :class="{'form-control':true}" type="text" required/>
                </div>
            </div>
        </fieldset>
        <fieldset>
            <div class="form-group row">
                <label class="col-4 col-form-label">{{$t("service.REGISTRY_DIAL_CRTECERT_IPTLBL_PWD")}}</label>
                <div class="col-8">
                    <b-form-input v-model="newImages.registries.passward" :class="{'form-control':true}" type="password" required/>
                </div>
            </div>
        </fieldset> 
        <fieldset>
            <div class="form-group row">
                <label class="col-4 col-form-label">{{$t("service.REGISTRY_DIAL_CRTECERT_IPTLBL_CONFIRM_PWD")}}</label>
                <div class="col-8">
                    <b-form-input v-model="newImages.registries.repassward" :class="{'form-control':true}" type="password" required/>
                </div>
            </div>
        </fieldset>                  
    </form>
    <template v-slot:modal-footer="{ok, cancel}">
      <b-button size="sm" variant="success" @click="ok()">{{$t("service.REGISTRY_DIAL_CRTECERT_BTN_SUBMIT")}}</b-button>
      <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.REGISTRY_DIAL_CRTECERT_BTN_CANCEL")}}</b-button>
    </template>
  </b-modal>
  <!-- 新建鏡像庫結束 -->

  <!-- 專案表單開始 -->
  <ProjectTable :strProject="strProject" @getPjname="getPjname" v-show="showProject"/>
  <!-- 專案表單結束 -->
  
  <!-- 分類表單開始 -->
  <v-client-table :data="tableData" :columns="columns" :options="options" v-show="showImage">
    <template slot="action" slot-scope="props">
      <div>
        <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)" v-b-tooltip.hover title="刪除"><i class="fas fa-trash-alt"></i></b-btn>
      </div>
    </template>
  </v-client-table>
  <!-- 分類表單結束 -->

</div>
</template>  

<script>
import Vue from 'vue';
import { ClientTable,  Event } from 'vue-tables-2';
import swal from 'sweetalert2'
import ProjectTable from "./service/ProjectTable";

Vue.use(ClientTable);

export default {
  components: {
    ClientTable,
    ProjectTable
  },
  data(){
    return{
      timer:'',
      selectedIndex:'',
      projectlist:[],
      strProject:'',
      harbor_address:'',
      tableData: [],
      showProject:true,
      showImage: false,
      showLink:false,
      newImages:{
        name:'',
        nameState: null,
        registries: {
            username:'',
            usernameState: null,
            passward:'',
            passwardState: null,
            repassward:'',
            repasswardState: null
        },
      },

      columns: ['name', 'username', 'action'],
      options: {
        sortable: ['name', 'username', 'date'],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder:this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count: this.$t('common.LBL_TOTAL') + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults:this.$t('common.LBL_NO_RECORD'),
        },
        headings: {
            name: () => this.$t('service.REGISTRY_DIAL_CRTECERT_IPTLBL_CERT_NAME'),
            username: () => this.$t('service.REGISTRY_DIAL_CRTECERT_IPTLBL_ACNT'),
            action: () => this.$t('service.REGISTRY_CERTLST_OPT'),
        },
        pagination: { chunk: 5 },
        sortIcon: {
            is: 'fa-sort',
            base: 'fas',
            up: 'fa-sort-up',
            down: 'fa-sort-down'
        },
      },

      isNew: false,
      isLoading: false,
    };
  },
  created(){
    this.isLoading = true;
    backendapi.get("/project").then((response) => {
      this.isLoading = false;
      this.projectlist = response.data;
    })
    .catch(error => {
      alert(error.message)
    });
  },
  methods:{

    getPjname(PJname){
      this.strProject = PJname
      clearInterval(this.timer1);
        backendapi.get(`/project/${this.strProject}/registry`).then((response) => {
          this.harbor_address = response.data.harbor_address
          this.isLoading = false;

          const b = response.data.credentail_list.map(item => {
            for(let i in item["registries"]){
              return item["registries"][i]
            }
          })

          const c = response.data.credentail_list.map(item => {
            return item
          })
          
          let merged = [];

          for(let i=0; i<b.length; i++) {
            merged.push({...b[i],...(c[i])});
          }

          this.tableData = merged.map((item, index) => {
            item["id"] = index;
            return item;
          });
          
          this.showProject = false
          if (this.tableData !== "" ){
            this.showImage = true
          }else{
            this.showImage = false;
          }

          this.showLink = true

        })
        .catch(error => {
          this.isLoading = false;
          alert(error.message)
        });
    },

    reloadd1(){
      backendapi.get(`/project/${this.strProject}/registry`).then((response) => {
        this.harbor_address = response.data.harbor_address
        const b = response.data.credentail_list.map(item => {
          for(let i in item["registries"]){
            return item["registries"][i]
          }
        })
        const c = response.data.credentail_list.map(item => {
          return item
        })
        let merged = [];
        for(let i=0; i<b.length; i++) {
          merged.push({...b[i],...(c[i])});
        }
        this.tableData = merged.map((item, index) => {
          item["id"] = index;
          return item;
        });
        if (this.tableData !== "" ){
          this.showImage = true
        }else{
          this.showImage = false;
        }
      })
    },
    // 選取專案 //
    selectedproject() {
        var projectlist = document.getElementById("projectlist");
        this.strProject = projectlist.options[projectlist.selectedIndex].value;
        this.isLoading = true;
        clearInterval(this.timer1);
        this.timer1 = setInterval(()=> {this.reloadd1()}, 3000);
        backendapi.get(`/project/${this.strProject}/registry`).then((response) => {
          this.harbor_address = response.data.harbor_address
          this.isLoading = false;

          const b = response.data.credentail_list.map(item => {
            for(let i in item["registries"]){
              return item["registries"][i]
            }
          })

          const c = response.data.credentail_list.map(item => {
            return item
          })
          
          let merged = [];

          for(let i=0; i<b.length; i++) {
            merged.push({...b[i],...(c[i])});
          }

          this.tableData = merged.map((item, index) => {
            item["id"] = index;
            return item;
          });
          
          this.showProject = false
          if (this.tableData !== "" ){
            this.showImage = true
          }else{
            this.showImage = false;
          }

           this.showLink = true

        })
        .catch(error => {
          this.isLoading = false;
          alert(error.message)
        });
    },
    // 驗證 //
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity();

      this.newImages.nameState = valid ? 'valid' : 'invalid'
      this.newImages.registries.usernameState = valid ? 'valid' : 'invalid'
      this.newImages.registries.passwardState = valid ? 'valid' : 'invalid'
      this.newImages.registries.repasswardState = valid ? 'valid' : 'invalid'

      if(this.newImages.name ==''){
        alert(this.$t('service.REGISTRY_DIAL_CRTECERT_ALERT_NO_ACCESS_CLAIM_NAME'));
      }else if (this.newImages.registries.username ==''){
        alert(this.$t('service.REGISTRY_DIAL_CRTECERT_ALERT_NO_USER_NAME'));
      }else if (this.newImages.registries.passward!==this.newImages.registries.repassward){
        alert(this.$t('service.REGISTRY_DIAL_CRTECERT_ALERT_INCONSISTENT_PWD'));
        this.newImages.registries.passward  = ""
        this.newImages.registries.repassward = ""
      }else{
        return valid;
      }

    },
    // 驗證 //
    handleOk(e) {
      e.preventDefault()
      this.handleSubmit()
    },
    // 預設值 //
    resetModal() {
      this.newImages.name = ''
      this.newImages.registries.username = ''
      this.newImages.registries.passward = ''
      this.newImages.registries.repassward = ''
      this.newImages.nameState = null
      this.newImages.registries.usernameState = null
      this.newImages.registries.passwardState = null
      this.newImages.registries.repasswardState = null
    },
    // 新建鏡像倉庫送出 //
    handleSubmit() {
      if (!this.checkFormValidity()) {
        return 
      }else{
        this.isLoading = true;
        backendapi.post(`/project/${this.strProject}/registry`,{
          name: this.newImages.name,
          registries: 
            {
              address:  this.harbor_address,
              username: this.newImages.registries.username,
              password: this.newImages.registries.passward
            }
        }).then((response) => {
          this.isLoading = false;
          this.selectedproject();
          this.$nextTick(() => {
            this.$refs.modal.hide()
          });
        })
        .catch(error => {
          alert(error.message);
        });
      }
    },
    // 分類表單刪除 //
    remove (row) {
      this.tableData.filter(item => row.id !== item.id);
      this.strimagename = this.tableData[row.id].name;
      backendapi.delete(`/project/${this.strProject}/registry/${this.strimagename}`).then(response => {
        this.selectedproject();
          this.isLoading = false;
          
        })
        .catch(error => {
          alert(error.message);
        });
    },
    // 刪除步驟 //
    checkCancel(row) { 
      swal({
          title: this.$t('service.REGISTRY_DIAL_DELCERT_MSG'),
          text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: this.$t('common.BTN_CONFIRM'),
          cancelButtonText: this.$t('common.BTN_CANCEL'),
          heightAuto: false
      }).then((result) => {
          if(result.value){
          this.remove(row);
            swal({
                title: this.$t('common.MSG_DEL_SUCCESS'),
                confirmButtonText: this.$t('common.BTN_OK'),
                type: "success",
                heightAuto: false
            });
          } 
      });
    },
  },
   destroyed: function() {
    console.log('image timer destroyed');
    clearInterval(this.timer1);
  }
}
</script>

<style>
#createImages .row{
  margin:0 15px;
}
fieldset:last-child{
  margin-bottom: 0;
}
</style>


