<template>
    <div>
        <navbar/>
        <div class="row" v-if="!isShow"  style="max-width: 1040px; margin:100px auto;">
            <div class="col-lg-4" >
                <div class="card b">
                    <div class="card-body text-center">
                        <a class="link-unstyled text-danger" href="#" @click.prevent="toControl()">
                            <em class="fa-5x fas fa-tv mb-3"></em>
                            <br/>
                            <span class="h4">{{$t("center.CONTROL")}}</span>
                            <br/>
                        </a>
                    </div>
                </div>
            </div>
            <div class="col-lg-4">
                <div class="card b">
                    <div class="card-body text-center">
                        <a class="link-unstyled text-info" href="#" @click.prevent="toService()">
                            <em class="fa-5x fa fa-object-group mb-3"></em>
                            <br/>
                            <span class="h4">{{$t("center.SERVICE")}}</span>
                            <br/>
                        </a>
                    </div>
                </div>
            </div>
            <div class="col-lg-4">
                <div class="card b">
                    <div class="card-body text-center">
                        <a class="link-unstyled text-purple" href="#">
                            <em class="fa-5x fa fa-cubes mb-3"></em>
                            <br/>
                            <span class="h4">{{$t("center.CATALOG")}}</span>
                            <br/>
                        </a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import AllMenu from './AllMenu'
import Sidebar from './Sidebar'
import Navbar from './Navbar'
import ContentWrapper from './ContentWrapper'
import Dashboard from './Dashboard'

export default {
    data() {
        return {
            isShow: false,
            menuType: "control"
        }
    },
    components:{  
      Sidebar,
      Navbar,
      ContentWrapper,
      Dashboard
  },
  mounted(){
    
    var curLocale = this.$i18n.locale;
    console.log("Center locale", this.$i18n.locale);

    if(curLocale == "ch"){
      this.selectedLanguage = "简体中文";

    }else if(curLocale == "tw") {
      this.selectedLanguage = "繁體中文";

    }else if(curLocale == "en") {
      this.selectedLanguage = "English";
    //   document.querySelector('.aside-inner').style.width = "222px";

    }else {
      this.selectedLanguage = "繁體中文";
    }    
  },
  created(){
  },
  
  methods: {
      toControl() {
        localStorage.setItem("current-type", 'control');
        this.$router.push({
        path: "/admin/charts", 
        });
        this.$router.go(0)
      },
      toService() {
          localStorage.setItem("current-type", 'service');
          this.$router.push({
              path: "/admin/service",
            });    
        },
      toCatalog() {

      }

  },
};
</script>

<style>
#section.section {
    margin-top: 64px;
}
</style>



