<template>
  <div>
      <form ref="form">
        <div class="form-group">
          <label for="password">{{$t('navbar.NEW_PWD')}}</label>
          <b-form-input type="password" id="newPassword" v-model="newPassword" :placeholder="$t('navbar.HINT_INPUT_NEW_PWD')" required></b-form-input>
        </div>
        <div class="form-group">
          <label for="password">{{$t('navbar.CONFIRM_PWD')}}</label>
          <b-form-input type="password" id="newPasswordAgain" v-model="newPasswordAgain" :placeholder="$t('navbar.HINT_CONFIRM_PWD')" required></b-form-input>
        </div>
      </form>
      <div class="modal-footer mt-4">
        <b-button size="sm" variant="success" @click="okk()">{{$t("service.ACNT_DIAL_NACNT_BTN_SUBMIT")}}</b-button>
        <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.ACNT_DIAL_NACNT_BTN_CANCEL")}}</b-button>
      </div>
  </div>
</template>

<script>
import Vue from 'vue';
import swal from 'sweetalert2'
export default {
  props:["userid"],
  components: {
  },
  data(){
    return{
      newPassword:'',
      newPasswordAgain:'',
    };
  },
  methods:{
    // 預設值 //
    resetModal() {
      this.newPassword = ''
      this.newPasswordAgain = ''
    },
    okk(e){
      this.newPassword.trim();
      this.newPasswordAgain.trim();
      if(this.newPassword==''){
        alert(this.$t('navbar.ALERT_NO_NEW_PWD'))
      }else if(this.newPasswordAgain==''){
        alert(this.$t('navbar.ALERT_NO_CONFIRM_PWD'))
      }else if(this.newPassword!==this.newPasswordAgain){
        alert(this.$t('navbar.ALERT_CONFIRM_PWD_FAIL'))
      }else{
        this.submit()
      }
    },
    cancel(){
     this.$emit('loadTable')
    },
    submit(){
      backendapi.put(`/user/${this.userid}`,{
        password: this.newPasswordAgain
      }).then((response) => {
        this.$emit('loadTable')
        swal({
            title: this.$t('navbar.PWD_MODIFY_SUCCESS'),
            confirmButtonText: this.$t('common.BTN_OK'),
            type: "success",
            heightAuto: false
        });
      })
      .catch(error => {
          this.$emit('loadTable')
          swal({
              title: this.$t('navbar.PWD_MODIFY_FAIL'),
              confirmButtonText: this.$t('common.BTN_OK'),
              type: "error",
              heightAuto: false
          });
      }); 
    },
    // 重設密碼區結束 //
  },
}
</script>
    




