<template>
  <div>
  <div v-if="role">  
    <!-- 讀取畫面 -->
    <loading :active.sync="isLoading"></loading>

    <!-- 大標題 -->
    <div class="content-heading">{{$t("sidebar.RESOURCE_MEM")}}</div>

    <!-- 新建資源池按鈕 -->
    <b-btn
      type="button"
      class="btn btn-primary mr-1 mb-1"
      variant="primary"
      @click="show = !show"
    >{{$t("service.RSRC_BTN_CRTE_RSRC_POOL")}}</b-btn>
    <br />
    <br />

    <!-- 新建資源池開始 -->
    <transition name="slide">
      <aside v-show="show">
        <b-card class="mb-3 border">
          <form ref="form" @submit.stop.prevent="handleSubmit">
            <dl class="row">
              <dt class="col text-center">{{$t("service.RSRC_BTN_CRTE_RSRC_POOL")}}</dt>
            </dl>
            <fieldset>
              <div class="form-group">
                <label class="col col-form-label">{{$t("service.RSRC_DIAL_CRTEPOOL_POOL_TYPE")}}</label>
                <div class="col">
                  <select id="resourcetype" class="custom-select custom-select-sm" required v-model="selectedResource">
                    <option value=null disabled selected>{{$t("service.RSRC_DIAL_CRTEPOOL_HINT_SELECT_POOL_TYPE")}}</option>
                    <option v-for="(item) in newResource.type" :value="{ id: item.id, text: item.name }">{{item.name}}</option>
                  </select>
                </div>
              </div>
            </fieldset>
            <fieldset>
              <div class="form-group">
                <label class="col col-form-label">{{$t("service.RSRC_DIAL_CRTEPOOL_POOL_NAME")}}</label>
                <div class="col">
                  <input v-model="newResource.name" :class="{'form-control':true}" type="text" />
                </div>
              </div>
            </fieldset>
            <fieldset>
              <div class="form-group">
                <label class="col col-form-label">{{$t("service.RSRC_DIAL_CRTEPOOL_QUANTITY")}}</label>
                <div class="col">
                  <input v-model="newResource.num" :class="{'form-control':true}" type="number" min="1"/>
                </div>
              </div>
            </fieldset>
          </form>
          <div class="text-right">
            <b-button
              size="sm"
              variant="success"
              @click="handleSubmit()"
            >{{$t("service.RSRC_DIAL_CRTEPOOL_SUBMIT")}}</b-button>
            <b-button
              size="sm"
              variant="danger"
              @click="show = !show"
            >{{$t("service.RSRC_DIAL_CRTEPOOL_CANCEL")}}</b-button>
          </div>
        </b-card>
      </aside>
    </transition>
    <!-- 新建資源池結束 -->

    <!-- 分類表單開始 -->
    <div class="resourceTable" v-for="(itemm, index) in tableData" :key="index">
      <dl class="row resource-heading align-items-center" style="position:relative;">
        <dt>{{$t("service.RSRC_LBL_POOL_NAME")}}</dt>
        <dd>{{itemm.name}}  <b-btn class="ml-3 shadow-none" variant="danger" size="xs" @click.prevent="deletePool(itemm)"><i class="fas fa-trash-alt" v-b-tooltip.hover :title="$t('service.RSRC_ICON_HINT_DELPOOL')"></i></b-btn></dd>
        <dd class="text-right" style="position:absolute;right:10px;">
          <!-- <b-btn variant="outline-light" size="xs" @click.prevent="deletePool(itemm)">{{$t("service.RSRC_DIAL_DELPOOL_TITLE")}}</b-btn> -->
          <b-btn variant="outline-light" size="xs" @click.prevent="addNode(itemm)">{{$t("service.RSRC_BTN_ADD_NODE")}}</b-btn>
          <b-btn variant="outline-light" size="xs" @click.prevent="removeNode(itemm)" v-if="itemm.type.length > 1">{{$t("service.RSRC_BTN_REMOVE_NODE")}}</b-btn>
        </dd>
      </dl>
      <v-client-table :data="itemm.type" :columns="columns" :options="options">
        <template slot="action" slot-scope="props">
         <div>
            <b-btn variant="outline-danger" class="btn-xs" @click.prevent="refreshNode(props.row, itemm)" v-b-tooltip.hover :title="$t('common.ICON_TIP_REDEPLOY')">
              <i class="fas fa-sync-alt"></i>
            </b-btn>
          </div>
        </template>
      </v-client-table>
    </div>
    <!-- 分類表單結束 -->


  </div>  
  </div>
</template>

<script>
import Vue from "vue";
import { mapMutations } from "vuex";
import { ClientTable, Event } from "vue-tables-2";
import swal from "sweetalert2";

Vue.use(ClientTable);

export default {
  components: {
    ClientTable
  },
  data() {
    return {
      role:false,
      timer: '',
      selectedResource: null,
      selectedIndex: "",
      show: false,
      stringpool: "",
      tableData: [],
      newResource: {
        type: [],
        name: "",
        num: 1
      },
      columns: ["state", "requested-hostname", "cpu", "mem", "action"],
      options: {
        sortable: ["state", "requested-hostname", "cpu", "mem", "pods"],
        filterable: false,
        headings: {
          state: () => this.$t("service.RSRC_LST_STATUS"),
          "requested-hostname": () => this.$t("service.RSRC_LST_NODE"),
          cpu: () => this.$t("service.RSRC_LST_CPU"),
          mem: () => this.$t("service.RSRC_LST_MEM"),
          // pods: () => this.$t("service.RSRC_LST_POD"),
          action: () => this.$t("service.RSRC_LST_OPT")
        },
        pagination: { chunk: 5 },
        sortIcon: {
          is: "fa-sort",
          base: "fas",
          up: "fa-sort-up",
          down: "fa-sort-down"
        },
        templates: {
          cpu(h, row) {
            if (row.usage) {
              const usedcpu = row.usage.cpu.replace("m", "") / 1024;
              return `${usedcpu.toFixed(2)} / ${row.allocatable.cpu} Cores`;
            } else {
              return "n/a";
            }
            
          },
          mem(h, row) {
            if (row.usage) {
              var usedmem = row.usage.memory;
              const allmem =
                row.allocatable.memory.replace("Ki", "") / 1024 / 1024;
              
              if (!usedmem.includes("Mi")) {

                if(usedmem.includes("Ki")) {
                  usedmem = usedmem.replace("Ki", "");
                }
                return (
                  (usedmem / 1024 / 1024).toFixed(0).concat("Mi") +
                  " / " +
                  allmem.toFixed(2) +
                  "GiB"
                );
              }
              return `${usedmem} / ${allmem.toFixed(2)}GiB`;
            } else {
              return "n/a";
            }
          },
          pods(h, row) {
            if (row.usage) {
              const usedpods = row.usage.pods;
              const allpods = row.allocatable.pods;
              return `${usedpods} / ${allpods}`;
            } else {
              return "n/a";
            }
          }
        }
      },
      isNew: false,
      isLoading: false
    };
  },
  created() {
    if(localStorage.getItem('cluster-role') == 'cluster-owner'){
      this.role = true
    }
    this.isLoading = true;
    backendapi
      .get("/nodepools")
      .then(response => {
        response.data.data.forEach((item, index) => {
          var obj = {
            id: "",
            name: "",
            type: []
          };
          obj.id = item.id;
          obj.name = item.hostnamePrefix;
          if (!obj.name.includes("master")) {
            this.tableData.push(obj);
          }
        });
        this.reloadd();
      })
      .catch(error => {
        alert("error");
        this.isLoading = false;
      });

    // TODO: Setting Timer
    clearInterval(this.timer);
    this.timer = setInterval(this.reloadd, 10000);
  },
  methods: {
    reloadd() {
      backendapi
        .get("/node")
        .then(response => {
          this.tableData.map((item, index) => {
            console.log("node---", item);
            item.type = response.data.nodes.filter(
              node => node["node-pool-id"] === item.id
            );
            item["no"] = index;
            return item;
          });
          this.isLoading = false;
        })
        .catch(error => {
          alert("error");
          this.isLoading = false;
        });

      backendapi
        .get("/nodeTemplates")
        .then(response => {
          // To confirm the array is empty
          this.newResource.type = [];
          response.data.data.map((item, index) => {
            if (item.name.includes(localStorage.getItem("group-id"))) {
              this.newResource.type.push({name: item.name, id: item.id, index: index});
            }
            this.isLoading = false;
          });
        })
        .catch(error => {
          alert("error");
          this.isLoading = false;
        });
    },
    toggleOffsidebar() {
      this.toggleSetting("offsidebarOpen");
    },
    loadTable() {
      this.isLoading = true;
      this.tableData = [];
      backendapi
        .get("/nodepools")
        .then(response => {
          response.data.data.forEach((item, index) => {
            var obj = {
              id: "",
              name: "",
              type: []
            };
            obj.id = item.id;
            obj.name = item.hostnamePrefix;
            if (!obj.name.includes("master")) {
              this.tableData.push(obj);
            }
            console.log(this.tableData);
          });
          this.reloadd();
        })
        .catch(error => {
          alert("error");
          this.isLoading = false;
        });
    },
    checkFormValidity() {},
    handleOk(e) {
      const vm = this;
      e.preventDefault();
      vm.handleSubmit();
    },
    handleSubmit() {
      var resourcetype = document.getElementById("resourcetype");
      var selected = resourcetype.selectedIndex;
      backendapi
        .post("/nodepools", {
          nodeTemplateId: this.selectedResource.id,
          hostnamePrefix: `${this.selectedResource.text}-${this.newResource.name}`,
          quantity: this.newResource.num
        })
        .then(response => {
          if(response.data.type === "warning"){
            if(typeof response.data.message === 'string'){
              swal({
                title: this.$t('service.RSRC_ALERT_ADD_NODE_FAIL_IN_PROGRESS'),
                confirmButtonText: this.$t('common.BTN_OK'),
                type: "warning",
                heightAuto: false
              });
              // alert(response.data.message);
            }else{
              swal({
                title: this.$t('service.RSRC_ALERT_ADD_NODE_FAIL_INSUFFICIENT_RESOURCE'),
                html:
                  `${response.data.message.cpu}</br>
                  ${response.data.message.mem}</br>
                  ${response.data.message.storage}`,
                confirmButtonText: this.$t('common.BTN_OK'),
                type: "warning",
                heightAuto: false
              });
            }
          }else{
            this.show = false;
            this.loadTable();
          }
        })
        .catch(error => {
          this.isLoading = false;
        });
    },
    deletePool(row) {
      swal({
        title: this.$t('service.RSRC_DIAL_DELPOOL_MSG'),
        text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#DD6B55",
        confirmButtonText: this.$t('common.BTN_CONFIRM'),
        cancelButtonText: this.$t('common.BTN_CANCEL'),
        heightAuto: false
      }).then(result => {
        if (result.value) {
          this.tableData.filter(item => row.no !== item.no);
          this.stringpool = this.tableData[row.no].id;
          backendapi
            .delete(`/nodepools/${this.stringpool}`)
            .then(response => {
              this.loadTable();
            })
            .catch(error => {
              alert("error");
              this.isLoading = false;
            });
          swal({
            title: this.$t('common.MSG_DEL_SUCCESS'),
            confirmButtonText: this.$t('common.BTN_OK'),
            type: "success",
            heightAuto: false
          });
        }
      });
    },
    refreshNode(row, nodepoolData) {
      swal({
        title: this.$t('service.RSRC_DIAL_RENODE_MSG'),
        text: this.$t('service.RSRC_DIAL_RENODE_SUBMSG'),
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#DD6B55",
        confirmButtonText: this.$t('common.BTN_CONFIRM'),
        cancelButtonText: this.$t('common.BTN_CANCEL'),
        heightAuto: false
      }).then(result => {
        if (result.value) {
          backendapi
            .delete(`/node/${row.id}`)
            .then(response => {
              this.loadTable();
            })
            .catch(error => {
              alert("error");
              this.isLoading = false;
            });
        }
      });
    },
    addNode(nodepool) {
      console.log('itemm', nodepool.id);
      var quantity = nodepool.type.length + 1;
      backendapi
        .put(`/nodepools/${nodepool.id}`, {
          quantity: quantity
        })
        .then(response => {
          if(response.data.type === "warning"){
            if(typeof response.data.message === 'string'){
              swal({
                title: this.$t('service.RSRC_ALERT_ADD_NODE_FAIL_IN_PROGRESS'),
                confirmButtonText: this.$t('common.BTN_OK'),
                type: "warning",
                heightAuto: false
              });
              // alert(response.data.message);
            }else{
              swal({
                title: this.$t('service.RSRC_ALERT_ADD_NODE_FAIL_INSUFFICIENT_RESOURCE'),
                html:
                  `${response.data.message.cpu}</br>
                  ${response.data.message.mem}</br>
                  ${response.data.message.storage}`,
                confirmButtonText: this.$t('common.BTN_OK'),
                type: "warning",
                heightAuto: false
              });
            }
          }else{
            this.show = false;
            this.loadTable();
          }
        })
        .catch(error => {
          this.isLoading = false;
        });
    },
    removeNode(nodepool) {
      
      var quantity = nodepool.type.length - 1;

      backendapi
        .put(`/nodepools/${nodepool.id}`, {
          quantity: quantity
        })
        .then(response => {
          if(response.data.type === "warning"){
            alert(response.data.message);
          }else{
            this.show = false;
            this.loadTable();
          }
        })
        .catch(error => {
          this.isLoading = false;
        });
    }
  },
  destroyed: function() {
    //移除 vue instance 之後
    console.log('resource timer destroyed');
    clearInterval(this.timer);
  }
};
</script>

<style>
.resource-heading {
  margin: 0;
  padding: 10px 0;
  background-color: #747d9c;
  border-radius: 5px 5px 0 0;
  color: #fff;
  border-left: 0.5px solid #eee;
  border-right: 0.5px solid #eee;
}
.resource-heading dt {
  margin-bottom: 0;
  padding-left: 8px;
}
.resource-heading dd {
  margin-bottom: 0;
  padding-left: 8px;
}
.resourceTable thead tr {
  background-color: #747d9c;
}
.resourceTable thead tr th {
  color: #fff;
}
.resourceTable tbody tr {
  background-color: #fafbfc;
}
.resourceTable .VuePagination__count {
  display: none;
}
.slide-enter-active {
  transition: all 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.slide-leave-active {
  transition: all 0.1s ease;
}
.slide-enter,
.slide-leave-active {
  transform: translateX(100%);
}
aside {
  width: 300px;
  height: 100vh;
  background: transparent;
  position: fixed;
  z-index: 999999;
  top: 64px;
  right: 0;
}
aside .card {
  width: 300px;
  height: 100vh;
  border-radius: 0;
}
aside .card fieldset {
  margin-bottom: 10px;
}
</style>



