<template>
<div>

  <div class="card card-default">
      <div class="card-body">
        <dl class="row">
          <dt class="col-12">
            <label class="switch">
              <input type="checkbox" v-model="open" name="switchPort"/>
              <span class="ml-2"></span>
              <small class="ml-2">啟用時僅開放下列服務可存取</small>
            </label>
          </dt>
        </dl>         
        <fieldset v-if="open">
            <div class="form-group row m-0" v-for="(item1, index) in authorize">
              <label class="col-2 col-form-label text-right">Namespace:</label>
              <div class="col-4">
                <select class="custom-select custom-select-sm" v-model="item1.namespace">
                  <option v-for="(item, index) in namespace" :value="item" >{{item}}</option>
                </select>
              </div>
              <label class="col-2 col-form-label text-right">服務名稱:</label>
              <div class="col-4">
                <select class="custom-select custom-select-sm" v-model="item1.service">
                  <option v-for="(item, index) in service" :value="item" >{{item}}</option>
                </select>
              </div>
            </div>
            <div class="row m-0 justify-content-start">
              <b-button size="sm" variant="success" @click="add()" >新增</b-button>
            </div>  
        </fieldset>
      </div>
  </div>
  <div class="row m-0 justify-content-end">
    <b-button size="sm" variant="success" @click="ok()" >提交</b-button>
  </div> 

</div>
</template>  

<script>
import Vue from 'vue';

export default {
  props: ['strProject','strNamespace','strservicename'],
  components: {

  },
  data(){
    return{
      rollback:[],
      open: false,
      namespace:[],
      service:[],
      authorize: [
        {
          namespace: "",
          service:""
        },
      ]
    }  
  },

  created(){
    backendapi.get(`/project/${this.strProject}/namespace`).then((response) => {
      response.data.map((item, index) => {
        this.namespace.push(item.name)
      }); 
      this.isLoading = false;
    })
    .catch(error => {
      alert(error.message);
      this.isLoading = false;
    });
    backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/service`).then((response) => {
      response.data.map((item, index) => {
        this.service.push(item.name)
      });  
      this.isLoading = false;
    })
    .catch(error => {
      alert(error.message);
      this.isLoading = false;
    });
  },

  methods:{
    add(){
      const ggg={
        namespace: "",
        service:""
      }
      this.authorize.push(ggg)
    },
    ok(){
      if(!this.open){
        this.authorize =[];
      }
      backendapi.put(`/project/${this.strProject}/namespace/${this.strNamespace}/service/${this.strservicename}/authorize`,{
        authorize:this.authorize
      }).then((response) => {
        this.$emit('childMethod');
      })
      .catch(error => {
        alert(error.message)
      });
    },
  }
}
</script>
    
<style src="vue-form-wizard/dist/vue-form-wizard.min.css"></style>

<style>
.vue-form-wizard .wizard-nav-pills>li>a>span {
    margin-left: 0rem;
    text-align: center;
    width: 100%;
}
.vue-form-wizard .wizard-nav-pills>li>a {
    padding: .5rem !important;
    border-radius: 7px;
}
.vue-form-wizard.md .wizard-icon-circle {
    font-size: 15px;
}
.vue-form-wizard.md .wizard-nav-pills>li.active>a .wizard-icon {
    font-size: 15px;
}
.vue-form-wizard .wizard-nav-pills>li>a>span>div {
    font-weight:bold;
}
.cloudDrive, .NFS, .config, .cloudDrive:last-child, .NFS:last-child, .config:last-child{
  padding: 20px;
  border: 1px dashed #eee;
  margin-bottom: 20px;
}
.form-group h4{
  text-align: center;
}
.deployform .form-inline{
  margin: 10px 0;
}
.deployform input{
  margin: 0px 5px;
}
.deployform fieldset{
  padding: 5px 0;
  margin: 0;
}
fieldset:last-child{
  margin-bottom: 0;
}

span.s_comment{
  display: block;
  font-size: 13px;
  margin-top: 10px;
}

.must_write{
  color:red;
  font-size: 13px;
  margin-left: 5px;
}

.input_tooltip{
    padding: 0px;
    box-shadow: none;
    border: none;
}
.input_tooltip i{
    font-size: 24px;
    color:red;
}
</style>



