<template>
  <div>

  <!-- 讀取畫面 -->
  <loading :active.sync="isLoading"></loading>

  <div class="content-heading">{{$t('service.CICD_TITLE')}}</div>

  <!-- Select選項開始 -->
  <b-form class="was-validated">
    <div class="form-group row justify-content-end">
      <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_PRJ")}}:</label>
      <div class="col-md-4">
        <select id="projectlist" class="custom-select custom-select-md mb-3" @change="selectedproject()" v-model="strProject">
          <option value disabled selected>{{$t("option.PROJECT")}}</option>
          <option v-for="(item) in projectlist" :value="item.name" :key="item.name">{{item.name}}</option>
        </select>
      </div>
    </div>
  </b-form>
  <!-- Select選項結束 -->

  <!-- 專案表單開始 -->
  <ProjectTable :strProject="strProject" @getPjname="getPjname" v-show="showProject"/>
  <!-- 專案表單結束 -->
  
  <!-- 尚未設定開始 -->
  <div v-show="$route.params.auth">
    <div>
      <strong>{{$t('service.CICD_NO_REPOSITORY')}}</strong>
    </div>  
    <b-btn type="button" class="btn btn-primary my-3" variant="primary" @click="clickGoAuth()">{{$t('service.CICD_BTN_SET_REPOSITORY')}}</b-btn>
  </div>
  <!-- 尚未設定結束 -->

  <div class="getid" v-if="isAuth">
    <a href="" v-b-modal.helpid @click.prevent="">{{$t('service.CICD_GUIDE_SET_APP_ID_AND_SECRET')}}</a>
    <br>
    <br>
    <form ref="form">
      <fieldset>
          <div class="form-group row align-items-end">
              <label class="col-3 col-form-label">Private GitLab IP Address:</label>
              <div class="col-6">
                  <b-form-input v-model="auth.address" :class="{'form-control':true}" type="text"/>
              </div>
          </div>
      </fieldset>      
      <fieldset>
          <div class="form-group row align-items-end">
            <label class="col-3 col-form-label">Application ID:</label>
            <div class="col-6">
              <b-form-input v-model="auth.id" :class="{'form-control':true}" type="text" required />
            </div>
          </div>
      </fieldset>
      <fieldset>
          <div class="form-group row align-items-end">
              <label class="col-3 col-form-label">Secret:</label>
              <div class="col-6">
                  <b-form-input v-model="auth.secret" :class="{'form-control':true}" type="text"/>
              </div>
          </div>
      </fieldset>             
    </form>   
    <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" @click="openAuth()">{{$t('service.CICD_BTN_AUTH')}}</b-btn>
  </div>   

  <!-- <div class="mt-5" v-if="isCode"> <strong>{{$t('service.CICD_DESC_OPEN_LINK_WITH_NEW_TAB')}}<br></strong> {{newlink}} <br><strong class="mt-3">並進行下面步驟。</strong> </div>--> 
    
  <hr v-if="isCode">

  <div class="getcode" v-if="isCode">
    <a class="" href="" v-b-modal.helpcode @click.prevent="">{{$t('service.CICD_GET_AUTH_CODE_GUIDE')}}</a>
    <br>
    <br>
    <form ref="form" @submit.stop.prevent="handleSubmit">
      <fieldset>
          <div class="form-group row align-items-end">
              <label class="col-3 col-form-label">Code:</label>
              <div class="col-6">
                <b-form-input v-model="auth.code" :class="{'form-control':true}" type="text" required/>
              </div>
          </div>
      </fieldset>     
    </form>
    <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" @click="openCode()">{{$t('service.CICD_BTN_SYNC_REPOSITORY')}}</b-btn>
  </div>  


    <!-- 如何獲得ID Secret popup開始 -->
    <b-modal class="modal fade configure" id="helpid" size="" :hide-footer="true" :title="$t('service.CICD_GUIDE_SET_APP_ID_AND_SECRET')" ref="modal">
      <ul class="list-group list-group-flush">
        <li class="list-group-item">
          {{$t('service.CICD_GUIDE_LOGIN_GITLAB')}}<br>http:// <strong>{your gitlab}</strong>/profile/applications</li>
        <li class="list-group-item">
            {{$t('service.CICD_GUIDE_FILL_UP_APP_FIELDS')}} {{$t('service.CICD_GUIDE_APP_NAME_EXAMPLE')}}<br>
            Name: testpipeline。<br>
            Redirect URI: {{auth.redirectUrl}} 
            <!-- 名稱:應用名稱，例如:testpipeline。<br>
            回調URL:  -->
        </li>
        <li class="list-group-item">{{$t('service.CICD_GUIDE_CLICK_SAVE_APP')}}</li>
        <li class="list-group-item">{{$t('service.CICD_GUIDE_COPY_APPID_SECRET')}}</li>
      </ul>
    </b-modal>
    <!-- 如何獲得ID Secret popup結束 -->


    <!-- 如何獲得code popup開始 -->
    <b-modal class="modal fade configure" id="helpcode" size="xl" :hide-footer="true" :title="$t('service.CICD_GET_AUTH_CODE_GUIDE')" ref="modal">
      <ul class="text-wrap">
        <li class="text-wrap mt-3">{{$t('service.CICD_GET_AUTH_CODE_GUIDE_CLICK_HYPERLINK_WITH_NEW_TAB')}}<br>{{newlink}}</li>
        <li class="text-wrap mt-3">{{$t('service.CICD_GET_AUTH_CODE_GUIDE_CLICK_AUTHORIZE')}}</li>
        <li class="text-wrap mt-3">{{$t('service.CICD_GET_AUTH_CODE_GUIDE_COPY_AUTH_CODE')}}</li>
      </ul>
    </b-modal>
    <!-- 如何獲得code popup結束 -->

    <div class="row" v-show="$route.params.gits">
      <div class="col-12 text-center">
        <v-client-table ref="piplineTable" v-if="true" :data="pipelineData" :columns="columes" :options="options">
          <template slot="url" slot-scope="props">
            <div>
              <div class="text-left">
                <a :href="props.row.url" target="_blank">{{props.row.url}}</a>
              </div>  
            </div>
          </template>
          <template slot="action" slot-scope="props">
            <div>  
              <div class="row m-0">
                <b-form-checkbox v-model="props.row.enable" name="check-button" switch  @change="goEnable(props.row.url,props.row.enable,props.row.pip_id)"></b-form-checkbox>

                <b-btn variant="outline-danger" class="btn-xs shadow-none" v-if="props.row.enable" @click.prevent="readyPlay(props.row.defaultBranch,props.row.pip_id,props.row.pip_id)" v-b-modal.runpipeline @ok="play(props.row.pip_id)"><i class="fas fa-play-circle"></i></b-btn>

                <b-btn variant="outline-success" class="btn-xs shadow-none mx-2" v-if="props.row.enable&&props.row.lastStarted!==null" @click.prevent="infoPipeline(props.row.pip_id, props.row.url)"><i class="fas fa-info-circle"></i></b-btn>
              </div>
            </div>
          </template>      
        </v-client-table>
      </div>
    </div>

    <!-- Pipeline 執行開始 -->
    <b-modal class="modal fade" id="runpipeline" :title="$t('service.CICD_RUN_PIPELINE')" ref="modal">
      <dl class="row m-0 mt-4 mb-3">
        <dt>{{$t('service.CICD_LOG_BRANCH')}}</dt>
        <b-form-input class="mt-3" v-model="defaultBranch" :class="{'form-control':true}" type="text"/>
      </dl>
      <template v-slot:modal-footer="{ok, cancel}">
        <b-button size="sm" variant="success" @click="play()">{{$t("service.REGISTRY_DIAL_CRTECERT_BTN_SUBMIT")}}</b-button>
        <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.REGISTRY_DIAL_CRTECERT_BTN_CANCEL")}}</b-button>
      </template>
    </b-modal>
    <!-- Pipeline 執行結束 -->

    <div v-show="$route.params.id">
      <dl class="row m-0 mb-5">
        <dt>{{$t('service.CICD_LOG_REPOSITORY')}}</dt>
        <dd class="col-sm-9"><a :href="logUrl" target="_blank">{{logUrl}}</a></dd>
      </dl>

      <div class="row justify-content-end">
        <b-btn variant="outline-danger" class="btn shadow-none m-3" v-b-modal.runpipeline><i class="fas fa-play-circle" style="font-size: 20px;vertical-align: middle;"></i></b-btn>
      </div>

      <div class="row">
        <strong class="text-left col-12">{{$t('service.CICD_EXE_RECORD')}}</strong>
        <div class="col-12 text-center">
          <v-client-table ref="piplineTable2" :data="pipelineData2" :columns="columes2" :options="options2" v-observe-visibility="visibilityChanged2">
            <template slot="action" slot-scope="props">
              <div>  
                <div class="row m-0">
                  <b-btn variant="outline-success" class="btn-xs shadow-none mx-1" v-if="props.row.executionState!=='Waiting'" @click.prevent="openlog(props.row.run, props.row.branch, props.row.started)" v-b-modal.containerLog v-b-tooltip.hover title="日誌資訊"><i class="fas fa-info-circle"></i></b-btn>
                </div>
              </div>
            </template>    
          </v-client-table>
        </div>
      </div>
    </div>

    <!-- 容器日誌開始 -->
    <b-modal class="modal fade" id="containerLog" :title="$t('service.CICD_LOG_TITLE')" ref="modal" size="xl" :hide-footer="true">
        <dl class="row m-0 mt-2">
          <dt>{{$t('service.CICD_LOG_REPOSITORY')}}</dt>
          <dd class="col-sm-9"><a :href="logUrl" target="_blank">{{logUrl}}</a></dd>
        </dl>
        <dl class="row m-0 mt-2 mb-3">
          <dt>{{$t('service.CICD_LOG_RUN_NO')}}</dt><dd class="col-sm-2"><strong>#{{run}}</strong></dd>
          <dt>{{$t('service.CICD_LOG_BRANCH')}}</dt><dd class="col-sm-2"><strong> {{branch}}</strong></dd>
          <dt>{{$t('service.CICD_LOG_EXE_TIME')}}</dt><dd><strong> {{started}}</strong></dd>

        </dl>
        <div class="row">
            <div class="col-12">
                <a></a>
                <b-tabs id="pipelineTab">

                  <b-tab @click.once="log1()">
                    <template v-slot:title style="position:relative;">
                      <i>1</i><span>Clone</span>
                      <div><h5><strong>{{logState1}}</strong></h5></div>
                    </template>
                    <div>
                      <loading id="pipelinerun" :active.sync="isLoading1" :is-full-page="false"></loading>
                      <div class="logContent" v-html="logContent1"></div>
                    </div>  
                  </b-tab>

                  <b-tab @click.once="log2()">
                    <template v-slot:title style="position:relative;">
                      <i>2</i><strong>Build</strong>
                      <div><h5><strong>{{logState2}}</strong></h5></div>
                    </template>
                    <div>
                      <loading id="pipelinerun" :active.sync="isLoading2" :is-full-page="false"></loading>
                      <div class="logContent" v-html="logContent2"></div>
                    </div>  
                  </b-tab>

                  <b-tab @click.once="log3()">
                    <template v-slot:title style="position:relative;">
                      <i>3</i><strong>Publish</strong>
                      <div><h5><strong>{{logState3}}</strong></h5></div>
                    </template>
                    <div>
                      <loading id="pipelinerun" :active.sync="isLoading3" :is-full-page="false"></loading>
                      <div class="logContent" v-html="logContent3"></div>
                    </div>  
                  </b-tab>

                  <b-tab @click.once="log4()">
                    <template v-slot:title style="position:relative;">
                      <i>4</i> <strong>Deploy</strong>
                      <div><h5><strong>{{logState4}}</strong></h5></div>
                    </template>
                    <div>
                      <loading id="pipelinerun" :active.sync="isLoading4" :is-full-page="false"></loading>
                      <div class="logContent" v-html="logContent4"></div>
                    </div>   
                  </b-tab>

                </b-tabs>
            </div>
        </div>
    </b-modal>
    <!-- 容器日誌結束 -->


 </div>
</template>

<script>
import Vue from 'vue';
import { ClientTable,  Event } from 'vue-tables-2';
import swal from 'sweetalert2'
import ProjectTable from "./service/ProjectTable";
Vue.use(ClientTable);
export default {
  components: {
    ClientTable,
    ProjectTable
  },
  data() {
    return {
      id:'',
      pip_id:'',
      set: false,
      isAuth: false,
      isCode: false,
      git1: false,
      git2: false,
      checked: true,
      timer1: "",
      cred_id: "",
      pipelineData: [],
      columes: ["url", "defaultBranch", "lastRunState", "lastStarted", "action"],
      options: {
        perPage: 25,
        perPageValues: [25],
        filterable: false,
        sortable: 'aaa',
        dateColumns:['lastStarted','started'],
        dateFormat: 'YYYY-MM-DD HH:mm:ss',
        orderBy:{
          column:"url",
          ascending:false
        },
        headings: {
            url: () => this.$t('service.CICD_LIST_URL'),
            defaultBranch: () => this.$t('service.CICD_LIST_BRANCH'),
            lastRunState: () => this.$t('service.CICD_LIST_LAST_EXE_STATE'),
            lastStarted: () => this.$t('service.CICD_LIST_LAST_EXE_TIME'),
            action: () => this.$t('service.CICD_LIST_ACTION'),
            // run: this.$t('service.CICD_LIST_RUN'),
            // executionState: this.$t('service.CICD_LIST_EXE_STATE'),
            // branch: this.$t('service.CICD_LIST_BRANCH'),
            // commit: this.$t('service.CICD_LIST_COMMIT'),
            // started: this.$t('service.CICD_LIST_STARTED')  
        },
      },
      columes2: ["run", "executionState", "branch", "commit", "started", "action"],
      options2: {
        perPage: 25,
        perPageValues: [25],
        filterable: false,
        sortable: 'aaa',
        dateColumns:['lastStarted','started'],
        dateFormat: 'YYYY-MM-DD HH:mm:ss',
        orderBy:{
          column:"run",
          ascending:false
        },
        headings: {
            action: () => this.$t('service.CICD_LIST_ACTION'),
            run: () => this.$t('service.CICD_LIST_RUN'),
            executionState: () => this.$t('service.CICD_LIST_EXE_STATE'),
            branch: () => this.$t('service.CICD_LIST_BRANCH'),
            commit: () => this.$t('service.CICD_LIST_COMMIT'),
            started: () => this.$t('service.CICD_LIST_STARTED')  
        },
      },
      pipelineData2: [],
      auth:{
        id:'',
        secret:'',
        address:'',
        code:'',
        rancher:'',
        redirectUrl:''
      },
      
      newlink:'',
      strProject:"",
      projectlist:[],
      showProject:true,
      logUrl:'',
      branch:'',
      run: "",
      defaultBranch:'master',
      started:'',
      logState1: "",
      logState2: "",
      logState3: "",
      logState4: "",
      logContent1: "",
      logContent2: "",
      logContent3: "",
      logContent4: "",
      isLoading:false,
      isLoading1:false,
      isLoading2:false,
      isLoading3:false,
      isLoading4:false,
    };
  },
  created(){
    this.isLoading = true;
    backendapi.get("/project").then((response) => {
      this.isLoading = false;
      this.projectlist = response.data;
    })
    .catch(error => {
       alert(error.message);
    });
  },
  methods: {

    getPjname(PJname){
      this.isLoading = true;
      this.strProject = PJname
      backendapi.get(`/project/${this.strProject}/pipeline/auth`).then((response) => {
        if(response.data.cred_displayName == null && response.data.cred_id == null){
          this.$router.push({
            name: "pipeline",
            params: { auth: "auth" }
          });
          this.showProject = false
          this.isAuth = false
          this.isCode = false
          this.git2 = false
          this.auth.redirectUrl = response.data.verify_path
          this.auth.rancher = response.data.verify_path.replace("https://","").replace("/verify-auth","")
          this.isLoading = false;
        }
        if(response.data.cred_displayName !== null && response.data.cred_id !== null){
            this.cred_id = response.data.cred_id
            console.log(this.cred_id)
            this.$router.push({
              name: "pipeline",
              params: { gits: "gits" }
            });
            this.showProject = false
            this.isAuth = false
            this.isCode = false
            this.git2 = false
            this.initTemplateTable()
            this.isLoading = false;

            
        }
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });
    },


    selectedproject() {
      this.isLoading = true;
      clearInterval(this.timer1);
      this.$router.push('/admin/pipeline')
      var projectlist = document.getElementById("projectlist");
      this.strProject = projectlist.options[projectlist.selectedIndex].value;
      backendapi.get(`/project/${this.strProject}/pipeline/auth`).then((response) => {
        if(response.data.cred_displayName == null && response.data.cred_id == null){
          this.$router.push({
            name: "pipeline",
            params: { auth: "auth" }
          });
          this.showProject = false
          this.isAuth = false
          this.isCode = false
          this.git2 = false
          this.auth.redirectUrl = response.data.verify_path
          this.auth.rancher = response.data.verify_path.replace("https://","").replace("/verify-auth","")
          this.isLoading = false;
        }
        if(response.data.cred_displayName !== null && response.data.cred_id !== null){
            this.cred_id = response.data.cred_id
            console.log(this.cred_id)
            this.$router.push({
              name: "pipeline",
              params: { gits: "gits" }
            });
            this.showProject = false
            this.isAuth = false
            this.isCode = false
            this.git2 = false
            this.initTemplateTable()
            this.isLoading = false;
        }
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });
    },

    clickGoAuth(){
      this.set = false
      this.isAuth = true
    },

    openAuth(){
      if(this.auth.address==''||this.auth.id==''||this.auth.secret==''){
        alert(this.$t('service.CICD_ALERT_INFO_INSSUFFICIENT'))
        return
      }else{
        this.isCode = true;
        this.newlink = `http://${this.auth.address}/oauth/authorize?client_id=${this.auth.id}\&redirect_uri=https%3A%2F%2F${this.auth.rancher}%2Fverify-auth\&response_type=code\&state=0.09672018516184587\&`
      }
    },

    openCode(){
      backendapi.post(`/project/${this.strProject}/pipeline/auth`,{
        "code": this.auth.code,
        "clientId": this.auth.id,
        "clientSecret": this.auth.secret,
        "redirectUrl": this.auth.redirectUrl,
        "inheritAuth": false,
        "hostname": this.auth.address,
        "tls": false
      }).then((response) => {
        this.selectedproject()
      })
      .catch(error => {
        alert(error.message);
      });
    },

    initTemplateTable() {
        if (this.strProject && this.strProject !== ""){
          this.isLoading = true;
          backendapi.get(`/project/${this.strProject}/pipeline/repository`)
            .then(res => {
              this.pipelineData = res.data.map((item, index) => {
                item["id"] = index;
                if(item['lastStarted'] !== null){
                  item['lastStarted'] = moment(item.lastStarted);
                }  
                return item;
              });
              this.isLoading = false;
            })
            .catch(err => {
              this.isLoading = false;
          });
        }
    },


    reloadded(){
      setTimeout(() => { 
        backendapi.get(`/project/${this.strProject}/pipeline/repository`)
          .then(res => {
            this.pipelineData = res.data.map((item, index) => {
              item["id"] = index;
              if(item['lastStarted'] !== null){
                item['lastStarted'] = moment(item.lastStarted);
              }  
              return item;
            });
            this.isLoading = false;
          })
          .catch(err => {
            loader.hide();
        });
      }, 10000)
    },


    goEnable(url,enable,pip_id){
      this.isLoading = true;
      this.pip_id = pip_id
      if(enable==true){
        backendapi.delete(`/project/${this.strProject}/pipeline/${this.pip_id}`).then((response) => {
          this.reloadded();
        })
        .catch(error => {
          alert(error.message);
          this.isLoading = false;
        });
      }else{
        backendapi.post(`/project/${this.strProject}/pipeline`,{
          "cred_id":this.cred_id,
          "url": url
        }).then((response) => {
            this.isLoading = true;
            this.reloadded();
        })
        .catch(error => {
          alert(error.message);
          this.isLoading = false;
        });
      }       
    },

    readyPlay(defaultBranch,pip_id){
      this.defaultBranch = defaultBranch
      this.pip_id = pip_id
    },

    play(){
      this.isLoading = true;
      backendapi.post(`/project/${this.strProject}/pipeline/${this.pip_id}`,{
        "branch": this.defaultBranch,
      }).then((response) => {
        this.$refs.modal.hide();
        alert(this.$t('service.CICD_ALERT_PIPELINE_IS_RUNNING'));
        this.isLoading = false;
      })
      .catch(error => {
        alert(this.$t('service.CICD_ALERT_BRANCH_NOT_FOUND'));
        this.isLoading = false;
      }); 
    },

    infoPipeline(pip_id, url){
      this.isLoading = true;
      this.logUrl = url
      this.pip_id = pip_id
      this.git1 = false
      this.git2 = true
      backendapi.get(`/project/${this.strProject}/pipeline/${this.pip_id}`).then((response) => {
        this.pipelineData2 = response.data.execution_data.map((item, index) => {
          item['started'] = moment(item.started);
          return item;
        });
        this.$router.push({
          name: "pipeline",
          params: {id: this.pip_id }
        });
        this.timer1 = setInterval(()=> {this.reloadInfoPipeline()}, 10000);
        this.isLoading = false;
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      }); 
    },

    reloadInfoPipeline(){
      this.git1 = false
      this.git2 = true
      backendapi.get(`/project/${this.strProject}/pipeline/${this.pip_id}`).then((response) => {
        this.pipelineData2 = response.data.execution_data.map((item, index) => {
          item['started'] = moment(item.started);
          return item;
        });
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      }); 
    },

    tabTitle(title, count) {
      return `<span class="badge badge-secondary">${count}</span> ${title}`;
    },

    openlog(run, branch, started){
      this.run = run
      this.branch = branch
      this.started = started
      backendapi.get(`/project/${this.strProject}/pipeline/${this.pip_id}/${run}`).then((response) => {
        this.logState1 = response.data.stages[0].state
        this.logState2 = response.data.stages[1].state
        this.logState3 = response.data.stages[2].state
        this.logState4 = response.data.stages[3].state
        this.log1()
      })
    },

    log1(){
      this.logContent1 = "";
      this.isLoading1 = true;
      backendapi.get(`/project/${this.strProject}/pipeline/${this.pip_id}/${this.run}/log?step=0&stage=0`).then((response) => {
        console.log(response.data)
        this.logContent1 = response.data.replace(/\n/g, "<br>");
        this.isLoading1 = false;
      })
      .catch(error => {
        alert(error.message);
        this.isLoading1 = false;
      }); 
    },
    log2(){
      this.logContent2 = "";
      this.isLoading2 = true;
      backendapi.get(`/project/${this.strProject}/pipeline/${this.pip_id}/${this.run }/log?step=0&stage=1`).then((response) => {
        this.logContent2 = response.data.replace(/\n/g, "<br>");
        this.isLoading2 = false;
      })
      .catch(error => {
        alert(error.message);
        this.isLoading2 = false;
      }); 
    },
    log3(){
      this.logContent3 = "";
      this.isLoading3 = true;
      backendapi.get(`/project/${this.strProject}/pipeline/${this.pip_id}/${this.run }/log?step=0&stage=2`).then((response) => {
        this.logContent3 = response.data.replace(/\n/g, "<br>");
        this.isLoading3 = false;
      })
      .catch(error => {
        alert(error.message);
        this.isLoading3 = false;
      }); 
    },
    log4(){
      this.logContent4 = "";
      this.isLoading4 = true;
      backendapi.get(`/project/${this.strProject}/pipeline/${this.pip_id}/${this.run }/log?step=0&stage=3`).then((response) => {
        this.logContent4 = response.data.replace(/\n/g, "<br>");
        this.isLoading4 = false;
      })
      .catch(error => {
        alert(error.message);
        this.isLoading4 = false;
      }); 
    },
    destroyed: function() {
      clearInterval(this.timer1);
    },
    // visibilityChanged(isVisible, entry) {
    //   console.log("1 gits", isVisible);
    //   console.log("1 route", this.$router.currentRoute.params);
    // },
    visibilityChanged2(isVisible, entry) {
      console.log("2 gits details", isVisible);
      console.log("2 route", this.$router.currentRoute.params);
      

      if(this.strProject && !isVisible){ // leave current page
        this.$router.go(-1);
        this.getPjname(this.strProject);//aa
        
      }
      
    },
  }
}
</script>


<style>
#pipelineTab .tab-content{
  padding:0px;
  background: #000;
  color: #fff;
  padding-left: 10px;
  font-size: 13px;
  min-height:460px!important;
  max-height:460px!important;
  overflow: auto;
}

#pipelineTab .nav-item>.nav-link.active {
    background-color: #000;
    color: #fff;
}

#pipelineTab .nav-item>.nav-link{
  padding: 20px 25px;
}
#pipelineTab .nav-item>.nav-link i{
  display: block;
}

#pipelinerun .vld-background{
  background-color:transparent!important
}

#pipelinerun svg{
  stroke: #fff!important;
}

</style>


