<template>
  <div>
    <router-view />    
  </div>
</template>

<script>
// import $ from 'jquery' 
export default {
  data(){
    return{        
  
        name: this.$t('sidebar.DASHBOARD'),
        path: '/admin/control/',
        to: 'charts',
        active: true,
        tablist:[
          {
            name: this.$t('sidebar.ACNT_MEM'),
            path: '/admin/control/',
            to: 'newuser',
            active: true
          },
          {
            name: this.$t('sidebar.PRJ_MEM'),
            path: '/admin/control/',
            to: 'newproject',
            active: true
          },
          {
            name: this.$t('sidebar.RESOURCE_MEM'),
            path: '/admin/control/',
            to: '',
            active: true
          },    
        ]
    }
  },
  mounted(){
    this.$router.push('/admin/control/charts');
    
    // $('#nav-tabs').find('.nav-item').eq(0).children('.nav-link').addClass('active');
    // $('.nav-item').on('click',function(){
    //   $('.nav-tabs').find('.nav-link').removeClass('active');
    //   $(this).children('.nav-link').addClass('active');
    // })
  },
  methods:{

  }
};



</script>





