import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/pages/Login'
import Allmenu from '@/components/AllMenu'
import Dashboard from '@/components/Dashboard'
import Service from '@/components/Service'
import Control from '@/components/Control'
import Charts from '@/components/Charts'
import User from '@/components/user/User'
import Project from '@/components/project/Project'
import Resource from '@/components/Resource'
import Configure from '@/components/Configure'
import Images from '@/components/Images'
import Volumes from '@/components/Volumes'
import Ingress from '@/components/Ingress'
import Pipeline from '@/components/Pipeline'
import Center from '@/components/Center'
Vue.use(Router)
export default new Router({
  routes: [{
      path: '/center',
      name: 'Center',
      component: Center,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/admin',
      name: 'Dashboard',
      component: Dashboard,
      meta: {
        requiresAuth: true
      },
      children: [{
          path: 'charts',
          name: 'charts',
          component: Charts,
          meta: {
            requiresAuth: true,
          },
        },
        {
          path: 'user',
          name: 'user',
          component: User,
          meta: {
            requiresAuth: true,
          },
        },
        {
          path: 'project',
          name: 'project',
          component: Project,
          meta: {
            requiresAuth: true
          },
        },
        {
          path: 'resource',
          name: 'resource',
          component: Resource,
          meta: {
            requiresAuth: true
          },
        },
        {
          path: 'service',
          name: 'service',
          component: Service,
          meta: {
            requiresAuth: true
          },
        },
        {
          path: 'configure',
          name: 'configure',
          component: Configure,
          meta: {
            requiresAuth: true
          },
        },
        {
          path: 'images',
          name: 'images',
          component: Images,
          meta: {
            requiresAuth: true
          },
        },
        {
          path: 'volumes',
          name: 'volumes',
          component: Volumes,
          meta: {
            requiresAuth: true
          },
        },
        {
          path: 'ingress',
          name: 'ingress',
          component: Ingress,
          meta: {
            requiresAuth: true
          },
        },
        {
          path: 'pipeline/:auth?/:gits?/:project?/:id?',
          name: 'pipeline',
          component: Pipeline,
          meta: {
            requiresAuth: true
          },
        }
      ],
    },
    {
      path: '/allmenu',
      name: 'allmenu',
      component: Allmenu,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/',
      component: Login
    },
  ]
})



