<template>
<div>

  <div class="card card-default">
      <div class="card-body mt-3">
        <!--當前鏡像 開始-->
        <!-- <div class="col-12 mx-5 my-3">
        <dl class="row m-0" v-show="up2">
          <dt class="col-2">{{$t("service.SVCM_DIAL_SVCUPGRADE_LBL_CURRNET_IMG")}}</dt>
          <dd class="col-9">{{nowImage2}}</dd>
        </dl> 
        </div> -->
        <!--當前鏡像 結束-->

        
        <div class="row m-0">

          <div class="col-12 mx-5 my-3">
            <fieldset>  
              <div class="row m-0">
                <label class="text-left col-2 pl-3">{{$t("service.SVCM_DIAL_SVCUPGRADE_LBL_UPGRADED_IMG")}} </label>
                <input class="col-8" v-model="upgrade.image" :class="{'form-control':true}" type="text" @change="linkimage()"/>
              </div>
            </fieldset> 
          </div>  

          <!--直接更新 開始-->
          <div class="col-12 mx-5 my-3">
            <fieldset>
              <label class="switch">
                <input type="radio" v-model="checkedImage" name="radioSwitch" value="1" @change="upconfirm()"/>
                <span></span>{{$t("service.SVCM_DIAL_SVCUPGRADE_LBL_UPGRADE_DIRECT")}}
              </label>
            </fieldset> 
          </div>
          <!--直接更新 結束-->


          <!--灰度發佈 開始-->
          <div class="col-12 mx-5 my-3">
            <div class="row mb-5">
              <div class="col-3">
                <label class="switch">
                  <input type="radio" v-model="checkedImage" name="radioSwitch" value="2" @change="upconfirm()"/>
                  <span></span>{{$t("service.SVCM_DIAL_SVCUPGRADE_LBL_CANARY_ROLLOUT")}}
                </label>
              </div>
              <div class="col-3">
                <label class="switch">
                  <input type="radio" v-model="checkedImage" name="radioSwitch" value="3" @change="upconfirm()" :disabled="stop"/>
                  <span></span>取消灰度發佈
                </label>
              </div>
            </div>           
            <!-- <fieldset v-show="up2">
              <div class="row m-0">
                <label class="text-left col-3 pl-3">{{$t("service.SVCM_DIAL_SVCUPGRADE_LBL_UPGRADED_IMG")}} </label>
                <input class="col-8" v-model="upgrade.rollout_image" :class="{'form-control':true}" type="text"/>
              </div>
            </fieldset> -->

            <fieldset :disabled="up1">
              <div class="row m-0">
                <label class="col-form-label col-6 pl-3">{{$t("service.SVCM_DIAL_SVCUPGRADE_LBL_CURRNET_IMG")}} <div v-if="checkedImage == 2">{{nowImage}}</div></label>
                <input class="col-2 mx-2" v-model="upgrade.weight_1" :class="{'form-control':true}" type="number" min="0" max="100" @change="vWeight()"/>%
              </div>
            </fieldset>
            <fieldset :disabled="up1">
              <div class="row m-0">
                <label class="col-form-label  col-6 pl-3">{{$t("service.SVCM_DIAL_SVCUPGRADE_LBL_UPGRADED_IMG")}}<div v-if="checkedImage == 2">{{upgrade.image}}</div></label>
                <input class="col-2 mx-2" v-model="upgrade.weight_2" :class="{'form-control':true}" type="number" min="0" max="100" @change="vvWeight()"/>%
              </div>
            </fieldset>
          </div>
          <!--灰度發佈 結束-->

          <div class="col-12 mx-5 my-3" >
            <fieldset>

            </fieldset>
          </div>  

        </div>
      </div>
  </div>

  <div class="row m-0 justify-content-end">
    <b-button size="sm" variant="success" @click="ok()">{{$t("service.SVCM_DIAL_SVCUPGRADE_BTN_SUBMIT")}}</b-button>
  </div> 

</div>
</template>  

<script>
import Vue from 'vue';
export default {
  props: ['strProject','strNamespace','strservicename'],
  components: {
},
  data(){
    return{
      rollback:[],
      open: true,
      up1: true,
      up2: false,
      stop: true,
      checkedImage:'',
      namespace:[],
      service:[],
      nowImage:"",
      nowImage2:"",
      rollout_image:"",
      upgrade:{
        action:'',
        image:"",
        weight_1: 100,
        weight_2: 0
      }
    }  
  },
  computed: {
    nowImage3() {
      return this.upgrade.image
    }
  },
  created(){
    backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/service/${this.strservicename}/upgrade`).then((response) => {
      console.log("aaaaa", response.data);
      this.nowImage = response.data.image_1;

      if (response.data.status == "v1"){
        this.checkedImage = 1
        // this.upgrade.action = response.data.status


        // this.upgrade.image_1 = response.data.image_1
        // // this.upgrade.rollout_image =  response.data.rollout_image;
        // this.nowImage =  response.data.image + "(灰度鏡像)";
        // this.nowImage2 =response.data.image;

      }else if (response.data.status == "v2"){
        this.checkedImage = 2
        this.up1 = false
        this.up2 = true
        this.stop= false
        this.upgrade.action = response.data.status
        this.nowImage = response.data.image_1
        this.nowImage2 = response.data.image_2
        this.upgrade.weight_1 = response.data.weight_1
        this.upgrade.weight_2 = response.data.weight_2
      }
      this.isLoading = false;
    })
    .catch(error => {
      alert(error.message);
      this.isLoading = false;
    });
  },

  methods:{
    vWeight(){
      this.upgrade.weight_2 = 100 - this.upgrade.weight_1
    },
    vvWeight(){
      this.upgrade.weight_1 = 100 - this.upgrade.weight_2
    },
    linkimage(){
      if(this.checkedImage == 1){
        this.nowImage2 = ""
      }else if(this.checkedImage == 2){
        this.nowImage2 = this.upgrade.image;
      }else if(this.checkedImage == 3){
        this.up1 = false
        this.up2 = false
      }
    },
    upconfirm(){
      // this.upgrade.image =""
      if(this.checkedImage == 1){
        this.up1 = true
        this.up2 = false
        this.nowImage2 = ""
      }else if(this.checkedImage == 2){
        this.up1 = false
        this.up2 = true
      }else if(this.checkedImage == 3){
        this.up1 = true
        this.up2 = false
      }
    },
    ok(){
      // var ddd = document.querySelector('input[name="radioSwitch"]:checked').value

      if(this.checkedImage == 1){
        this.upgrade.action = "v1"
        delete this.upgrade.weight_1
        delete this.upgrade.weight_2

      }else if(this.checkedImage == 2){
        this.upgrade.action = "v2"
        this.upgrade.weight_1 = Number(this.upgrade.weight_1)
        this.upgrade.weight_2 = Number(this.upgrade.weight_2)
        
      }else if(this.checkedImage == 3){
        this.upgrade.action = "cancel"
        delete this.upgrade.weight_1
        delete this.upgrade.weight_2
      }
      console.log("aaaa", this.upgrade)
      backendapi.put(`/project/${this.strProject}/namespace/${this.strNamespace}/service/${this.strservicename}/upgrade`,this.upgrade).then((response) => {
        this.$emit('childMethod');
      })
      .catch(error => {
        alert(error.message)
      });
    },
  }
}
</script>
    
<style src="vue-form-wizard/dist/vue-form-wizard.min.css"></style>

<style>
.vue-form-wizard .wizard-nav-pills>li>a>span {
    margin-left: 0rem;
    text-align: center;
    width: 100%;
}
.vue-form-wizard .wizard-nav-pills>li>a {
    padding: .5rem !important;
    border-radius: 7px;
}
.vue-form-wizard.md .wizard-icon-circle {
    font-size: 15px;
}
.vue-form-wizard.md .wizard-nav-pills>li.active>a .wizard-icon {
    font-size: 15px;
}
.vue-form-wizard .wizard-nav-pills>li>a>span>div {
    font-weight:bold;
}
.cloudDrive, .NFS, .config, .cloudDrive:last-child, .NFS:last-child, .config:last-child{
  padding: 20px;
  border: 1px dashed #eee;
  margin-bottom: 20px;
}
.form-group h4{
  text-align: center;
}
.deployform .form-inline{
  margin: 10px 0;
}
.deployform input{
  margin: 0px 5px;
}
.deployform fieldset{
  padding: 5px 0;
  margin: 0;
}
fieldset:last-child{
  margin-bottom: 0;
}

span.s_comment{
  display: block;
  font-size: 13px;
  margin-top: 10px;
}

.must_write{
  color:red;
  font-size: 13px;
  margin-left: 5px;
}

.input_tooltip{
    padding: 0px;
    box-shadow: none;
    border: none;
}
.input_tooltip i{
    font-size: 24px;
    color:red;
}
</style>



