<template>
<div >

  <div v-if="role">

    <!-- 讀取畫面 -->
    <loading :active.sync="isLoading"></loading>

    <div class="content-heading">{{$t("sidebar.PRJ_MEM")}}</div>

    <!-- 新建專案開始 -->
    <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" v-b-modal.createproject>{{$t("service.PRJ_BTN_NPRJ")}}</b-btn>
    <br />
    <br />
    <b-modal class="modal fade" id="createproject" :title="$t('common.LBL_PRJ')" ref="modal" size="xl" :hide-footer="true">
      <NewProject @loadTable="loadTable()" @stopLoadTable="stopLoadTable()"/>
    </b-modal>
    <!-- 新建專案結束 -->
    
    <!-- 分類表單開始 -->
    <template>
      <div>
          <v-client-table id="projectTable" class="dark" :data="tableData" :columns="columns" :options="options">
              <template slot="state" slot-scope="props">
                  <div>
                      <div class="p-2" :class="[unactive, props.row.state=='active'? active : '']">
                          {{props.row.state}}
                      </div>
                  </div>
              </template>
              <template slot="action" slot-scope="props">
                  <div>
                      <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="loadmembers(props.row)" v-b-modal.memberdata v-b-tooltip.hover :title="$t('common.ICON_TIP_MEMBER_MGM')">
                          <i class="fas fa-users"></i>
                      </b-btn>
                      <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="loadproject(props.row.name)" v-b-modal.editproject v-b-tooltip.hover :title="$t('common.ICON_TIP_EDIT')">
                          <i class="fas fa-edit"></i>
                      </b-btn>
                      <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)" v-b-tooltip.hover :title="$t('common.ICON_TIP_DEL')">
                          <i class="fas fa-trash-alt"></i>
                      </b-btn>
                  </div>
              </template>
              <template slot="logs" slot-scope="props">
                  <div>
                      <label class="switch">
                          <input type="checkbox" v-if="props.row.state=='active'" @change.prevent="switchToggle(props.row, $event)" :checked="switchState(props.row)" />
                          <span></span>&nbsp;&nbsp;
                          <b-btn variant="outline-info" class="btn-xs" @click.prevent="openNewTab(props.row)" v-if="switchState(props.row)" :ref="props.row.project_id" v-b-tooltip.hover :title="$t('common.ICON_TIP_LOG')">
                              <em :ref="'eye-'+ props.row.project_id" class="fa-x fas fa-eye"></em>
                          </b-btn>

                          <b-btn variant="outline-info" class="btn-xs" @click.prevent="openNewTab(props.row)" v-else :ref="props.row.project_id" disabled v-b-tooltip.hover :title="$t('common.ICON_TIP_LOG')">
                              <em :ref="'eye-'+ props.row.project_id" class="fa-x fas fa-eye-slash"></em>
                          </b-btn>

                      </label>
                  </div>
              </template>
          </v-client-table>
      </div>
    </template>
    <!-- 分類表單結束 -->

    <!-- 編輯專案成員開始 -->
    <b-modal class="modal fade" id="memberdata" size="xl" :title="$t('service.PRJ_DIAL_PRJMM_TITLE')" ref="modal" :hide-footer="true">
      <EditProjectMember :strProjectName="strProjectName" @loadTable="loadTable()" @stopLoadTable="stopLoadTable()"/>
    </b-modal>
    <!-- 編輯專案成員結束 -->

    <!-- 編輯專案開始 -->
    <b-modal class="modal fade" id="editproject" :title="$t('service.PRJ_DIAL_UPDATE_CONFIG')" ref="modal" size="xl" :hide-footer="true">
      <EditProject :strProjectName="strProjectName" @loadTable="loadTable()" @stopLoadTable="stopLoadTable()"/>
    </b-modal>
    <!-- 編輯專案結束 -->

  </div>

</div>

</template>

<script>
import Vue from "vue";
import { ClientTable, Event } from "vue-tables-2";
import swal from "sweetalert2";
import NewProject from "./NewProject";
import EditProject from "./EditProject";
import EditProjectMember from "./EditProjectMember";

Vue.use(ClientTable);
  
export default {
  components: {
    ClientTable,
    NewProject,
    EditProject,
    EditProjectMember
  },
  data() {
    return {
      role: false,
      unactive:'unactive',
      active:'active',
      timer1:'',

      selectedIndex: "",
      selectProject: "",
      strProjectName: "",

      tableData: [],
      columns: ["name", "state", "description", "createdTS", "logs", "action"],
      options: {
        // rowClassCallback: function(row) { return 'bg-primary'},
        sortable: ["name", "state", "description", "createdTS"],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder: this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count: this.$t('common.LBL_TOTAL') + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults: this.$t('common.LBL_NO_RECORD'),
        },
        headings: {
          name: () => this.$t("service.PRJ_LST_PRJ_NAME"),
          state: () => this.$t("service.PRJ_LST_STATUS"),
          description: () => this.$t("service.PRJ_LST_DESC"),
          createdTS: () => this.$t("service.PRJ_LST_CRTE_TIME"),
          logs: () => this.$t("service.PRJ_LST_LOGS"),
          action: () => this.$t("service.PRJ_LST_OPT")
        },
        columnsClasses:{
          state: "state",
        },
        pagination: { chunk: 5 },
        sortIcon: {
          is: "fa-sort",
          base: "fas",
          up: "fa-sort-up",
          down: "fa-sort-down"
        },
        dateColumns:['createdTS'],
        dateFormat: 'YYYY-MM-DD HH:mm:ss',
        orderBy:{
          column:"createdTS",
          ascending:false
        }, 
      },
      isLoading: false
    };
  },

  created() { 
    if(localStorage.getItem('cluster-role') == 'cluster-owner'){
      this.role = true
    }
    // console.log("aaaa");
    this.isLoading = true;
    backendapi.get("/project")
      .then(response => {
        // this.isLoading = false;
        var projects = response.data.map((item, index) => {
          // console.log("project id", item);
          item["project_id"] = item.id;
          item["id"] = index;
          item['createdTS'] = moment(item.createdTS);

          // TODO: Initial project logging related fields
          item['discovery_url'] = "";
          item['index_prefix'] = "";
          item['pattern_id'] = "";
          item['pattern_title'] = "";
          item['projectLogging_id'] = "";
          item['switch_status'] = false;
          return item;
        });
        // TODO: Binding first, 
        console.log("aaaaa");
        this.tableData = projects;

        // TODO: Mapping ProjectLogging Status
        this.loadProjectLogging(projects);
      })
      .catch(error => {
        console.log(error);
        this.isLoading = false;
      });

    backendapi.get("/user")
      .then(response => {
        // this.isLoading = false;
        console.log(response.data);
        this.tableData = response.data.user.map((item, index) => {
          item["id"] = index;
          return item;
        });
        this.isLoading = false;
      })
      .catch(error => {
        console.log(error);
      });
  },

  computed: {

  },

  methods: {
    loadproject(name) {
      this.strProjectName = name
    },

    loadmembers(row) {
      this.strProjectName = row.name;
    },

    loadTable() {
      this.$nextTick(() => {
        this.$refs.modal.hide();
      });
      this.timer1 = setInterval(()=> {this.reloadd()}, 5000);
    },

    stopLoadTable(){
      clearInterval(this.timer1);
    },

    reloadd(){
      backendapi.get("/project")
        .then(response => {
          var projects = response.data.map((item, index) => {
            item["project_id"] = item.id;
            item["id"] = index;
            item['createdTS'] = moment(item.createdTS);
            
            // TODO: Initial project logging related fields
            item['discovery_url'] = "";
            item['index_prefix'] = "";
            item['pattern_id'] = "";
            item['pattern_title'] = "";
            item['projectLogging_id'] = "";
            item['switch_status'] = false;
            return item;
          });
          // TODO: Mapping ProjectLogging Status
          this.loadProjectLogging(projects);
        })
    },

    loadProjectLogging(projects) {
        // TODO: Mapping ProjectLogging Status
        backendapi.get("/kibana/get_project_logging_info")
          .then(res => {
            var projectLoggings = res.data.data;
            for (var i = 0; i < projectLoggings.length; i++) {
              var target = projects.find(function(item, index, array) {
                return item.project_id == projectLoggings[i].project_id;
              });

              if(target != null){
                target.discovery_url = projectLoggings[i].discovery_url;
                target.index_prefix = projectLoggings[i].index_prefix;
                target.pattern_id = projectLoggings[i].pattern_id;
                target.pattern_title = projectLoggings[i].pattern_title;
                target.projectLogging_id = projectLoggings[i].projectLogging_id;
                target.switch_status = projectLoggings[i].switch_status;
              }
            }
            this.tableData = projects;
          })
          .catch(err => {
            console.error("get_project_logging_info error:", err);     
          })
          .finally(() => {
            this.tableData = projects;
          });    
    },


    remove(row) {
      this.isLoading = true;
      this.tableData.filter(item => row.id !== item.id);
      this.selectProject = this.tableData[row.id].name;
      backendapi.delete(`/project/${this.selectProject}`)
        .then(response => {
          this.loadTable();
          this.isLoading = false;
        })
        .catch(error => {
          console.log(error);
        });
    },


    checkCancel(row) {
      swal({
        title: this.$t('service.PRJ_DIAL_DELPRJ_MSG'),
        text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#DD6B55",
        confirmButtonText: this.$t('common.BTN_CONFIRM'),
        cancelButtonText: this.$t('common.BTN_CANCEL'),
        heightAuto: false
      }).then(result => {
        if (result.value) {
          this.remove(row);
          swal({
            title: this.$t('common.MSG_DEL_SUCCESS'),
            confirmButtonText: this.$t('common.BTN_OK'),
            type: "success",
            heightAuto: false
          });
        }
      });
    },

    switchToggle(projectinfo, event) {
      this.isLoading = true;
      console.log(projectinfo);
      if (event.target.checked) {
        backendapi.get("/elasticsearch_endpoint")
          .then(res => {
            backendapi
              .post("/projectloggings", {
                projectid: projectinfo.project_id,
                projectname: projectinfo.name,
                endpoint: res.data.endpoint
              })
              .then(res => {
                projectinfo["projectLogging_id"] = res.data.data.id;

                // TODO: Create Kinaba Index
                backendapi
                  .post("/kibana/index_pattern", {
                    indexPattern:
                      res.data.data.elasticsearchConfig.indexPrefix + "-*"
                  })
                  .then(res => {
                    projectinfo["pattern_id"] = res.data.data.id;
                    projectinfo["pattern_title"] = res.data.data.attributes.title;
                    projectinfo["discovery_url"] = res.data.discovery_url;
                    projectinfo["switch_status"] = true;

                    // TODO: Enable Viewer
                    var viewerBtn = this.$refs[projectinfo.project_id];
                    var icon = this.$refs[`eye-${projectinfo.project_id}`];
                    icon.classList.remove("fa-eye-slash");
                    icon.classList.add("fa-eye");
                    viewerBtn["disabled"] = false;

                    this.isLoading = false;
                  })
                  .catch(error => {
                    console.error(error);
                    this.isLoading = false;
                  });


              })
              .catch(error => {
                console.log('after setting project logging', error);
                this.isLoading = false;
              });
          })
          .catch(err => {
            console.error("error", err);
            this.logSwitch = false;
          });
      } else {
        // TODO: Delete ES Logging
        this.isLoading = true;
        backendapi
          .delete(`/projectloggings/${projectinfo.projectLogging_id}`)
          .then(res => {
            backendapi.delete(`/kibana/index_pattern/${projectinfo.pattern_id}`)
              .then(res => {
                projectinfo["discovery_url"] = "";
                projectinfo["switch_status"] = false;

                // TODO: Disable viewer button
                var viewerBtn = this.$refs[projectinfo.project_id];
                var icon = this.$refs[`eye-${projectinfo.project_id}`];
                icon.classList.remove("fa-eye");
                icon.classList.add("fa-eye-slash");
                viewerBtn["disabled"] = true;

                this.isLoading = false;

              })
              .catch(err => {
                console.error(err);
                this.isLoading = false;
              });
          })
          .catch(err => {
            console.error(err);
            this.isLoading = false;
          });
      }
    },
    switchState(projectinfo) {
      if (projectinfo.switch_status == null) {
        return false;
      } else {
        return projectinfo.switch_status;
      }
    },
    openNewTab(projectinfo) {
      window.open(projectinfo.discovery_url, "_blank");
    },
    viewState(projectinfo) {
      if (projectinfo.discovery_url != "") {
        return false;
      } else {
        return true;
      }
    }
  },

  destroyed: function() {
    console.log('project timer destroyed');
    clearInterval(this.timer1);
  }
  
};
</script>


<style>
#tableResource.table input {
  max-width: 85%;
  display: inline;
}
#tableResource.table .thead-dark th {
  background-color: #3f51b5;
}

#projectTable.vuetable th#__state {
    width: 300px;
}
</style>



