<template>
<div>

  <!-- 讀取畫面 -->
  <loading :active.sync="isLoading"></loading>

  <div class="content-heading">{{$t("sidebar.INGRESS")}}</div>
 
    <!-- select選項開始 -->
    <b-form class="was-validated">
      <div class="form-group row">
        <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_PRJ")}}:</label>
        <div class="col-md-4">
          <select id="projectlist" class="custom-select custom-select-md mb-3" @change="selectedproject()" v-model="strProject">
            <option value disabled selected>{{$t("option.PROJECT")}}</option>
            <option v-for="(item) in projectlist" :value="item.name" :key="item.name">{{item.name}}</option>
          </select>
        </div>
        <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_NAMESPACE")}}:</label>
        <div class="col-md-4">
          <select id="namespacelist" class="custom-select custom-select-md mb-3" @change="selectednamespace()" :disabled="disabled" v-model="strNamespace">
            <option id="myselected" value disabled selected>{{$t("option.NAMESPACE")}}</option>
            <option v-for="item in namespacelist" :value="item.name" :key="item.name">{{item.name}}</option>
          </select>
        </div>
      </div>
    </b-form>
    <!-- select選項結束 -->

  <!-- 新建Ingress開始 -->
  <b-modal class="modal fade" id="createIngress" :title="$t('service.INGRS_BTN_CRTE_INGRS')" ref="modal" size="xl" @show="resetModal" @hidden="resetModal" @ok="handleOk">
    <form ref="form" @submit.stop.prevent="validateBeforeSubmit('newIngress')" data-vv-scope="newIngress">
      <fieldset>
        <dl class="row">
          <dt class="col-sm-3">{{$t("service.INGRS_DIAL_CRTEINGRS_LBL_PRJ_NAME")}}:</dt>
          <dd class="col-sm-9">{{strProject}}</dd>
          <dt class="col-sm-3">{{$t("service.INGRS_DIAL_CRTEINGRS_LBL_NAMESPACE")}}:</dt>
          <dd class="col-sm-9">{{strNamespace}}</dd>
        </dl>
      </fieldset>
      <fieldset>
          <div class="form-group row align-items-end">
              <label class="col-2 col-form-label">{{$t("service.INGRS_LST_INGRS_NAME")}}:</label>
              <div class="col-3">
                  <input name="name" v-validate="'required'" v-model="newIngress.name" :class="{'form-control':true, 'is-invalid': errors.has('newIngress.name')}" type="text"/>
              </div>
              <span class="must_write">{{$t('common.HINT_NECESSARY_FIELD')}}</span>
              <span v-show="errors.has('newIngress.name')" class="invalid-feedback">{{ errors.first('newIngress.name') }}</span>
          </div>
      </fieldset>
      <fieldset>
          <div class="form-group row align-items-end">
              <label class="col-2 col-form-label">{{$t("service.INGRS_DIAL_CRTEINGRS_IPTLBL_DOMAIN_NAME")}}:</label>
              <div class="col-6">
                <input name="newhost" v-model="newhost" :class="{'form-control':true}" type="text" @change="changehost()"/>
              </div>
          </div>
      </fieldset>
      <fieldset>
          <div class="form-group row align-items-end"  v-for="(item, index) in newIngress.paths" >
            <div class="col-4">
                <label class="col-form-label">{{$t("service.INGRS_DIAL_CRTEINGRS_IPTLBL_PATH")}}:</label>
                <input v-model="item.path" :class="{'form-control':true}" type="text"/>
            </div>
            <div class="col-1">
              <h4 class="text-center"> = </h4>
            </div>
            <div class="col-3">
                <label class="col-form-label">{{$t("service.INGRS_DIAL_CRTEINGRS_IPTLBL_DEST")}}:</label>
                <select class="custom-select custom-select-md" v-model="item.selected[0].name"  required>
                  <option value="" selected disabled hidden>{{$t('service.INGRS_DIAL_CRTEINGRS_HINT_DEST')}}</option>
                  <option v-for="(child, idx) in workloadIds" :value="child.name">{{child.name}}</option>
                </select>
            </div>
            <div class="col-1">
              <h4 class="text-center"> = </h4>
            </div>
            <div class="col-2">
                <label class="col-form-label">{{$t("service.INGRS_DIAL_CRTEINGRS_PORT")}}:</label>
                <input v-model="item.targetPort" :class="{'form-control':true}" type="number" min="0"/>
            </div>
            <div class="col-1">
              <b-btn variant="outline-danger" class="btn-xs" @click.prevent="removeRule(index)" style="margin-bottom: 5px;" v-b-tooltip.hover :title="$t('common.ICON_TIP_DEL')"><i class="fas fa-trash-alt"></i></b-btn>
            </div>
          </div>
        <br> 
          <div class="col-12 ml-3">
            <button class="btn btn-labeled btn-info" type="button" @click="addrule()">
              <span class="btn-label"><i class="fa fa-plus"></i></span>{{$t("service.INGRS_DIAL_CRTEINGRS_BTN_ADD_NEW_RULE")}}
            </button> 
          </div>  
      </fieldset>
      <fieldset>
        <div class="form-group row align-items-end">
          <div class="col-12">
            <label class="switch">{{$t("service.INGRS_DIAL_CRTEINGRS_REWRITE_TARGET")}}:
              <input type="checkbox" :checked="newIngress.rewritebackend" v-model="newIngress.rewritebackend"/>
              <span class="ml-2"></span>
            </label>
          </div>
        </div>
      </fieldset>
    </form>
    <template v-slot:modal-footer="{ok, cancel}">
      <b-button size="sm" variant="success" @click="ok()">{{$t("service.INGRS_DIAL_CRTEINGRS_BTN_SUBMIT")}}</b-button>
      <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.REGISTRY_DIAL_CRTECERT_BTN_CANCEL")}}</b-button>
    </template>
  </b-modal>
  <!-- 新建Ingress結束 --> 

  <!-- 專案表單開始 -->
  <ProjectTable :strProject="strProject" @getPjname="getPjname" v-show="showProject"/>
  <!-- 專案表單結束 -->

  <!-- 命名空間表單開始 -->
  <NamespaceTable :strProject="strProject" :tableDataNamespace="tableDataNamespace" @getNamespace="getNamespace" v-if="showNamespace"/>
  <!-- 命名空間表單結束 -->


 <!-- 分類表單開始 -->
  <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" @click.prevent="workload()" v-b-modal.createIngress v-show="showIngress">{{$t("service.INGRS_BTN_CRTE_INGRS")}}</b-btn>
  <br>
  <br>
  <v-client-table :data="tableData3" :columns="columns" :options="options" v-show="showIngress">
    <template slot="state" slot-scope="props">
      <div>
        <div class="p-2" :class="[unactive, props.row.state=='active'? active : '']">
          {{props.row.state}}
        </div>  
      </div>
    </template>
    <template slot="action" slot-scope="props">
      <div>
        <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="editpop(props.row)"  v-b-modal.editIngress v-b-tooltip.hover :title="$t('common.ICON_TIP_EDIT')"><i class="fas fa-edit"></i></b-btn>
        <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)"><i class="fas fa-trash-alt" v-b-tooltip.hover :title="$t('common.ICON_TIP_DEL')"></i></b-btn>
      </div>
    </template>
  </v-client-table>
  <!-- 分類表單結束 -->

  <!-- 編輯Ingress開始 -->
  <b-modal class="modal fade" id="editIngress" :title="$t('service.INGRS_DIAL_EDITINGRES_TITLE')" ref="modal" size="xl" @show="resetModal" @hidden="resetModal" @ok="handleOk">
    <form ref="form" @submit.stop.prevent="handleSubmit">
      <fieldset>
        <dl class="row">
          <dt class="col-sm-3">{{$t("service.INGRS_DIAL_CRTEINGRS_LBL_PRJ_NAME")}}:</dt>
          <dd class="col-sm-9">{{strProject}}</dd>
          <dt class="col-sm-3">{{$t("service.INGRS_DIAL_CRTEINGRS_LBL_NAMESPACE")}}:</dt>
          <dd class="col-sm-9">{{strNamespace}}</dd>
          <dt class="col-sm-3">{{$t("service.INGRS_LST_INGRS_NAME")}}:</dt>
          <dd class="col-sm-9">{{editIngress.name}}</dd>
          <dt class="col-sm-3">{{$t("service.INGRS_DIAL_CRTEINGRS_IPTLBL_DOMAIN_NAME")}}:</dt>
          <dd class="col-sm-9">{{editIngress.host}}</dd>
        </dl>
      </fieldset>

      <fieldset>
          <div class="form-group row align-items-end"  v-for="(item, index) in editIngress.paths">
            <div class="col-4">
                <label class="col-form-label">{{$t("service.INGRS_DIAL_CRTEINGRS_IPTLBL_PATH")}}:</label>
                <input v-model="item.path" :class="{'form-control':true}" type="text"/>
            </div>
            <div class="col-1">
              <h4 class="text-center"> = </h4>
            </div>
            <div class="col-3"> 

                <label class="col-form-label">{{$t("service.INGRS_DIAL_CRTEINGRS_IPTLBL_DEST")}}:</label>
                <select class="custom-select custom-select-md" v-model="item.selected[0].name" required>
                 <option value="" selected disabled hidden>{{$t('service.INGRS_DIAL_CRTEINGRS_HINT_DEST')}}</option>
                 <option v-for="(child, idx) in workloadIds" :value="child.name">{{child.name}}</option>

                </select>
            </div>
            <div class="col-1">
              <h4 class="text-center"> = </h4>
            </div>
            <div class="col-2">
                <label class="col-form-label">{{$t("service.INGRS_DIAL_CRTEINGRS_PORT")}}:</label>
                <input v-model="item.targetPort" :class="{'form-control':true}" type="number"/>
            </div>
            <div class="col-1">
              <b-btn variant="outline-danger" class="btn-xs" @click.prevent="editremoveRule(index)" style="margin-bottom: 5px;"><i class="fas fa-trash-alt"></i></b-btn>
            </div>
          </div>
        <br> 
          <div class="col-12 ml-3">
            <button class="btn btn-labeled btn-info" type="button" @click="editaddrule()">
              <span class="btn-label"><i class="fa fa-plus"></i></span>{{$t("service.INGRS_DIAL_CRTEINGRS_BTN_ADD_NEW_RULE")}}
            </button> 
          </div>
      </fieldset>
    </form>
    <template v-slot:modal-footer="{ok, cancel}">
      <b-button size="sm" variant="success" @click="editSubmit()">{{$t("service.INGRS_DIAL_CRTEINGRS_BTN_SUBMIT")}}</b-button>
      <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.REGISTRY_DIAL_CRTECERT_BTN_CANCEL")}}</b-button>
    </template>
  </b-modal>
  <!-- 編輯Ingress結束 -->
  
 


</div>
</template>  

<script>
import Vue from 'vue';
import { ClientTable,  Event } from 'vue-tables-2';
import swal from 'sweetalert2'
import VeeValidate from 'vee-validate';
import ProjectTable from "./service/ProjectTable";
import NamespaceTable from "./service/NamespaceTable";

Vue.use(VeeValidate, {
    fieldsBagName: 'formFields'  // fix issue with b-table
})
Vue.use(ClientTable);

export default {
  components: {
    ClientTable,
    ProjectTable,
    NamespaceTable
  },
  data(){
    return{
      unactive:'unactive',
      active:'active',
      timer1:'',
      name:'',
      nameState: null,
      text:'',
      textState: null,
      submittedNames: [],
      selectedIndex: '',
      selectedProject: '',
      alldata:[],
      projectlist:[],
      strProject:'',
      namespacelist:[],
      strNamespace:'',
      strspacename:'',
      stringressname:'',
      stringedit:'',
      tableData3: [],
      tableDataNamespace :[],
      showProject: true,
      showNamespace: false,
      showIngress: false,
      disabled: true,
      ip:'',
      workloadIds:[],
      newIngress:{
        name:'',
        rewritebackend: false,
        host: '',
        paths:[
          {
            path:'/',
            targetPort: "80",
            workloadIds: [],
            selected: [
              {
                name: ""
              }
            ]
          }
        ],
        
      },
      
      editIngress:{
        name:'',
        rewritebackend: false,
        host: '',
        paths:[
          {
            path:'/',
            targetPort: "80",
            workloadIds: [],
            selected: [
              {
                name: ""
              }
            ]
          }
        ],
      },
      selectedaaa: '',
      curWorkloads: [],
      columns: ['name','state', 'host', 'action'],
      options: {
        sortable: ['name', 'host'],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder:this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count:this.$t('common.LBL_TOTAL') + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults:this.$t('common.LBL_NO_RECORD'),
        },
        headings: {
            name: () => this.$t('service.INGRS_LST_INGRS_NAME'),
            state: () => this.$t('service.VOLUME_SELLBL_LST_STATUS'),
            host: () => this.$t('service.INGRS_LST_ROUTE'),
            action: () => this.$t('service.INGRS_LST_OPT'),
        },
        pagination: { chunk: 5 },
        sortIcon: {
            is: 'fa-sort',
            base: 'fas',
            up: 'fa-sort-up',
            down: 'fa-sort-down'
        },
        templates: {
          // 自訂欄位 路由規則 //
          host(h, row) {
            const hostUrlInfos = [];
            
            row.rules.forEach(rule => {
              const host = rule.host;

              rule.paths.forEach(path => {
                const completePath = host + path.path;

                const url = "http://" + completePath;
                const workloadId = path.workloadIds[0];
                var result = workloadId.split(":");
                var last = result[result.length - 1];

                hostUrlInfos.push(
                  <div>
                    <a href={url} target="_blank">
                      {completePath}
                    </a>{" "}
                    &#x2192;{last}
                  </div>
                );
              });
            });
            return <div>{hostUrlInfos}</div>;
          }
        }, 
      },     
      isNew: false,
      isLoading: false
    };
  },
  created(){
    // this.curWorkloads = [];
    this.isLoading = true;
    backendapi.get("/project").then((response) => {
      this.isLoading = false;
      this.alldata = response.data;
      this.projectlist = response.data;
    })
    .catch(error => {
       alert(error.message)
    });
  },
  computed: {
    newhost() {
      return this.newIngress.name + "." + this.ip + ".xip.io"
    }
  },
  methods:{

    getPjname(PJname){
      this.strProject = PJname
      this.isLoading = true;
      clearInterval(this.timer1);
      this.timer1 = setInterval(()=> {this.reloadd1()}, 3000);
      backendapi.get(`/project/${this.strProject}/namespace`)
        .then(response => {
          this.namespacelist = response.data;
          this.tableDataNamespace = response.data.map((item, index) => {
            item["id"] = index;
            item['createdTS'] = moment(item.createdTS);
            return item;
          });
          this.selected = true;
          this.showProject = false
          this.showNamespace = true
          this.isLoading = false
          document.getElementById("myselected").selected = true;
          if (this.namespacelist.length >= 1) {
            this.disabled = false;
          } else {
            this.disabled = true;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },

    getNamespace(namespace){
      this.strNamespace = namespace
      clearInterval(this.timer1);
      this.timer1 = setInterval(()=> {this.reloadd1()}, 3000);
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/ingress`).then((response) => {
        this.workload()
        this.showProject = false
        this.showNamespace = false
        this.showIngress = true;
        this.ip = response.data.lb_external_ip

        this.tableData3 = response.data.ingress.map((item, index) => {
            item['id'] = index
            return item
        });

        // TODO: Reset newIngress
        this.newIngress = {
            name:'',
            rewritebackend: false,
            host: '',
            paths:[
              {
                path:'/',
                targetPort: "80",
                workloadIds: [],
                selected: [
                  {
                    name: ""
                  }
                ]
              }
            ]
          };
      })
      .catch(error => {
        alert(error.message)
      });
    },



    validateBeforeSubmit(scope) {
        this.$validator.validateAll(scope).then((result) => {
        if (result) {
            this.handleSubmit();
            return;
          }
            console.log('Correct them errors!');
        });
    },
    reloadd1(){
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/ingress`).then((response) => {
        this.workload()
        this.ip = response.data.lb_external_ip
        this.tableData3 = response.data.ingress.map((item, index) => {
            item['id'] = index
            return item
        });
      })
    },
    // 選取專案 //
    selectedproject() {
      clearInterval(this.timer1);
      var projectlist = document.getElementById("projectlist");
      this.strProject = projectlist.options[projectlist.selectedIndex].value;

      this.isLoading = true;
      this.tableData3 = []
      backendapi.get(`/project/${this.strProject}/namespace`).then((response) => {
        this.namespacelist = response.data;

        this.tableDataNamespace = response.data.map((item, index) => {
          item["id"] = index;
          item['createdTS'] = moment(item.createdTS);
          return item;
        });
        
        this.showProject = false
        this.showNamespace = true
        this.showIngress = false;
        this.selected = true;
        document.getElementById("myselected").selected = true;
        if (this.namespacelist.length >= 1 ){
          this.disabled = false
        }else{
          this.disabled = true
        }
        this.isLoading = false;
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    },
    // 選取命名空間 //
    selectednamespace() {
      var namespacelist = document.getElementById("namespacelist");
      this.strNamespace = namespacelist.options[namespacelist.selectedIndex].value;
      clearInterval(this.timer1);
      this.timer1 = setInterval(()=> {this.reloadd1()}, 3000);
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/ingress`).then((response) => {
        this.workload()
        this.showProject = false
        this.showNamespace = false
        this.showIngress = true;
        this.ip = response.data.lb_external_ip

        this.tableData3 = response.data.ingress.map((item, index) => {
            item['id'] = index
            return item
        });

        // TODO: Reset newIngress
        this.newIngress = {
            name:'',
            rewritebackend: false,
            host: '',
            paths:[
              {
                path:'/',
                targetPort: "80",
                workloadIds: [],
                selected: [
                  {
                    name: ""
                  }
                ]
              }
            ]
          };
      })
      .catch(error => {
        alert(error.message)
      });
    },
    // 重新讀取表單 //
    loadTable(){
      setTimeout(() => {
        if(this.strProject && this.strNamespace) {
            console.log("timeout");
            backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/ingress`).then((response) => {
              this.showIngress = true;
              this.ip = response.data.lb_external_ip;
              this.tableData3 = response.data.ingress.map((item, index) => {
                  item['id'] = index
                  return item
              });
              this.isLoading = false;
            })
            .catch(error => {
              alert(error.message)
              this.isLoading = false;
            });
        }
      }, 5000);
    },
    // 獲取 workloadId //
    workload(){
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/service`).then((response) => {
        this.newIngress.host = this.newIngress.name + "." + this.ip + ".xip.io"
        // console.log("workload() response.data------", response.data);
        this.workloadIds = response.data;
      })
      .catch(error => {
        alert(error.message)
      });
    },
    // 驗證 //
    checkFormValidity() {
      const vm = this;
      const valid = vm.$refs.form.checkValidity();
      this.nameState = valid ? 'valid' : 'invalid'
      return valid
    },
    // 驗證 //
    handleOk(e) {
      const vm = this;
      e.preventDefault();
      if(e.componentId === "createIngress") {
        this.validateBeforeSubmit('newIngress');
      }
    },
    // 驗證 //
    resetModal() {
      const vm = this;
      vm.name = ''
      vm.text = ''
      vm.nameState = null
      vm.textState = null
      vm.newIngress.name = "";
      vm.newIngress.rewritebackend = false;
      vm.paths = [
          {
            path:'/',
            targetPort: "80",
            workloadIds: [],
            selected: [
              {
                name: ""
              }
            ]
          }
        ];
    },
    // 名稱 域名連動 //
    changehost(){
      this.newIngress.host = this.newhost;
    },
    // 新增規則按鈕 //
    addrule(){
      const newObject = {
        path:'/',
        targetPort: "80",
        workloadIds: [],
        selected: [
              {
                name: ""
              }
        ]
      }
      
      this.newIngress.paths.push(newObject);
    },
    // 刪除規則按鈕 //
    removeRule (index) {
      this.newIngress.paths.map((item, index) => {
        item['id'] = index
        return item
      });  
      if (this.newIngress.paths.length <= 1) {
        return
      }else{
        var outIndex = this.newIngress.paths.filter(item => (this.newIngress.paths[index].id === item.id));
        this.newIngress.paths.splice(outIndex[0].id, 1)
      }  
    },
    // 新建Ingress送出 //
    handleSubmit() {
      // debugger;
      this.isLoading = true;

      this.newIngress.paths.forEach((path, idx) => {
        path.workloadIds = [];
        path.workloadIds.push("deployment:" + this.strNamespace +":"+ path.selected[0].name);
      });
      
      this.newIngress.host = this.newhost
      backendapi.post(`/project/${this.strProject}/namespace/${this.strNamespace}/ingress`,{
        name: this.newIngress.name,
        rewritebackend: this.newIngress.rewritebackend,
        rules: [
          {
            host: this.newIngress.host,
            paths: this.newIngress.paths
          }  
        ]
      }).then((response) => {
        // this.curWorkloads = [];
        this.selectednamespace()
        this.isLoading = false;
        this.loadTable();
        this.$nextTick(() => {
          this.$refs.modal.hide()
        });
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    },

    // 表單編輯出現 //
    editpop (row) {
      console.log("pppppp");
      this.stringedit = row.name
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/ingress/${this.stringedit}`).then((response) => {
        // debugger;
        this.editIngress.name = response.data.name
        this.editIngress.host = response.data.rules[0].host
        this.editIngress.paths = response.data.rules[0].paths

        // console.log("workloadids", this.workloadIds);
        // console.log("paths", this.editIngress.paths);
        // console.log("length", this.workloadIds.length);

        // debugger;

        if(this.workloadIds.length > 0){
          this.editIngress.paths.forEach((path, idx) => {
            this.$set(path, "selected", []);
            var curWorkload = path.workloadIds[0].split(":")[2];
            path.selected.push({name: curWorkload});

          });
        }else{
          this.editIngress.paths.forEach(path => {
            this.$set(path, "selected", [{
              name: ''
            }]);
          });
        }
        // console.log("current workloadids", this.curWorkloads);
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    },
    // 表單編輯 新增規則 //
    editaddrule(){
      const newObject = {
        name: '',
        path:'/',
        targetPort: "80",
        workloadIds: [],
        selected: [
              {
                name: ""
              }
        ]
      }
      // console.log(this.workloadIds[0].name);
      // this.curWorkloads.push(
      // {
      //   name: this.workloadIds[0].name
      // });

      this.editIngress.paths.push(newObject);
    },
    // 表單編輯 刪除規則 //
    editremoveRule (index) {
      this.editIngress.paths.map((item, index) => {
        item['id'] = index
        return item
      });  
      if (this.editIngress.paths.length <= 1) {
        return
      }else{
        var outIndex = this.editIngress.paths.filter(item => (this.editIngress.paths[index].id === item.id));
        this.editIngress.paths.splice(outIndex[0].id, 1)
      }  
    },
    // 表單編輯 送出 //
    editSubmit(){
      this.isLoading = true;
      
      this.editIngress.paths.forEach((path, idx) => {
        console.log("edit submit", path);
        path.workloadIds = [];
        path.workloadIds.push("deployment:" + this.strNamespace +":"+ path.selected[0].name);
      });

      backendapi.put(`/project/${this.strProject}/namespace/${this.strNamespace}/ingress/${this.stringedit}`,{
        rules: [
          {
            host: this.editIngress.host,
            paths: this.editIngress.paths
          }  
        ]
      }).then((response) => {
        
        this.selectednamespace()
        this.isLoading = false;
        this.loadTable();
        this.$nextTick(() => {
          this.$refs.modal.hide();
        });
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    },

    // 分類表單刪除 //
    remove (row) {
      this.isLoading = true;
      this.tableData3.filter(item => (row.id !== item.id));
      this.stringressname = this.tableData3[row.id].name
      backendapi.delete(`/project/${this.strProject}/namespace/${this.strNamespace}/ingress/${this.stringressname}`).then((response) => {
        this.loadTable();
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    },
    // 刪除步驟 //
    checkCancel(row) { 
      swal({
          title: this.$t('service.INGRS_DIAL_DELINGRES_MSG'),
          text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: this.$t('common.BTN_CONFIRM'),
          cancelButtonText:this.$t('common.BTN_CANCEL'),
          heightAuto: false
      }).then((result) => {
          if(result.value){
          this.remove(row);
            swal({
                title: this.$t('common.MSG_DEL_SUCCESS'),
                confirmButtonText: this.$t('common.BTN_OK'),
                type: "success",
                heightAuto: false
            });
          } 
      });
    }
  },
  destroyed: function() {
    console.log('ingress timer destroyed');
    clearInterval(this.timer1);
  }
}
</script>

<style>
#createIngress .row{
  margin:0 15px;
}
#createIngress .switch input:checked+span {
    background-color: #18a0ca;
    border-color: #18a0ca;
}
fieldset:last-child{
  margin-bottom: 0;
}

.must_write{
  color:red;
  font-size: 13px;
  margin-left: 5px;
  margin-bottom: 5px;
}

.invalid-feedback {
  display: block;
  margin-left: 200px;
}
</style>


