<template>
  <div>
    
    <form ref="form" @submit.stop.prevent="handleSubmit">
      <div class="form-group">
        <label for="userName">{{$t("service.ACNT_DIAL_NACNT_IPTLBL_ACNT")}}</label>
        <b-form-input type="text" id="userName" :placeholder="$t('service.ACNT_DIAL_NACNT_HINT_ACNT_NAME')" v-model="userName"></b-form-input>
      </div>
      <div class="form-group">
        <label for="password">{{$t("service.ACNT_DIAL_NACNT_IPTLBL_PWD")}}</label>
        <b-form-input type="password" id="password" v-model="password" :placeholder="$t('service.ACNT_DIAL_NACNT_HINT_PWD')"></b-form-input>
      </div>
      <div class="form-group">
        <label for="password">{{$t("navbar.CONFIRM_PWD")}}</label>
        <b-form-input type="password" id="password" v-model="repassword" :placeholder="$t('navbar.HINT_CONFIRM_PWD')"></b-form-input>
      </div>
    </form>
    <div class="modal-footer mt-4">
      <b-button size="sm" variant="success" @click="handleSubmit()">{{$t("service.ACNT_DIAL_NACNT_BTN_SUBMIT")}}</b-button>
      <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.ACNT_DIAL_NACNT_BTN_CANCEL")}}</b-button>
    </div>
 
  </div>
</template>

<script>
import Vue from 'vue';
import swal from 'sweetalert2'
export default {
  components: {
  },
  data(){
    return{
      userName:'',
      userNameState: null,
      password:'',
      passwordState: null,
      repassword:'',
      repasswordState: null,
    };
  },
  methods:{
    // 驗證 //
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity();
      this.userNameState = valid ? 'valid' : 'invalid'
      this.passwordState = valid ? 'valid' : 'invalid'
      this.repasswordState = valid ? 'valid' : 'invalid'
      this.userName = this.userName.trim()
      this.password = this.password.trim()
      this.repassword = this.repassword.trim()
      if(this.userName == ''){
        alert(this.$t('service.ACNT_NACNT_ALERT_NO_ACNT'));
      }
      else if(this.userNameState !== 'valid'){
        alert(this.$t('service.ACNT_NACNT_ALERT_NO_ACNT'));
      }else if (this.password==""){
        alert(this.$t('service.ACANT_NACNT_ALERT_NO_PWD'));
      }else if (this.password!==this.repassword){
        alert(this.$t('navbar.ALERT_CONFIRM_PWD_FAIL'));
        this.password = ""
        this.repassword = ""
      }else{
        return valid;
      }
    },
    // 驗證 //
    handleOk(e) {
      e.preventDefault();
      this.handleSubmit();
    },
    // 預設值 //
    resetModal() {
      this.userName = ''
      this.password = ''
      this.repassword = ''
      this.userNameState = null
      this.passwordState = null
      this.repasswordState = null
    },
    cancel(){
      this.$emit('loadTable')
    },
    // 新建用戶送出 //
    handleSubmit() {
      if (!this.checkFormValidity()) {
        return
      }
      backendapi.post('/user', {
        name: this.userName,
        password: this.password
      }).then((response) => {
        this.$emit('loadTable')
      })
      .catch(error => {
        this.$emit('loadTable')
        alert(error.message);
      });
    },
  },
}
</script>
    




