    return{
      ports:[
        {
          key:"port",
          value:"8080"
        },
      ],
      environment: [
      ],
      configList: [
      ],
      labels:[
      ],

      pvcSelect:[
      ],
      pvcObject:{
        name:'',
        mountPath:'',
        persistentVolumeClaim:{
          persistentVolumeClaimId:'',
          readOnly:false
        }
      },

      configSelect:[
      ],
      configObject:{
        name:'',
        mountPath:'',
        configMap:{
          name:'',
        }
      },

      nfsObject:{
        name:'',
        mountPath:'',
        nfs:{
          path:'',
          server:''
        }
      },
      strategy: "1",
      scale: 1,
      max_replicas: 5,
      min_replicas: 1,
      target_cpu: 30,

      isPort: false,

      loadbalancer: [
        "LEAST_CONN",
        "ROUND_ROBIN",
        "RANDOM"
      ],
      iscircuitbreaker:true,
      openPort:0,
      demo: {
        name: '',
        image: '',
        ports:{

        },
        environment:{

        },
        environmentFrom: [
        ],
        labels:{
        },
        volumeMounts:[
        ],
        volumes:[
        ],
        scale:"",
        target_cpu:"",
        max_replicas:"",
        min_replicas:"",
        limitsCpu: "200",
        limitsMemory: "128",
        requestsCpu: "200",
        requestsMemory: "128",
        loadbalancer:"LEAST_CONN",
        circuitbreaker:"",
        port_list:[],
        connecttimeout: 10000,
	      maxconnections: 100
      },
    };
  },

  created(){
    backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap`).then((response) => {
      response.data.map((item, index) => {
        this.configList.push(item['name'])
      });  
      this.isLoading = false;
    })
    .catch(error => {
      alert(error.message);
      this.isLoading = false;
    });
    backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/persistentvolumeclaim`).then((response) => {
      this.pvcSelect = []
      response.data.map((item, index) => {
        this.pvcSelect.push(item)
      });
    })
    .catch(error => {
      alert(error.message);
    });
    backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap`).then((response) => {
      this.configSelect = []
      response.data.map((item, index) => {
        this.configSelect.push(item)
      });
    })
    .catch(error => {
      alert(error.message);
    });
  },

  methods:{
    // 基本訊息必填驗證 //
    step1(){
      if(this.demo.name.match(/[A-Z]|[\u4e00-\u9fa5]/) || this.demo.image.match(/[A-Z]|[\u4e00-\u9fa5]/)){
        return false;
      }     
      if(this.demo.name==this.strNamespace){
        alert("命名空間不可與服務名稱相同")
        return false;
      }
      if(this.demo.name.match(/[a-z]|\d|"|_/g) && this.demo.image.match(/[a-z]|\d|"|_/g) && this.demo.name!==this.strNamespace){
        return true;
      }
    },
    // 環境變數驗證 //
    step2(){
      var checkStep2 = true
      for(var x=0; x<this.environment.length; x++){
        if(this.environment[x].key==""){
          alert("環境變數不可為空值")
          checkStep2 = false
          return checkStep2 
          break;
        }else{
          checkStep2 = true
        }
      }
      for(var i=0; i<this.demo.environmentFrom.length; i++){
        if(this.demo.environmentFrom[i].sourceName==""){
          alert("不可為空值")
          checkStep2 = false
          return checkStep2 
          break;
        }else{
          checkStep2 = true
        }
      }
      return checkStep2        
    },
    // 標籤驗證 //
    step3(){
      var checkStep3 = true
      for(var y=0; y<this.labels.length; y++){
        if(this.labels[y].key==""){
          alert("標籤不可為空值")
          checkStep3 = false
          return checkStep3
          break;
        }else{
          checkStep3 = true
        }
      }
      return checkStep3            
    },
    // 存儲驗證 //
    step4(){
      var checkStep4 = true
      for(var z=0; z<this.demo.volumes.length; z++){
        if(this.demo.volumes[z].name==""){
          alert("存儲名稱不可為空值")
          checkStep4 = false
          return checkStep4
          break;
        }else{
          checkStep4 = true
        }
      }
      for(var s=0; s<this.demo.volumes.length; s++){
        if(this.demo.volumes[s].nfs.server==""){
          alert("存儲路徑不可為空值")
          checkStep4 = false
          return checkStep4
          break;
        }else{
          checkStep4 = true
        }
      }
      for(var k=0; k<this.demo.volumes.length; k++){
        if(this.demo.volumes[k].nfs.path==""){
          alert("存儲路徑不可為空值")
          checkStep4 = false
          return checkStep4
          break;
        }else{
          checkStep4 = true
        }
      }
      return checkStep4   
    },
    // 基本訊息增加端口 //
    addport(){
      const newPort = {
        key:"port",
        value: ""
      }
      this.ports.push(newPort);
    },
    // 基本訊息刪除端口 //
    removePorts (index) {
      this.ports.map((item, index) => {
        item['id'] = index
        return item
      });  
      if (this.ports.length <= 1) {
        return
      }else{
        var outIndex = this.ports.filter(item => (this.ports[index].id === item.id));
        this.ports.splice(outIndex[0].id, 1)
      }  
    },    
    // 環境變數增加變量 //
    addrule(){
      const newRule = {
        key:"",
        value: ""
      }
      this.environment.push(newRule);
    },
    // 基本訊息刪除變量 //
    removeEnvironment (index) {
      this.environment.map((item, index) => {
        item['id'] = index
        return item
      });  
      if (this.environment.length <= 0) {
        return
      }else{
        var outIndex = this.environment.filter(item => (this.environment[index].id === item.id));
        this.environment.splice(outIndex[0].id, 1)
      }  
    },
    // 環境變數匯入配置項 //
    addconfig(){
      const newConfig = {
        source: "configMap",
        sourceName: ""
      }
      this.demo.environmentFrom.push(newConfig);
    },
    // 環境變數刪除配置項 //
    removeConfig(index){
      this.demo.environmentFrom.map((item, index) => {
        item['id'] = index
        return item
      });  
      if (this.demo.environmentFrom.length <= 0) {
        return
      }else{
        var outIndex = this.demo.environmentFrom.filter(item => (this.demo.environmentFrom[index].id === item.id));
        this.demo.environmentFrom.splice(outIndex[0].id, 1)
      }
    },
    // 標籤變量 //
    addlabel(){
      const newLabel = {
        key:"",
        value: ""
      }
      this.labels.push(newLabel);
    },
    removelabel(index){
      this.labels.map((item, index) => {
        item['id'] = index
        return item
      });  
      if (this.labels.length <= 1) {
        return
      }else{
        var outIndex = this.labels.filter(item => (this.labels[index].id === item.id));
        this.labels.splice(outIndex[0].id, 1)
      }
    },
    // 數據卷 新增雲硬碟 //
    pushvolpvc(){
      var a = document.getElementById("pvcpvc");
      const pvcVol = {
        name: this.pvcObject.name,
        mountPath: this.pvcObject.mountPath,
        persistentVolumeClaim: {
          persistentVolumeClaimId: a.options[a.selectedIndex].value,
          readOnly: false
        }
      }
      const pvcMounts ={
        name: this.pvcObject.name,
        mountPath: this.pvcObject.mountPath
      }
      this.demo.volumes.push(pvcVol)
      this.demo.volumeMounts.push(pvcMounts)
      this.pvcObject = {
        name:'',
        mountPath:'',
        persistentVolumeClaim:{
          persistentVolumeClaimId:'',
          readOnly:false
        }
      }
    },
    // 數據卷 新增配置項 //
    pushvolconfig(){
      var b = document.getElementById("configconfig");
      const configVol = {
        name: this.configObject.name,
        mountPath: this.configObject.mountPath,
        configMap: {
          name: b.options[b.selectedIndex].value,
        }
      }
      const configMounts = {
        name: this.configObject.name,
        mountPath: this.configObject.mountPath
      }


      this.demo.volumes.push(configVol)
      this.demo.volumeMounts.push(configMounts)

      this.configObject = {
        name:'',
        mountPath:'',
        configMap:{
          name:'',
        }
      }

    },
    // 數據卷 新增nfs //
    pushvolnfs(){
      const nfsVol = {
        name: this.nfsObject.name,
        mountPath: this.nfsObject.mountPath,
        nfs: {
          path:this.nfsObject.nfs.path,
          server:this.nfsObject.nfs.server
        }
      }
      const nfsMounts = {
        name: this.nfsObject.name,
        mountPath: this.nfsObject.mountPath
      }
      this.demo.volumes.push(nfsVol)
      this.demo.volumeMounts.push(nfsMounts)

      this.nfsObject = {
        name:'',
        mountPath:'',
        nfs:{
          path:'',
          server:''
        }
      }
    },
    // 刪除 數據卷 //
    removeVolumes(index){
      this.demo.volumes.map((item, index) => {
        item['id'] = index
        return item
      });
      this.demo.volumeMounts.map((item, index) => {
        item['id'] = index
        return item
      });
      if (this.demo.volumes <= 1) {
        return
      }else{
        var outIndex = this.demo.volumes.filter(item => (this.demo.volumes[index].id === item.id));
        this.demo.volumes.splice(outIndex[0].id, 1)
        var outIndex2 = this.demo.volumeMounts.filter(item => (this.demo.volumeMounts[index].id === item.id));
        this.demo.volumeMounts.splice(outIndex2[0].id, 1)
      }
    },
    // Number限制確認 //
    numberCheck(){
      if(this.target_cpu > 100){
        this.target_cpu = 100
      }
      if(this.target_cpu < 0){
        this.target_cpu = 0
      }
      if(this.openPort > 60000){
        this.openPort = 60000
      }
      if(this.openPort < 0){
        this.openPort = 0
      }
    },
    // 新增開放端口 //
    pushPort(){
      var aaa = [parseInt(this.openPort)]
      this.demo.port_list.push(aaa)
    },
    // 刪除開放端口 //
    removePort (index) {
      var outIndex = this.demo.port_list.filter((item,index) => (this.demo.port_list[index] === item[index]));
      this.demo.port_list.splice(outIndex, 1)
    },
    // 負載均衡Select //
    loadbalanceChange() {
      console.log(this.demo.loadbalancer)
    },
    // Final Check //
    submit(){
      for (var i = 0; i < this.ports.length; i++) {
        this.ports[i].key = this.ports[i].key + [i]
        this.demo.ports[this.ports[i].key] = this.ports[i].value;
      }
      for (var i = 0; i < this.environment.length; i++) {
        this.demo.environment[this.environment[i].key] = this.environment[i].value;
      }
      for (var i = 0; i < this.labels.length; i++) {
        this.demo.labels[this.labels[i].key] = this.labels[i].value;
      }
      for (var i = 0; i < this.demo.volumes.length; i++) {
        delete this.demo.volumes[i]["mountPath"];
      }

      // 部屬策略 //
      if(this.strategy == "1"){
        delete this.demo.target_cpu
        delete this.demo.max_replicas
        delete this.demo.min_replicas
        this.demo.scale = parseInt(this.scale)
      }else if(this.strategy == "2"){
        this.demo.target_cpu = parseInt(this.target_cpu)
        this.demo.max_replicas = parseInt(this.max_replicas)
        this.demo.min_replicas = parseInt(this.min_replicas)
        this.demo.scale = 1
      }

      this.demo.limitsCpu=this.demo.limitsCpu + "m"
      this.demo.limitsMemory=this.demo.limitsMemory + "Mi"
      this.demo.requestsCpu=this.demo.requestsCpu + "m"
      this.demo.requestsMemory=this.demo.requestsMemory + "Mi"


      if(this.isPort == true){
        this.demo.port_list = this.demo.port_list.map(item => [...item, ...item]);
      }else{
        this.demo.port_list = []
      }

      // 熔斷機制 //
      if(this.iscircuitbreaker == true){
        this.demo.circuitbreaker="enabled"
      }else if(this.iscircuitbreaker == false){
        this.demo.circuitbreaker="disabled"
      }
      this.go();
    },

    go(){
      backendapi.post(`/project/${this.strProject}/namespace/${this.strNamespace}/service`,this.demo).then((response) => {
        this.$emit('childMethod');
      })
      .catch(error => {
        alert(error.message)
        this.$emit('childMethod');
      });
      console.log(this.demo)
    }
  }
}
</script>
    
<style src="vue-form-wizard/dist/vue-form-wizard.min.css"></style>

<style>
.vue-form-wizard .wizard-nav-pills>li>a>span {
    margin-left: 0rem;
    text-align: center;
    width: 100%;
    font-weight: bold;
}
.vue-form-wizard .wizard-nav-pills>li>a {
    padding: .5rem !important;
    border-radius: 7px;
}
.vue-form-wizard.md .wizard-icon-circle {
    font-size: 15px;
}
.vue-form-wizard.md .wizard-nav-pills>li.active>a .wizard-icon {
    font-size: 15px;
}
.vue-form-wizard .wizard-nav-pills>li>a>span>div {
    font-weight:bold;
}
.cloudDrive, .NFS, .config, .cloudDrive:last-child, .NFS:last-child, .config:last-child{
  padding: 20px;
  border: 1px dashed #eee;
  margin-bottom: 20px;
}
.icon-new_service{
  border:none;
  box-shadow:none;
}
.tab-content{
  border-width: 0 0 0 1px;
}
.form-group h4{
  text-align: center;
}
.deployform .form-inline{
  margin: 10px 0;
}
.deployform input{
  margin: 0px 5px;
}
.deployform fieldset{
  padding: 5px 0;
  margin: 0;
}
fieldset:last-child{
  margin-bottom: 0;
}

span.s_comment{
  display: block;
  font-size: 13px;
  margin-top: 10px;
}

.must_write{
  color:red;
  font-size: 13px;
  margin-left: 5px;
}

.input_tooltip{
    padding: 0px;
    box-shadow: none;
    border: none;
}
.input_tooltip i{
    font-size: 24px;
    color:red;
}
</style>



