<template>
  
  <div class="loginbg">
    <div class="block-center wd-xl">
      <!-- 登入區域開始 -->
      <div class="card card-flat">
        <div
          class="card-header text-center bg-dark"
          style="color:#fff; font-size:1.2rem;font-weight:bold;"
        >
          <img src="static/image/logo.png" style="max-height: 100%;" />
        </div>
        <div class="card-body">
          <!-- <p class="text-center py-2">請先登入</p> -->
          <form class="mb-3 pt-3" @submit.prevent="login">
            <div class="form-group">
              <div class="input-group with-focus">
                <input
                  :class="{'form-control border-right-0':true}"
                  :placeholder="$t('common.LOGIN_HINT_CUSTOMER_ID')"
                  v-model="user.id"
                  type="text"
                  name="group-id"
                />
                <div class="input-group-append">
                  <span class="input-group-text text -muted bg-transparent border-left-0">
                    <em class="fa fa-id-badge"></em>
                  </span>
                </div>
              </div>
            </div>
            <div class="form-group">
              <div class="input-group with-focus">
                <input
                  :class="{'form-control border-right-0':true}"
                  :placeholder="$t('common.LOGIN_HINT_USER_NAME')"
                  v-model="user.name"
                  type="text"
                  name="name"
                />
                <div class="input-group-append">
                  <span class="input-group-text text -muted bg-transparent border-left-0">
                    <em class="fa fa-envelope"></em>
                  </span>
                </div>
              </div>
            </div>
            <div class="form-group">
              <div class="input-group with-focus">
                <input
                  :class="{'form-control  border-right-0':true}"
                  :placeholder="$t('common.LOGING_HINT_PWD')"
                  v-model="user.password"
                  type="password"
                />
                <div class="input-group-append">
                  <span class="input-group-text text-muted bg-transparent border-left-0">
                    <em class="fa fa-lock"></em>
                  </span>
                </div>
              </div>
            </div>

            <!-- <div class="clearfix">
              <div class="float-left">
                  <div class="custom-control custom-checkbox">
                      <small>預設ID:id00001,id12345</small><br>
                      <small>預設帳號:alex-tp@test.com, test1@test.com</small><br>
                      <small>預設密碼:123456</small>
                  </div>
              </div>
            </div>-->

            <div class="d-flex justify-content-center align-items-center ll">
              {{$t('navbar.SEL_LANGUAGE')}}:&ensp;&ensp;
              <b-dropdown class="langBtn" :text="selectedLanguage">
                <b-dropdown-item class="shadow-none" v-for="language in languages" :value="language.value" :key="language.value" @click="changeLanguage(language.value)">{{language.text}}</b-dropdown-item>
              </b-dropdown>
            </div> 

            <button class="btn btn-block btn-login mt-3" type="submit">{{$t('common.LOGIN_BTN_LOGIN')}}</button>

          </form>
        </div>
      </div>
      <!-- 登入區域結束 -->
    </div>
  </div>
</template>

<script>
import { myLoginRoutine } from "./LoginRoutine";
export default {
  name: "App",
  data() {
    return {
      languages: [
        {
          "value": "tw",
          "text": "繁體中文"
        },
        {
          "value": "ch",
          "text": "简体中文"
        },
        {
          "value": "en",
          "text": "English"
        }
      ],
      selectedLanguage:  "繁體中文",
      user: {
        id: "",
        name: "",
        password: ""
      }
    };
  },
  created() {
    console.log("bbbb");
  },
  mounted() {
    

    var curLocale = this.$i18n.locale;
    this.changeLanguage(curLocale);

    // if(curLocale == "ch"){
    //   this.selectedLanguage = "简体中文";

    // }else if(curLocale == "tw") {
    //   this.selectedLanguage = "繁體中文";

    // }else if(curLocale == "en") {
    //   this.selectedLanguage = "English";
    //   // document.querySelector('.aside-inner').style.width = "250";

    // }else {
    //   this.selectedLanguage = "繁體中文";
    // }    
  },
  methods: {
    signin() {
      backendapi
        .post("/login", {
          "group-id": this.user.id,
          name: this.user.name,
          password: this.user.password
        })
        .then(response => {
          // this.clusterid = response.data.clusterid
          // this.Authorization = "bearer " + response.data.token
          // localStorage.clear()
          localStorage.setItem("cluster-role", response.data["cluster-role"]);
          localStorage.setItem("clusterid", response.data["clusterid"]);
          localStorage.setItem("createdTS", response.data["createdTS"]);
          localStorage.setItem("group-id", response.data["group-id"]);
          localStorage.setItem("name", response.data["name"]);
          localStorage.setItem("token", response.data["token"]);
          localStorage.setItem("ttl", response.data["ttl"]);
          // this.$router.push("/admin/charts");
          this.$router.push("/center");  
        })
        .catch(error => {
          alert(this.$t('common.LOGIN_ALERT_LOGIN_FAIL'));
        });
    },
    login: function() {
      var groupId = this.user.id;
      var name = this.user.name;
      var password = this.user.password;

      // console.log("groupId", groupId);
      // console.log("name", name);
      // console.log("password", password);

      myLoginRoutine({ groupId, name, password }).then(() => {
        // this.$router.push("/admin/charts");
        this.$router.push("/center");
      });
    },
    changeLanguage(language) {
      if(language == "tw"){
        localStorage.setItem("language", 'tw');
        this.$i18n.locale = 'tw'
        this.selectedLanguage = "繁體中文";
        if (document.getElementById('section-container') != null) {
          document.getElementById('section-container').style.marginLeft = "";
        }
      }else if(language == "ch"){
        localStorage.setItem("language", 'ch');
        this.$i18n.locale = 'ch'
        this.selectedLanguage = "简体中文";
        if (document.getElementById('section-container') != null) {
          document.getElementById('section-container').style.marginLeft = "";
        }
      }else if(language == "en"){
        localStorage.setItem("language", 'en');
        this.$i18n.locale = 'en';
        this.selectedLanguage = "English";
        if (document.getElementById('section-container') != null) {
          document.getElementById('section-container').style.marginLeft = "255px";
        }
      }else{
        localStorage.setItem("language", 'tw');
        this.$i18n.locale = 'tw'
        this.selectedLanguage = "繁體中文";
        if (document.getElementById('section-container') != null) {
          document.getElementById('section-container').style.marginLeft = "";
        }
      }
    },
  }
};
</script>

<style>
body .loginbg {
  background-color: #3a3f51;
  position: relative;
  width: 100vw;
  height: 100vh;
  padding-top: 50px;
}
body .loginbg .card {
  background: #3a3f51;
}
body .loginbg .card-body {
  box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2),
    0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
  border-radius: 0.25rem !important;
  background: #fff;
}
.langBtn button{
  box-shadow:none!important
}
</style>



