<template>
  <div>
    <!-- 專案表單開始 -->
    <template>
      <div>
          <v-client-table id="projectTable" class="dark" :data="tableData" :columns="columns" :options="options">
            <template slot="name" slot-scope="props">
              <div>
                <a href="javascript:void(0)" @click.prevent="output(props.row.name)">{{props.row.name}}</a>
              </div>
            </template>
            <template slot="state" slot-scope="props">
              <div>
                <div class="p-2" :class="[unactive, props.row.state=='active'? active : '']">
                    {{props.row.state}}
                </div>
              </div>
              </template>
              <template slot="state" slot-scope="props">
                <div>
                  <div class="p-2" :class="[unactive, props.row.state=='active'? active : '']">
                      {{props.row.state}}
                  </div>
                </div>
              </template>
              <template slot="action" slot-scope="props">
                <div>
                  <b-btn variant="outline-info" class="btn-xs" @click.prevent="openNewTab(props.row)" v-if="switchState(props.row)" :ref="props.row.project_id" v-b-tooltip.hover :title="$t('common.ICON_TIP_LOG')">
                      <em :ref="'eye-'+ props.row.project_id" class="fa-x fas fa-eye"></em>
                  </b-btn>

                  <b-btn variant="outline-info" class="btn-xs" @click.prevent="openNewTab(props.row)" v-else :ref="props.row.project_id" disabled v-b-tooltip.hover :title="$t('common.ICON_TIP_LOG')">
                      <em :ref="'eye-'+ props.row.project_id" class="fa-x fas fa-eye-slash"></em>
                  </b-btn>

                  <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'&&props.row['token-role']=='project-owner'" @click.prevent="loadmembers(props.row)" v-b-modal.memberdata v-b-tooltip.hover :title="$t('common.ICON_TIP_MEMBER_MGM')">
                      <i class="fas fa-users"></i>
                  </b-btn>
                  <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'&&props.row['token-role']=='project-owner'" @click.prevent="loadproject(props.row.name)" v-b-modal.editproject v-b-tooltip.hover :title="$t('common.ICON_TIP_EDIT')">
                      <i class="fas fa-edit"></i>
                  </b-btn>

                  <b-btn variant="outline-danger" class="btn-xs" v-if="props.row.state=='active'&& cluster_role=='cluster-owner'" @click.prevent="checkCancel(props.row)" v-b-tooltip.hover :title="$t('common.ICON_TIP_DEL')">
                      <i class="fas fa-trash-alt"></i>
                  </b-btn>
                </div>
              </template>
          </v-client-table>
      </div>
    </template>
    <!-- 專案表單結束 -->

    <b-modal class="modal fade" id="memberdata" size="xl" :title="$t('service.PRJ_DIAL_PRJMM_TITLE')" ref="modal" :hide-footer="true"> 
      <EditProjectMember :strProjectName="strProjectName" @loadTable="loadTable()" @stopLoadTable="stopLoadTable()" /> 
    </b-modal>

    <!-- 編輯專案開始 -->
    <b-modal class="modal fade" id="editproject" :title="$t('service.PRJ_DIAL_UPDATE_CONFIG')" ref="modal" size="xl">
      <form ref="form" @submit.stop.prevent="handleSubmit">
          <div class="form-group row m-0 mt-3">
              <label class="mx-3" for="projectName">{{$t("service.PRJ_LST_PRJ_NAME")}}:</label>{{projectName}}

              <label class="mx-3" for="textarea-default">{{$t("service.PRJ_LST_DESC")}}:</label>
              <b-form-input class="col-md-5" id="textarea-default" v-model="projectText" :placeholder="$t('service.PRJ_DIAL_NPRJ_HINT_IPT_PRJ_DESC')" required></b-form-input>
          </div>
          <div class="form-group row m-2 mt-3">
              <label class="switch">
                  <input type="checkbox" v-model="open" name="switchPort"/>
                  <span class="ml-2"></span>
                  <h6 class="ml-2 mt-1">{{$t('service.PRJ_DIAL_NPRJ_LBL_ENABLE_PRJ_RSRC_LIMIT')}}</h6>
                  <small class="ml-2" style="color:red;">{{$t('service.PRJ_DIAL_NPRJ_HINT_ENABLE_PRJ_RSRC_LIMIT')}}</small>
                </label>
          </div>
          <div v-if="open" class="m-2" >
              <table id="tableResource" class="table">
                  <thead class="thead-dark">
                      <tr>
                          <th scope="col">{{$t('service.SVCM_DIAL_NEWNAMESPACE_TABLE_RSRC_TYPE')}}</th>
                          <th scope="col">{{$t('service.SVCM_DIAL_NEWNAMESPACE_TABLE_PRJ_RSRC_LIMIT')}}</th>
                          <th scope="col">{{$t('service.SVCM_DIAL_NEWNAMESPACE_TABLE_NAMESPACE_RSRC_LIMIT')}}</th>
                          <th scope="col">{{$t('service.SVCM_DIAL_NEWNAMESPACE_TABLE_CONTAINER_RSRC_LIMIT')}}</th>
                      </tr>
                  </thead>
                  <tbody>
                      <tr>
                          <th scope="row">CPU Limit</th>
                          <td>
                              <input v-model="resourceQuota.limit.limitsCpu" type="number" :class="{'form-control':true}" required min="0"/>
                              <label class="mx-2">m</label>
                          </td>
                          <td>
                              <input v-model="namespaceDefaultResourceQuota.limit.limitsCpu" type="number" :class="{'form-control':true}" required min="0"/>
                              <label class="mx-2">m</label>
                          </td>
                          <td>
                              <input v-model="containerDefaultResourceLimit.limitsCpu" type="number" :class="{'form-control':true}" required @change="runrun()" min="0"/>
                              <label class="mx-2">m</label>
                          </td>
                      </tr>
                      <tr>
                          <th scope="row">CPU Reservation</th>
                          <td>
                              <input v-model="resourceQuota.limit.requestsCpu" type="number" :class="{'form-control':true}" required min="0"/>
                              <label class="mx-2">m</label>
                          </td>
                          <td>
                              <input v-model="namespaceDefaultResourceQuota.limit.requestsCpu" type="number" :class="{'form-control':true}" required min="0"/>
                              <label class="mx-2">m</label>
                          </td>
                          <td>
                              <input v-model="containerDefaultResourceLimit.requestsCpu" type="number" :class="{'form-control':true}" required @change="runrun()" min="0"/>
                              <label class="mx-2">m</label>
                          </td>
                      </tr>
                      <tr>
                          <th scope="row">Memory Limit</th>
                          <td>
                              <input v-model="resourceQuota.limit.limitsMemory" type="number" :class="{'form-control':true}" required min="0"/>
                              <label class="mx-2">Mi</label>
                          </td>
                          <td>
                              <input v-model="namespaceDefaultResourceQuota.limit.limitsMemory" type="number" :class="{'form-control':true}" required min="0"/>
                              <label class="mx-2">Mi</label>
                          </td>
                          <td>
                              <input v-model="containerDefaultResourceLimit.limitsMemory" type="number" :class="{'form-control':true}" required @change="runrun()" min="0"/>
                              <label class="mx-2">Mi</label>
                          </td>
                      </tr>
                      <tr>
                          <th scope="row">Memory Reservation</th>
                          <td>
                              <input v-model="resourceQuota.limit.requestsMemory" type="number" :class="{'form-control':true}" required min="0"/>
                              <label class="mx-2">Mi</label>
                          </td>
                          <td>
                              <input v-model="namespaceDefaultResourceQuota.limit.requestsMemory" type="number" :class="{'form-control':true}" required min="0"/>
                              <label class="mx-2">Mi</label>
                          </td>
                          <td>
                              <input v-model="containerDefaultResourceLimit.requestsMemory" type="number" :class="{'form-control':true}" required @change="runrun()" min="0"/>
                              <label class="mx-2">Mi</label>
                          </td>
                      </tr>
                  </tbody>
              </table>
          </div>
      </form>      
      <template v-slot:modal-footer="{ok, cancel}">
          <b-button size="sm" variant="success" @click="okk()">{{$t("service.PRJ_DIAL_NPRJ_BTN_SUBMIT")}}</b-button>
          <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.PRJ_DIAL_NPRJ_BTN_CANCEL")}}</b-button>
      </template>
    </b-modal>
    <!-- 編輯專案結束 -->

  </div>
</template>

<script>
import Vue from "vue";
import { ClientTable, Event } from "vue-tables-2";
import swal from "sweetalert2";
import EditProjectMember from "../project/EditProjectMember";


Vue.use(ClientTable);

export default {
  props: {
    strProject: '',
    parentName: ''
  },
  components: {
    ClientTable,
    EditProjectMember
  },
  data() {
    return {
      pjname:"",
      role: false,
      cluster_role:'',
      unactive:'unactive',
      active:'active',
      timer1:'',
      open: false,
      projectName: "",
      projectNameState: null,
      projectText: "",
      projectTextState: null,
      submittedNames: [],
      selectedIndex: "",
      selectProject: "",
      alldata: [],
      groups: [],
      selectedGroup: "",
      namespace: [],
      selectedNamespace: "",
      strProjectName: "",


      resourceQuota: {
        limit: {
          limitsCpu: 1024,
          requestsCpu: 1024,
          limitsMemory: 2048,
          requestsMemory: 2048
        }
      },

      namespaceDefaultResourceQuota: {
        limit: {
          limitsCpu: 512,
          requestsCpu: 512,
          limitsMemory: 1024,
          requestsMemory: 1024
        }
      },

      containerDefaultResourceLimit: {
        limitsCpu: 200,
        requestsCpu: 200,
        limitsMemory: 256,
        requestsMemory: 256
      },



      tableData: [],
      // columns: ["name", "state", "description", "createdTS", "action"],
      columns: ["name", "state", "description", "createdTS"],
      options: {
        sortable: ["name", "state", "description", "createdTS"],
        filterable: false,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder: this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count: this.$t('common.LBL_TOTAL') + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults: this.$t('common.LBL_NO_RECORD')
        },
        headings: {
          name: () => this.$t("service.PRJ_LST_PRJ_NAME"),
          state: () => this.$t("service.PRJ_LST_STATUS"),
          description: () => this.$t("service.PRJ_LST_DESC"),
          createdTS: () => this.$t("service.PRJ_LST_CRTE_TIME"),
          action: () => this.$t("service.PRJ_LST_OPT")
        },
        columnsClasses:{
          state: "state",
        },
        pagination: { chunk: 5 },
        sortIcon: {
          is: "fa-sort",
          base: "fas",
          up: "fa-sort-up",
          down: "fa-sort-down"
        },
        dateColumns:['createdTS'],
        dateFormat: 'YYYY-MM-DD HH:mm:ss',
        orderBy:{
          column:"createdTS",
          ascending:false
        },  
      },
      isNew: false,
      isLoading: false
    };
  },
  mounted(){
    this.cluster_role = localStorage.getItem('cluster-role')
    if(this.parentName === "Service") {
      this.columns = ["name", "state", "description", "createdTS", "action"];
    }else{
      this.columns = ["name", "state", "description", "createdTS"];
    }
  },
  created() {
    this.isLoading = true;
    backendapi
      .get("/project")
      .then(response => {
        // this.isLoading = false;
        this.tableData = response.data.map((item, index) => {
          // console.log("project id", item);
          item["project_id"] = item.id;
          item["id"] = index;
          item['createdTS'] = moment(item.createdTS);

          // TODO: Initial project logging related fields
          item['discovery_url'] = "";
          item['index_prefix'] = "";
          item['pattern_id'] = "";
          item['pattern_title'] = "";
          item['projectLogging_id'] = "";
          item['switch_status'] = false;
          return item;
        });
        // TODO: Mapping ProjectLogging Status
        this.loadProjectLogging();
      })
      .catch(error => {
        console.log(error);
        this.isLoading = false;
      });

    // backendapi
    //   .get("/user")
    //   .then(response => {
    //     this.tableData = response.data.user.map((item, index) => {
    //       item["id"] = index;
    //       return item;
    //     });
    //   })
    // this.timer1 = setInterval(()=> {this.reloadd()}, 3000);
  },
  methods: {


//更新專案//
    loadproject(name) {
      this.strProjectName = name
      backendapi.get(`/project/${this.strProjectName}`)
        .then(response => {
          console.log(response.data)
          this.projectName = response.data.name
          this.projectText = response.data.description
          if(response.data.containerDefaultResourceLimit !== null && response.data.namespaceDefaultResourceQuota !== null && response.data.resourceQuota !== null){
            this.open = true
            this.resourceQuota.limit.limitsCpu = parseInt(response.data.resourceQuota.limit.limitsCpu.replace("m",""))
            this.resourceQuota.limit.requestsCpu = parseInt(response.data.resourceQuota.limit.requestsCpu.replace("m",""))
            this.resourceQuota.limit.limitsMemory = parseInt(response.data.resourceQuota.limit.limitsMemory.replace("Mi",""))
            this.resourceQuota.limit.requestsMemory = parseInt(response.data.resourceQuota.limit.requestsMemory.replace("Mi",""))
        
            this.namespaceDefaultResourceQuota.limit.limitsCpu = parseInt(response.data.namespaceDefaultResourceQuota.limit.limitsCpu.replace("m",""))
            this.namespaceDefaultResourceQuota.limit.requestsCpu = parseInt(response.data.namespaceDefaultResourceQuota.limit.requestsCpu.replace("m",""))
            this.namespaceDefaultResourceQuota.limit.limitsMemory = parseInt(response.data.namespaceDefaultResourceQuota.limit.limitsMemory.replace("Mi",""))
            this.namespaceDefaultResourceQuota.limit.requestsMemory = parseInt(response.data.namespaceDefaultResourceQuota.limit.requestsMemory.replace("Mi",""))
            
            this.containerDefaultResourceLimit.limitsCpu = parseInt(response.data.containerDefaultResourceLimit.limitsCpu.replace("m",""))
            this.containerDefaultResourceLimit.requestsCpu = parseInt(response.data.containerDefaultResourceLimit.requestsCpu.replace("m",""))
            this.containerDefaultResourceLimit.limitsMemory = parseInt(response.data.containerDefaultResourceLimit.limitsMemory.replace("Mi",""))
            this.containerDefaultResourceLimit.requestsMemory = parseInt(response.data.containerDefaultResourceLimit.requestsMemory.replace("Mi",""))
          }else{
            this.open = false
          }
        }).catch(error => {
          alert(error.message);
      });
    },

    okk(){
      if (this.open == false) {
        this.resourceQuota = null;
        this.namespaceDefaultResourceQuota = null;
        this.containerDefaultResourceLimit = null;
      }
      if (this.open == true) {
        this.resourceQuota.limit.limitsCpu =
          this.resourceQuota.limit.limitsCpu.toString() + "m";
        this.namespaceDefaultResourceQuota.limit.limitsCpu =
          this.namespaceDefaultResourceQuota.limit.limitsCpu.toString() + "m";
        this.containerDefaultResourceLimit.limitsCpu =
          this.containerDefaultResourceLimit.limitsCpu.toString() + "m";
        this.resourceQuota.limit.requestsCpu =
          this.resourceQuota.limit.requestsCpu.toString() + "m";
        this.namespaceDefaultResourceQuota.limit.requestsCpu =
          this.namespaceDefaultResourceQuota.limit.requestsCpu.toString() + "m";
        this.containerDefaultResourceLimit.requestsCpu =
          this.containerDefaultResourceLimit.requestsCpu.toString() + "m";
        this.resourceQuota.limit.limitsMemory =
          this.resourceQuota.limit.limitsMemory.toString() + "Mi";
        this.namespaceDefaultResourceQuota.limit.limitsMemory =
          this.namespaceDefaultResourceQuota.limit.limitsMemory.toString() +
          "Mi";
        this.containerDefaultResourceLimit.limitsMemory =
          this.containerDefaultResourceLimit.limitsMemory.toString() + "Mi";
        this.resourceQuota.limit.requestsMemory =
          this.resourceQuota.limit.requestsMemory.toString() + "Mi";
        this.namespaceDefaultResourceQuota.limit.requestsMemory =
          this.namespaceDefaultResourceQuota.limit.requestsMemory.toString() +
          "Mi";
        this.containerDefaultResourceLimit.requestsMemory =
          this.containerDefaultResourceLimit.requestsMemory.toString() + "Mi";
      }

      this.go2();
    },

    go2(){
      backendapi
        .put(`/project/${this.strProjectName}`, {
          name: this.projectName,
          description: this.projectText,
          resourceQuota: this.resourceQuota,
          namespaceDefaultResourceQuota: this.namespaceDefaultResourceQuota,
          containerDefaultResourceLimit: this.containerDefaultResourceLimit
        })
        .then(response => {
          this.loadTable();
        })
        .catch(error => {
          alert(error.message);
          this.isLoading = false;
        });
        this.$nextTick(() => {
          this.$refs.modal.hide();
      });
    },
//更新專案//

    loadmembers(row) {
      this.strProjectName = row.name;
    },

    loadTable() {
      this.$nextTick(() => {
        this.$refs.modal.hide();
      });
      this.timer1 = setInterval(()=> {this.reloadd()}, 5000);
    },

    stopLoadTable(){
      clearInterval(this.timer1);
    },

    output(name){
      this.pjname = name
      this.$emit('getPjname', this.pjname)
    },
    reloadd(){
      backendapi.get("/project")
        .then(response => {
          var projects = response.data.map((item, index) => {
            item["project_id"] = item.id;
            item["id"] = index;
            item['createdTS'] = moment(item.createdTS);
            
            // TODO: Initial project logging related fields
            item['discovery_url'] = "";
            item['index_prefix'] = "";
            item['pattern_id'] = "";
            item['pattern_title'] = "";
            item['projectLogging_id'] = "";
            item['switch_status'] = false;
            return item;
          });
          // TODO: Mapping ProjectLogging Status
          this.loadProjectLogging(projects);
        })
    },
    loadProjectLogging() {
        // TODO: Mapping ProjectLogging Status
        backendapi
          .get("/kibana/get_project_logging_info")
          .then(res => {
            var projectLoggings = res.data.data;
            for (var i = 0; i < projectLoggings.length; i++) {
              var target = this.tableData.find(function(item, index, array) {
                return item.project_id == projectLoggings[i].project_id;
              });

              if(target != null){
                // console.log("target", target);
                target.discovery_url = projectLoggings[i].discovery_url;
                target.index_prefix = projectLoggings[i].index_prefix;
                target.pattern_id = projectLoggings[i].pattern_id;
                target.pattern_title = projectLoggings[i].pattern_title;
                target.projectLogging_id = projectLoggings[i].projectLogging_id;
                target.switch_status = projectLoggings[i].switch_status;
              }
            }
          })
          .catch(err => {
            console.error("get_project_logging_info error:", err);     
          })
          .finally(() => {
          });    
    },
    loadmembers(row) {
      this.strProjectName = row.name;
    },

    remove(row) {
      this.isLoading = true;
      this.tableData.filter(item => row.id !== item.id);
      this.selectProject = this.tableData[row.id].name;
      backendapi
        .delete(`/project/${this.selectProject}`)
        .then(response => {
          this.loadTable();
          this.isLoading = false;
        })
        .catch(error => {
          console.log(error);
        });
    },

    checkCancel(row) {
      swal({
        title: this.$t('service.PRJ_DIAL_DELPRJ_MSG'),
        text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#DD6B55",
        confirmButtonText: this.$t('common.BTN_CONFIRM'),
        cancelButtonText: this.$t('common.BTN_CANCEL'),
        heightAuto: false
      }).then(result => {
        if (result.value) {
          this.remove(row);
          swal({
            title: this.$t('common.MSG_DEL_SUCCESS'),
            confirmButtonText: this.$t('common.BTN_OK'),
            type: "success",
            heightAuto: false
          });
        }
      });
    },

    switchToggle(projectinfo, event) {
      this.isLoading = true;
      console.log(projectinfo);
      if (event.target.checked) {
        backendapi
          .get("/elasticsearch_endpoint")
          .then(res => {
            // console.log("projectinfo--->", projectinfo);
            // TODO: Create ES Logging
            backendapi
              .post("/projectloggings", {
                projectid: projectinfo.project_id,
                projectname: projectinfo.name,
                endpoint: res.data.endpoint
              })
              .then(res => {
                projectinfo["projectLogging_id"] = res.data.data.id;
                // console.log('after setting project logging', projectinfo);

                // TODO: Create Kinaba Index
                backendapi
                  .post("/kibana/index_pattern", {
                    indexPattern:
                      res.data.data.elasticsearchConfig.indexPrefix + "-*"
                  })
                  .then(res => {
                    console.log("Create Kibana Index", res.data);
                    projectinfo["pattern_id"] = res.data.data.id;
                    projectinfo["pattern_title"] =
                      res.data.data.attributes.title;
                    projectinfo["discovery_url"] = res.data.discovery_url;
                    projectinfo["switch_status"] = true;

                    // TODO: Enable Viewer
                    console.log("ref opennnn", this.$refs[`eye-${projectinfo.project_id}`]);
                    var viewerBtn = this.$refs[projectinfo.project_id];
                    var icon = this.$refs[`eye-${projectinfo.project_id}`];
                    icon.classList.remove("fa-eye-slash");
                    icon.classList.add("fa-eye");
                    viewerBtn["disabled"] = false;

                    this.isLoading = false;
                  })
                  .catch(error => {
                    console.error(error);
                    this.isLoading = false;
                  });


              })
              .catch(error => {
                console.error(error);
                this.isLoading = false;
              });
          })
          .catch(err => {
            console.error("error", err);
            this.logSwitch = false;
          });
      } else {
        // TODO: Delete ES Logging
        this.isLoading = true;
        backendapi
          .delete(`/projectloggings/${projectinfo.projectLogging_id}`)
          .then(res => {
            console.log("Delete rancher project setting success!");
            backendapi
              .delete(`/kibana/index_pattern/${projectinfo.pattern_id}`)
              .then(res => {
                console.log("Delete kibana index pattern success!");
                console.log("after delete:", projectinfo);
                projectinfo["discovery_url"] = "";
                projectinfo["switch_status"] = false;

                // TODO: Disable viewer button
                console.log("ref", this.$refs[projectinfo.project_id].classList);
                var viewerBtn = this.$refs[projectinfo.project_id];
                var icon = this.$refs[`eye-${projectinfo.project_id}`];
                icon.classList.remove("fa-eye");
                icon.classList.add("fa-eye-slash");
                viewerBtn["disabled"] = true;

                this.isLoading = false;

              })
              .catch(err => {
                console.error(err);
                this.isLoading = false;
              });
          })
          .catch(err => {
            console.error(err);
            this.isLoading = false;
          });
      }
    },
    switchState(projectinfo) {
      if (projectinfo.switch_status == null) {
        return false;
      } else {
        return projectinfo.switch_status;
      }
    },
    openNewTab(projectinfo) {
      console.log(projectinfo);
      console.log("open " + projectinfo.discovery_url);
      window.open(projectinfo.discovery_url, "_blank");
    },
    viewState(projectinfo) {
      if (projectinfo.discovery_url != "") {
        return false;
      } else {
        return true;
      }
    }
  },
  destroyed: function() {
    console.log('project timer destroyed');
    clearInterval(this.timer1);
  }
};
</script>


<style>
#tableResource.table input {
  max-width: 85%;
  display: inline;
}
#tableResource.table .thead-dark th {
  background-color: #3f51b5;
}

#projectTable.vuetable th#__state {
    width: 300px;
}
</style>



