<template>
  <div>


    <!-- Namespace表單開始 -->
    <v-client-table :data="tableDataNamespace" :columns="columns" :options="options">
      <template slot="name" slot-scope="props">
        <div>
          <div class="text-left">
            <a href="javascript:void(0)" @click.prevent="goNamespace(props.row.name)">{{props.row.name}}</a>
          </div>  
        </div>
      </template>
      <template slot="state" slot-scope="props">
        <div>
          <div class="p-2" :class="[unactive, props.row.state=='active'? active : '']">
            {{props.row.state}}
          </div>  
        </div>
      </template>
      <!-- <template slot="action" slot-scope="props">
        <div>
          <b-btn variant="outline-info" class="btn-xs" v-b-tooltip.hover :title="$t('common.ICON_TIP_TRACE')">
            <a :href="'http://kiali.10.62.164.150.xip.io/console/graph/namespaces/?edges=requestsPerSecond&graphType=versionedApp&namespaces='+ props.row.name + '&unusedNodes=true&injectServiceNodes=false&pi=15000&duration=60&layout=dagre'" target="blank" style="display:block;"><i class="fas fa-link"></i></a> 
          </b-btn>
          <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="editpop(props.row)" v-b-modal.editNamespace v-b-tooltip.hover :title="$t('common.ICON_TIP_EDIT')">
              <i class="fas fa-edit"></i>
          </b-btn>
          <b-btn variant="outline-danger" class="btn-xs" v-if="props.row.state!=='removing'" @click.prevent="checkCancel(props.row)" v-b-tooltip.hover :title="$t('common.ICON_TIP_DEL')">
            <i class="fas fa-trash-alt"></i>
          </b-btn>
        </div>
      </template> -->
    </v-client-table>
    <!-- Namespace表單結束 -->


  </div>
</template>

<script>
import Vue from "vue";
import { ClientTable, Event } from "vue-tables-2";
import swal from "sweetalert2";

Vue.use(ClientTable);

export default {
  props: ['strProject','tableDataNamespace'],
  components: {
    ClientTable,
  },
  data() {
    return {
      pjname:"",
      namespace:'',
      role: false,
      cluster_role:'',
      unactive:'unactive',
      active:'active',
      timer1:'',
      open: false,
      projectName: "",
      projectNameState: null,
      projectText: "",
      projectTextState: null,
      submittedNames: [],
      selectedIndex: "",
      selectProject: "",
      alldata: [],
      groups: [],
      selectedGroup: "",
      selectedNamespace: "",
      strProjectName: "",




      columns: ["name", "state", "description", "createdTS"],
      options: {
        sortable: ["name", "state", "description", "createdTS"],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder: this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count: this.$t('common.LBL_TOTAL')  + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults:this.$t('common.LBL_NO_RECORD')
        },
        headings: {
          name: () => this.$t('service.SVCM_LST_NAMESPACE_NAME'),
          state: () => this.$t('service.SVCM_LST_NAMESPACE_STATUS'),
          description: () => this.$t('service.SVCM_LST_NAMESPACE_DESC'),
          createdTS: () => this.$t('service.SVCM_LST_NAMESPACE_CREATE_TIME'),
          action: () => this.$t('service.SVCM_LST_NAMESPACE_OPT')
        },
        columnsClasses:{
          name:"name-table2",
          state: "state-table2",
          description:"description-table",
          action:"action-table2",
          createdTS: "date-table2",
          traceroute_link: "link2"
        },
        pagination: { chunk: 5 },
        sortIcon: {
          is: "fa-sort",
          base: "fas",
          up: "fa-sort-up",
          down: "fa-sort-down"
        },
        dateColumns:['createdTS'],
        dateFormat: 'YYYY-MM-DD HH:mm:ss',
        orderBy:{
          column:"createdTS",
          ascending:false
        }, 
      },
      isNew: false,
      isLoading: false
    };
  },
  mounted(){

  },
  created() {

  },
  methods: {
    goNamespace(name){
      this.namespace = name
      this.$emit('getNamespace', this.namespace)
    },
  }

};
</script>


<style>
#tableResource.table input {
  max-width: 85%;
  display: inline;
}
#tableResource.table .thead-dark th {
  background-color: #3f51b5;
}

#projectTable.vuetable th#__state {
    width: 300px;
}

.description-table{
  width: 40%;
}
</style>



