<template>
<div>
  <div id="member" class="form-group row justify-content-between">
      <b-tabs class="col-5">
        <b-tab :title="$t('service.PRJ_DIAL_PRJMM_MEMBER_LST')">
          <div>
            <draggable class="list-group" :list="list1" group="people"  v-bind="dragOptions" @start="drag = true" @end="drag = false">
              <transition-group type="transition">
                <li class="list-group-item" v-for="(element, index) in list1" :key="element.name">
                  {{ element.name }}
                </li>
              </transition-group>
            </draggable>
          </div>
        </b-tab>
      </b-tabs>  

      <div class="col-1 d-flex flex-column align-items-top justify-content-center mt-5">
        <div class="mt-5 text-center">{{$t('service.PRJ_DIAL_PRJMM_TABLE_DRAG')}}</div>
        <i class="col-12 fas fa-exchange-alt" style="font-size:36px;"></i>
      </div> 
      
      <b-tabs class="col-6" align="right">
        <b-tab :title="$t('service.PRJ_DIAL_PRJMM_TABLE_ADMIN')">
          <draggable class="list-group" :list="list2" group="people" v-bind="dragOptions" @start="drag = true" @end="drag = false">
            <transition-group type="transition">
              <li class="list-group-item" v-for="(element, index) in list2" :key="element.name">
                {{ element.name }}
              </li>
            </transition-group>
          </draggable>
        </b-tab>

        <b-tab :title="$t('service.PRJ_DIAL_PRJMM_TABLE_MEMBER')">
          <draggable class="list-group" :list="list3" group="people" v-bind="dragOptions" @start="drag = true" @end="drag = false">
            <transition-group type="transition">
              <li class="list-group-item" v-for="(element, index) in list3" :key="element.name">
                {{ element.name }}
              </li>
            </transition-group>
          </draggable>
        </b-tab>

      </b-tabs>

  </div>
<div class="d-flex justify-content-end">
  <b-button size="sm" variant="success" @click="ok()">{{$t("service.PRJ_DIAL_NPRJ_BTN_SUBMIT")}}</b-button>
</div>
</div>
</template> 

<script>
import Vue from 'vue';
import draggable from 'vuedraggable'

export default {
  name: "two-lists",
  display: "Transitions",
  order: 1,
  props:["strProjectName"],
  components: {
    draggable
  },
  data(){
    return{
      drag: false,
      list1: [],
      list2: [],
      list3: [],
      list4: [],
      submitList: []
    }
  },
  computed: {
    dragOptions() {
      return {
        animation: 500,
        group: "description",
        disabled: false,
        ghostClass: "ghost"
      };
    }
  },
  mounted(){
    this.$emit('stopLoadTable');
    this.isLoading = true;
    backendapi.get(`/project/${this.strProjectName}/member-v2?projectrole=project-owner`).then((response) => {
      this.list2 = response.data.member
      this.list2.map(item=>{
        this.list4.push(item.name)
      })      
      backendapi.get(`/project/${this.strProjectName}/member-v2?projectrole=project-member`).then((response) => {
        this.list3 = response.data.member
        this.list3.map(item=>{
          this.list4.push(item.name)
        })
        backendapi.get(`/user`).then((response) => {
          this.list1 = response.data.user.filter((word) => !this.list4.includes(word.name));
        })
        .catch(error => {
          alert(error.message)
          this.isLoading = false;
        });
        this.isLoading = false;
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    })
    .catch(error => {
      alert(error.message)
      this.isLoading = false;
    });
  },
  methods:{
    ok(){
      this.list2.map(item=>{
        const newPO ={
          "user-id": "",
          "project-role": ""
        }
        newPO['user-id'] = item['user-id']
        newPO['project-role'] = "project-owner"
        this.submitList.push(newPO)
      })
      this.list3.map(item=>{
        const newPM ={
          "user-id": "",
          "project-role": ""
        }
        newPM['user-id'] = item['user-id']
        newPM['project-role'] = "project-member"
        this.submitList.push(newPM)
      })
      backendapi.put(`/project/${this.strProjectName}/member-v2`,{
        member: this.submitList
      }
      ).then((response) => {
        this.$emit('loadTable');
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    }
  },
}
</script>
    
<style>
.button {
  margin-top: 35px;
}
.flip-list-move {
  transition: transform 0.5s;
}
.no-move {
  transition: transform 0s;
}
.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}
.list-group {
  min-height: 20px;
}
.list-group-item {
  cursor: move;
}
.list-group-item i {
  cursor: pointer;
}
.list-group .list-group-item{
  cursor: pointer;

}
.list-group .list-group-item:hover{
  cursor: pointer;
  color:#fff;
  background-color: #3f51b5
}
#member .nav-tabs>.nav-item>.nav-link {
  border: none;
}
#member .nav-tabs>.nav-item>.nav-link.active {
  background-color: #3f51b5;
}
#member .nav-tabs>.nav-item>.nav-link.active, #member .nav-tabs>.nav-item>.nav-link:hover, #member .nav-tabs>.nav-item>.nav-link.active:focus {
    color: #fff;
    background-color: #3f51b5;
}
</style>



