<template>
<div>

  <div v-if="role">

    <!-- 讀取畫面 -->
    <loading :active.sync="isLoading"></loading>

    <!-- 大標題 -->
    <div class="content-heading">{{$t("sidebar.ACNT_MEM")}}</div>

    <!-- 新建用戶開始 -->
    <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" v-b-modal.newuser>{{$t("service.ACNT_DIAL_NACNT_TITLE")}}</b-btn><br><br>
    <b-modal class="modal fade" id="newuser" :title="$t('service.ACNT_DIAL_NACNT_TITLE')" ref="modal" :hide-footer="true">
      <NewUser @loadTable="loadTable()"/>
    </b-modal>       
    <!-- 新建用戶結束 -->

    <!-- 重設密碼開始 -->
    <b-modal class="modal fade" id="resetPassword" :title="$t('service.ACNT_DIAL_REST_PWD')" ref="modal" :hide-footer="true">
      <ResetPassword :userid="userid" @loadTable="loadTable()"/>
    </b-modal>       
    <!-- 重設密碼結束 -->

    <!-- 所屬專案開始 -->
    <b-modal class="modal fade" id="projectList" :title="$t('service.ACANT_DIAL_BELONGING_PRJ')" ref="modal" :hide-footer="true">
      <UserProject :userid="userid" @loadTable="loadTable()"/>
    </b-modal>   
    <!-- 所屬專案結束 -->

    <!-- 分類表單開始 -->
    <template>
      <div>
        <v-client-table class="dark" :data="tableData" :columns="columns" :options="options">
          <template slot="name" slot-scope="props">
            <div>
              <a href="javascript:(0)" @click.prevent="getUserid(props.row)" v-b-modal.projectList>{{props.row.name}}</a>
            </div>
          </template>
          <template slot="action" slot-scope="props">
            <div>
              <b-btn variant="outline-primary" class="btn-xs" v-if="props.row['cluster-role']=='cluster-member'" @click.prevent="getUserid(props.row)" v-b-modal.resetPassword v-b-tooltip.hover :title="$t('common.ICON_TIP_REST_PWD')"><i class="fas fa-key"></i></b-btn>
              <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)" v-b-tooltip.hover :title="$t('common.ICON_TIP_DEL')"><i class="fas fa-trash-alt"></i></b-btn>
            </div>
          </template>
        </v-client-table>
      </div>
    </template>
    <!-- 分類表單結束 -->

  </div>

</div>
</template>

<script>
import Vue from 'vue';
import NewUser from "./NewUser";
import ResetPassword from "./ResetPassword";
import UserProject from "./UserProject";
import { ClientTable,  Event } from 'vue-tables-2';
import swal from 'sweetalert2'
Vue.use(ClientTable);

export default {
  components: {
    ClientTable,
    NewUser,
    ResetPassword,
    UserProject
  },
  data(){
    return{
      role: false,
      userid:'',
      tableData: [],
      columns: ['name', 'created','cluster-role','action'],
      options: {
        // rowClassCallback: function(row) { return 'bg-primary'},
        sortable: ['name', 'created', 'cluster-role'],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder:this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count:this.$t('common.LBL_TOTAL') + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults:this.$t('common.LBL_NO_RECORD'),
        },
        headings: {
            name: () => this.$t('service.ACNT_LST_ACNT'),
            created: () => this.$t('service.ACNT_LST_CRTE_TIME'),
            'cluster-role': () => this.$t('service.ACNT_LST_ROLE'),
            action: () => this.$t('service.ACNT_LST_OPT'),
        },
        pagination: { chunk: 5 },
        sortIcon: {
            is: 'fa-sort',
            base: 'fas',
            up: 'fa-sort-up',
            down: 'fa-sort-down'
        },
        dateColumns:['created'],
        dateFormat: 'YYYY-MM-DD HH:mm:ss',
        orderBy:{
          column:"created",
          ascending:false
        },
      },
      isNew: false,
      isLoading: false,
    };
  },
  created(){
    if(localStorage.getItem('cluster-role') == 'cluster-owner'){
      this.role = true
    }
    this.isLoading = true;
    backendapi.get("/user").then((response) => {
      this.tableData = response.data.user.map((item, index) => {
        item['id'] = index;
        item['created'] = moment(item.created);
        return item
      });
      this.isLoading = false;
    })
    .catch(error => {
      alert(error.message);
      this.isLoading = false;
    });
  },
  methods:{
    // 重設密碼區開始 //
    getUserid(row){
      this.userid = row['user-id']
    },
    // 重新讀取表單 //
    loadTable(){
      this.$nextTick(() => {
        this.$refs.modal.hide();
      });  
      setTimeout(() => {
        backendapi.get("/user").then((response) => {
          this.tableData = response.data.user.map((item, index) => {
            item['id'] = index;
            item['created'] = moment(item.created);
            return item
          });
        })
        .catch(error => {
          alert(error.message);
        });
      },3000);
    },
    // 分類表單刪除 //
    remove (row) {
      this.isLoading = true;
      this.tableData.filter(item => (row.id !== item.id));
      this.userid = this.tableData[row.id]['user-id']
      backendapi.delete(`/user/${this.userid}`).then((response) => {
        this.loadTable()
        this.isLoading = false;
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });   
    },
    // 刪除步驟 //
    checkCancel(row) { 
      swal({
          title: this.$t('service.ACNT_DIAL_DELACNT_MSG'),
          text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: this.$t('common.BTN_CONFIRM'),
          cancelButtonText:this.$t('common.BTN_CANCEL'),
          heightAuto: false
      }).then((result) => {
        if(result.value){
        this.remove(row);
          swal({
            title: this.$t('common.MSG_DEL_SUCCESS'),
            confirmButtonText: this.$t('common.BTN_OK'),
            type: "success",
            heightAuto: false
          });
        } 
      });
    },
  },
}
</script>
    




