import axios from 'axios'
import Configuration from '../../ConfigProvider'
export const myLoginRoutine = user => new Promise((resolve, reject) => {

  // const reverseProxyUrl = Configuration.value('backendHost') || "http://10.62.164.169:8686";
  const reverseProxyUrl = Configuration.value('backendHost') || "https://enigmatic-river-95619.herokuapp.com";
  // const baseURL = "https://192.168.56.104/paas";
  const https = require('https');
  const agent = new https.Agent({
    rejectUnauthorized: false
  });

  axios
    .post(`https://enigmatic-river-95619.herokuapp.com/login`, {
      "group-id": user.groupId,
      name: user.name,
      password: user.password
    }, {
      httpsAgent: agent
    })
    .then(response => {

      localStorage.setItem("cluster-role", response.data["cluster-role"]);
      localStorage.setItem("clusterid", response.data["clusterid"]);
      localStorage.setItem("createdTS", response.data["createdTS"]);
      localStorage.setItem("group-id", response.data["group-id"]);
      localStorage.setItem("name", response.data["name"]);
      localStorage.setItem("token", response.data["token"]);
      localStorage.setItem("ttl", response.data["ttl"]);
      // TODO: Inital Global axios
      const backendapi = axios.create({
        "Content-Type": "application/json",
        baseURL: reverseProxyUrl,
        httpsAgent: new https.Agent({
          rejectUnauthorized: false
        }),
        headers: {
          clusterid: response.data["clusterid"],
          Authorization: "bearer " + response.data["token"]
        },
      });
      window.backendapi = backendapi;

      resolve(response)
    })
    .catch(err => {
      // console.log("Login Routine Error", err)
      alert("認證失敗!請重新輸入。", err.message)
      localStorage.removeItem('token') // if the request fails, remove any possible user token if possible
      reject(err)
    })
})



