<template>
<div>

  <!-- 讀取畫面 -->
  <loading :active.sync="isLoading"></loading>

  <div class="content-heading">{{$t("sidebar.CONFIG_MAP")}}</div>

    <!-- select選項開始 -->
    <b-form class="was-validated">
      <div class="form-group row">
        <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_PRJ")}}:</label>
        <div class="col-md-4">
          <select id="projectlist" class="custom-select custom-select-md mb-3" @change="selectedproject()" v-model="strProject">
            <option value disabled selected>{{$t("option.PROJECT")}}</option>
            <option v-for="(item) in projectlist" :value="item.name" :key="item.name">{{item.name}}</option>
          </select>
        </div>
        <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_NAMESPACE")}}:</label>
        <div class="col-md-4">
          <select id="namespacelist" class="custom-select custom-select-md mb-3" @change="selectednamespace()" :disabled="disabled" v-model="strNamespace">
            <option id="myselected" value disabled selected>{{$t("option.NAMESPACE")}}</option>
            <option v-for="item in namespacelist" :value="item.name" :key="item.name">{{item.name}}</option>
          </select>
        </div>
      </div>
    </b-form>
    <!-- select選項結束 -->

  <!-- 新建配置開始 -->
  <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" v-b-modal.createConfigure v-show="showConfig" @show="resetModal" @hidden="resetModal">{{$t("service.CFGMAP_BTN_NEW_CFGMAP")}}</b-btn>
  <br>
  <br>
  <b-modal class="modal fade configure" id="createConfigure" size="xl" :title="$t('service.CFGMAP_DIAL_NEWCFGMAP_TITLE')" ref="modal"  @show="resetModal" @hidden="resetModal" @ok="handleOk">
    <form ref="form" @submit="handleSubmit" @change="handleChange"  @invalid.capture.prevent="handleInvalid">
      <b-card class="mb-3 border">
        <fieldset>
          <dl class="row">
            <dt class="col-sm-3">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_LBL_PRJ_NAME")}}:</dt>
            <dd class="col-sm-9">{{strProject}}</dd>
          </dl>
          <dl class="row">       
            <dt class="col-sm-3">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_LBL_NAMESPACE")}}:</dt>
            <dd class="col-sm-9">{{strNamespace}}</dd>
          </dl>
        </fieldset>           
        <fieldset>
            <div class="form-group row">
                <label class="col-2 col-form-label">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_CFGMAP_NAME")}}</label>
                <div class="col-10">
                    <input v-model="newConfigure.name" :class="{'form-control':true}" type="text" name="name" @blur="checkDuplicate" required/>
                    <p v-if="fieldErrors.name" class="help is-danger">{{fieldErrors.name}}</p>
                    <p v-if="duplicate" class="help is-danger">{{$t('service.CFGMAP_DIAL_NEWCFGMAP_HINT_FILENAME_EXIST')}}</p>
                </div>
            </div>
        </fieldset>
        <fieldset>
            <div class="form-group row">
                <label class="col-2 col-form-label">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_CFGMAP_DESC")}}</label>
                <div class="col-10">
                    <input v-model="newConfigure.description" :class="{'form-control':true}" type="text"/>
                </div>
            </div>
        </fieldset>        
        <fieldset>
            <div class="form-group row align-items-end" v-for="(item, index) in newdata">
              <div class="col-5">
                <div>
                  <label class="col-form-label">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_IPTLBL_KEY")}}</label>
                  <input v-model="item.key" :class="{'form-control':true}" type="text" name="configKey" required/>
                </div>
                <div>
                  <p v-if="fieldErrors.configKey" class="help is-danger">{{fieldErrors.configKey}}</p>
                </div>
              </div>             
              <div class="col-1">
                <h4 class="text-center"> = </h4>
              </div>
              <div class="col-5">
                <div>
                 <label class="col-form-label">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_IPTLBL_VALUE")}}</label>
                  <textarea v-model="item.value" :class="{'form-control':true}" type="text" name="configValue" required style="height:35px;"></textarea>
                </div>
                <div>
                  <p v-if="fieldErrors.configValue" class="help is-danger">{{fieldErrors.configValue}}</p>
                </div>
              </div>
              <div class="col-1">
                <div class="col-1">
                  <b-btn variant="outline-danger" class="btn-xs" @click.prevent="removeRule(index)" style="margin-bottom: 5px;"  v-b-tooltip.hover :title="$t('common.ICON_TIP_DEL')"><i class="fas fa-trash-alt"></i></b-btn>
                </div>
              </div>
            </div>
          <br> 
          <div>
              
              
          </div> 
          <button class="btn btn-labeled btn-info" type="button" @click="addrule()">
            <span class="btn-label"><i class="fa fa-plus"></i></span>{{$t('service.CFGMAP_DIAL_NEWCFGMAP_BTN_ADD_VAR')}}
          </button>

        </fieldset>
      </b-card>    
    </form>
    <template v-slot:modal-footer="{ok, cancel}">
      <b-button size="sm" variant="success" @click="ok()">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_BTN_SUBMIT")}}</b-button>
      <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.RSRC_DIAL_DELPOOL_CANCEL")}}</b-button>
    </template>
  </b-modal>
  <!-- 新建配置結束 -->

  <!-- 專案表單開始 -->
  <ProjectTable :strProject="strProject" @getPjname="getPjname" v-show="showProject"/>
  <!-- 專案表單結束 -->

  <!-- 命名空間表單開始 -->
  <NamespaceTable :strProject="strProject" :tableDataNamespace="tableDataNamespace" @getNamespace="getNamespace" v-if="showNamespace"/>
  <!-- 命名空間表單結束 -->

  <!-- 分類表單開始 -->
  <v-client-table :data="tableData" :columns="columns" :options="options" v-show="showConfig">
    <template slot="action" slot-scope="props">
      <div>
        <b-btn variant="outline-info" class="btn-xs" @click.prevent="editpop(props.row)" v-b-modal.editConfigure v-b-tooltip.hover :title="$t('common.ICON_TIP_EDIT')"><i class="fas fa-edit"></i></b-btn>
        <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)"><i class="fas fa-trash-alt" :title="$t('common.ICON_TIP_DEL')"></i></b-btn>
      </div>
    </template>
  </v-client-table>
  <!-- 分類表單結束 -->

  <!-- 編輯配置開始 -->
  <b-modal class="modal fade configure" id="editConfigure" size="xl" :title="$t('service.CFGMAP_DIAL_UPD_CFGMAP_TITLE')" ref="modal"  @show="resetModal" @hidden="resetModal" @ok="handleOk">
    <form ref="form" @submit.stop.prevent="handleSubmit">
      <b-card class="mb-3 border">
        <fieldset>
          <dl class="row">
            <dt class="col-sm-3">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_LBL_PRJ_NAME")}}:</dt>
            <dd class="col-sm-9">{{strProject}}</dd>
          </dl>
          <dl class="row">       
            <dt class="col-sm-3">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_LBL_NAMESPACE")}}:</dt>
            <dd class="col-sm-9">{{strNamespace}}</dd>
          </dl>
          <dl class="row">       
            <dt class="col-sm-3">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_CFGMAP_NAME")}}:</dt>
            <dd class="col-sm-9">{{editConfigure.name}}</dd>
          </dl>
          <dl class="row">       
            <dt class="col-sm-3">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_CFGMAP_DESC")}}:</dt>
            <dd class="col-sm-9">{{editConfigure.description}}</dd>
          </dl>
        </fieldset>                  
        <fieldset>
            <div class="form-group row align-items-end" v-for="(item, index) in editnewdata">
              <div class="col-5">
                <div>
                    <label class="col-form-label">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_IPTLBL_KEY")}}</label>
                    <input v-model="item.key" :class="{'form-control':true}" type="text"/>
                </div>
              </div>
              <div class="col-1">
                <h4 class="text-center"> = </h4>
              </div>
              <div class="col-5">
                  <label class="col-form-label">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_IPTLBL_VALUE")}}</label>
                  <textarea v-model="item.value" :class="{'form-control':true}" type="text" style="height:35px;"></textarea>
              </div>
              <div class="col-1">
                <div class="col-1">
                  <b-btn variant="outline-danger" class="btn-xs" @click.prevent="editremoveRule(index)" style="margin-bottom: 5px;"><i class="fas fa-trash-alt"></i></b-btn>
                </div>
              </div>
            </div>
          <br>  
          <button class="btn btn-labeled btn-info" type="button" @click="editaddrule()">
            <span class="btn-label"><i class="fa fa-plus"></i></span>{{$t('service.CFGMAP_DIAL_NEWCFGMAP_BTN_ADD_VAR')}}
          </button>  
        </fieldset>
      </b-card>    
    </form>
    <template v-slot:modal-footer="{ok, cancel}">
      <b-button size="sm" variant="success" @click="editSubmit()">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_BTN_SUBMIT")}}</b-button>
      <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.RSRC_DIAL_DELPOOL_CANCEL")}}</b-button>
    </template>
  </b-modal>
  <!-- 編輯配置結束 -->

</div>
</template>  

<script>
import Vue from 'vue';
import { ClientTable,  Event } from 'vue-tables-2';
import swal from 'sweetalert2'
import ProjectTable from "./service/ProjectTable";
import NamespaceTable from "./service/NamespaceTable";

Vue.use(ClientTable);

export default {
  components: {
    ClientTable,
    ProjectTable,
    NamespaceTable
  },
  data(){
    return{
      duplicate: false,
      fieldErrors: {},
      
      timer1:'',
      name:'',
      nameState: null,
      text:'',
      textState: null,
      submittedNames: [],
      selectedIndex: '',
      selectedProject: '',
      projectlist:[],
      strProject:'',
      namespacelist:[],
      strNamespace:'',
      stringedit:'',
      alldata:[],
      groups:[],
      selectedGroup:'',
      namespace:[],
      selectedNamespace:'',
      tableData: [],
      tableDataNamespace :[],
      showProject:true,
      showNamespace: false,
      showConfig: false,
      disabled: true,
      newdata:[
        {
          key:"",
          value:""
        },             
      ],

      editnewdata:[           
      ],

      editConfigure:{
        data:{},
        name:'',
        description:''
      },

      newConfigure:{
        projectName:'',
        spacetName:'',
        name:'',
        description:'',
        data:{}
      },

      columns: ['name', 'description', 'createdTS', 'action'],
      options: {
        sortable: ['name', 'description', 'createdTS'],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder:this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count:this.$t('common.LBL_TOTAL') + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults:this.$t('common.LBL_NO_RECORD'),
        },
        headings: {
            name: () => this.$t("service.CFGMAP_LST_CFGMAP_NAME"),
            description: () => this.$t("service.CFGMAP_LST_DESC"),
            createdTS: () => this.$t("service.CFGMAP_LST_CRTE_TIME"),
            action: () => this.$t("service.CFGMAP_LST_OPT"),
        },
        pagination: { chunk: 5 },
        sortIcon: {
            is: 'fa-sort',
            base: 'fas',
            up: 'fa-sort-up',
            down: 'fa-sort-down'
        },
        dateColumns:['createdTS'],
        dateFormat: 'YYYY-MM-DD HH:mm:ss',
        orderBy:{
          column:"createdTS",
          ascending:false
        }, 
      },

      isNew: false,
      isLoading: false,
    };
  },
  validators: {
    editnewdata: function(value) {
      console.log('aaaaaaaaaaaa');
    }
  },
  created(){
    this.isLoading = true;
    backendapi.get("/project").then((response) => {
      this.isLoading = false;
      this.projectlist = response.data;
    })
    .catch(error => {
      alert(error.message);
      this.isLoading = false;
    });
  },
  methods:{

    getPjname(PJname){
      this.strProject = PJname
      this.isLoading = true;
      clearInterval(this.timer1);
      this.timer1 = setInterval(()=> {this.reloadd1()}, 3000);
      backendapi.get(`/project/${this.strProject}/namespace`)
        .then(response => {
          this.namespacelist = response.data;
          this.tableDataNamespace = response.data.map((item, index) => {
            item["id"] = index;
            item['createdTS'] = moment(item.createdTS);
            return item;
          });
          this.selected = true;
          this.showProject = false
          this.showNamespace = true
          this.isLoading = false
          document.getElementById("myselected").selected = true;
          if (this.namespacelist.length >= 1) {
            this.disabled = false;
          } else {
            this.disabled = true;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },

    getNamespace(namespace){
      this.strNamespace = namespace
      clearInterval(this.timer1);
      this.timer1 = setInterval(()=> {this.reloadd1()}, 3000);
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap`).then((response) => {
        this.showProject = false
        this.showNamespace = false
        this.showConfig = true
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });
    },

    checkDuplicate() {
      if (this.newConfigure.name){
        // console.log('aaaaaa', this.tableData);
        var target = this.tableData.find(item => item.name === this.newConfigure.name);

        if(target){
          this.duplicate = true;
        }else{
          this.duplicate = false;
        }

      }else{
        console.log('empty');
      }
    },
    reloadd1(){
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap`).then((response) => {
        this.tableData = response.data.map((item, index) => {
            item['id'] = index;
            item['createdTS'] = moment(item.createdTS);
            return item
        });
        this.isLoading = false;
      })
    },
    // 選取專案 //
    selectedproject() {
      var projectlist = document.getElementById("projectlist");
      this.strProject = projectlist.options[projectlist.selectedIndex].value;
      backendapi.get(`/project/${this.strProject}/namespace`).then(response => {
        this.namespacelist = response.data;
        this.tableDataNamespace = response.data.map((item, index) => {
          item["id"] = index;
          item['createdTS'] = moment(item.createdTS);
          return item;
        });
        this.showProject = false
        this.showNamespace = true
        this.showConfig = false
        this.isLoading = false
        document.getElementById("myselected").selected = true;
        if (this.namespacelist.length >= 1) {
          this.disabled = false;
        } else {
          this.disabled = true;
        }
      })
      .catch(error => {
        console.log(error);
      });
    },

    // 選取命名空間 //
    selectednamespace() {
      var namespacelist = document.getElementById("namespacelist");
      this.strNamespace = namespacelist.options[namespacelist.selectedIndex].value;
      clearInterval(this.timer1);
      this.timer1 = setInterval(()=> {this.reloadd1()}, 3000);
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap`).then((response) => {
        this.showProject = false
        this.showNamespace = false
        this.showConfig = true
        this.tableData = response.data.map((item, index) => {
            item['id'] = index;
            item['createdTS'] = moment(item.createdTS);
            return item
        });
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });
    },
    // 重新讀取表單 //
    loadTable(){
      setTimeout(() => {
        backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap`).then((response) => {
          this.showConfig = true;
          this.tableData = response.data.map((item, index) => {
              item['id'] = index;
              item['createdTS'] = moment(item.createdTS);
              return item
          });
        })
        .catch(error => {
          alert(error.message);
          this.isLoading = false;
        });
      },3000);
    },
    // 新增變量 //
    addrule(){
      const newObject = {
        key:"",
        value: ""
      }
      this.newdata.push(newObject);
    },
    // 刪除變量 //
    removeRule (index) {
      this.newdata.map((item, index) => {
        item['id'] = index
        return item
      });  
      if (this.newdata.length <= 1) {
        return
      }else{
        var outIndex = this.newdata.filter(item => (this.newdata[index].id === item.id));
        this.newdata.splice(outIndex[0].id, 1)
      }  
    },
    // 驗證 //
    checkFormValidity() {
      const vm = this;
      const valid = vm.$refs.form.checkValidity()  && !this.duplicate;
      this.nameState = valid? 'valid' : 'invalid'
      console.log(this.nameState);
      return valid
    },
    // 驗證 //
    handleOk(e) {
      const vm = this;
      e.preventDefault()
      vm.handleSubmit()
    },
    // 預設值 //
    resetModal() {
      this.newConfigure.name = ""
      this.newConfigure.description = ""
      this.newdata = [        
        {
          key:"",
          value:""
        },]
    },
    // 新建配置項送出 //
    handleSubmit() {
      console.log("handleSubmit");
      if (!this.checkFormValidity()) {
        return
      }
      var result = {};
      for (var i = 0; i < this.newdata.length; i++) {
        result[this.newdata[i].key] = this.newdata[i].value;
      }
      this.isLoading = true;
      backendapi.post(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap`,{
        name: this.newConfigure.name,
        description: this.newConfigure.description,
        data: result
      }).then((response) => {
        this.selectednamespace()
        this.isLoading = false;
        this.loadTable();
        this.$nextTick(() => {
          this.$refs.modal.hide()
        });
        this.resetModal()
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });
    },
    handleChange(evt) {
      console.log('handleChange :: ', evt.target.name);
      this.fieldErrors = {
        ...this.fieldErrors,
        [evt.target.name]: ""
      };
    },
    handleInvalid(evt) {
      console.log('handleInvalid :: ', evt.target.name);
      this.fieldErrors = {
        ...this.fieldErrors,
        [evt.target.name]: evt.target.validationMessage
      };
    },
    errorClass(field) {
      return this.fieldErrors[field] ? "{is-danger, {'form-control':true}}" : ""
    },

    // 表單編輯出現 //
    editpop (row) {
      this.stringedit = row.name
      this.editnewdata = []
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap/${this.stringedit}`).then((response) => {
        this.editConfigure.name = response.data.name
        this.editConfigure.description = response.data.description
        var result = Object.entries(response.data.data);
        for(var i = 0; i < result.length; i++) { 
          let obj = {
            key:'',
            value:''
          };
          obj['key'] = result[i][0]
          obj['value'] = result[i][1]
          this.editnewdata.push(obj)
        }          
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    },
    // 表單編輯 新增規則 //
    editaddrule(){
      const newObject = {
        key:"",
        value: ""
      }
      this.editnewdata.push(newObject);
    },
    // 表單編輯 刪除規則 //
    editremoveRule (index) {
      this.editnewdata.map((item, index) => {
        item['id'] = index
        return item
      });  
      if (this.editnewdata.length <= 1) {
        return
      }else{
        var outIndex = this.editnewdata.filter(item => (this.editnewdata[index].id === item.id));
        this.editnewdata.splice(outIndex[0].id, 1)
      }   
    },
    // 表單編輯 送出 //
    editSubmit(){
      var result = {};
      for (var i = 0; i < this.editnewdata.length; i++) {
        console.log([this.editnewdata[i].key]);
        if([this.editnewdata[i].key] != "") {
          result[this.editnewdata[i].key] = this.editnewdata[i].value;
        }
      }
      backendapi.put(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap/${this.stringedit}`,{
        data: result
      }).then((response) => {
        this.loadTable();
        this.$nextTick(() => {
          this.$refs.modal.hide()
        });
        this.isLoading = false;
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });
    },
    // 分類表單刪除 //
    remove (row) {
      this.isLoading = true;
      this.tableData.filter(item => (row.id !== item.id));
      this.strconfigname = this.tableData[row.id].name;
      backendapi.delete(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap/${this.strconfigname}`).then((response) => {
        this.reloadd1();
        console.log("before");
        this.isLoading = false;
        console.log("after");
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });
    },
    // 刪除步驟 //
    checkCancel(row) { 
      swal({
          title: this.$t('service.CFGMAP_DIAL_CFGMAPDEL_MSG'),
          text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: this.$t('common.BTN_CONFIRM'),
          cancelButtonText: this.$t('common.BTN_CANCEL'),
          heightAuto: false
      }).then((result) => {
          if(result.value){
            this.remove(row);
            swal({
                title: this.$t('common.MSG_DEL_SUCCESS'),
                confirmButtonText: this.$t('common.BTN_OK'),
                type: "success",
                heightAuto: false
            });
          } 
      });
    },
  },

 destroyed: function() {
    console.log('configure timer destroyed');
    clearInterval(this.timer1);
  }
}
</script>

<style>
#createConfigure .modal-xl {
  max-width: 900px;
}

#createConfigure .help {
    display: block;
    font-size: 0.75rem;
    margin-top: 0.25rem;
}

#createConfigure .help.is-danger{
    color: #ff3860;
}
#configure  fieldset:last-child{
  margin-bottom: 0;
}
</style>


