<template>
  <div>
      <table class="table">
        <thead class="text-center">
          <tr>
            <th scope="col">{{$t('service.PRJ_LST_PRJ_NAME')}}</th>
            <th scope="col">{{$t('service.ACNT_LST_ROLE')}}</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item,index) in projectList" class="text-center">
            <td>{{item['project-name']}}</td>
            <td>{{item['project-role']}}</td>
          </tr>
        </tbody>
      </table>
  </div>
</template>

<script>
import Vue from 'vue';
export default {
  props:["userid"],
  components: {
  },
  data(){
    return{
      projectList:[],
    };
  },
  created(){
    backendapi.get(`/user/${this.userid}/project-list`).then((response) => {
      this.projectList = response.data['project-list']
    })
    .catch(error => {
      alert(error.message);
    });
  }
}
</script>
    




