<template>
  <div>
    <nav class="navbar navbar-dark fixed-top bg-dark flex-nowrap p-0 shadow" style="height:64px">
      <a class="navbar-brand col-sm-3 col-md-2 mr-0" href="#" @click.prevent="toCenter()">
        <img src="static/image/logo_en.png" style="max-height: 46px;"/>
      </a>
        <!-- <input class="form-control form-control-dark w-100" type="text" placeholder="Search" aria-label="Search"> -->  
      <ul id="navbar1" class="navbar-nav px-1" style="margin-block-start: 0em; margin-block-end: 0em;">     
        <li class="nav-item text-nowrap px-2">      
          <b-dropdown class="langBtn"  variant="dark" :text="selectedLanguage" >
            <b-dropdown-item v-for="language in languages" :value="language.value" :key="language.value" @click="changeLanguage(language.value)">{{language.text}}</b-dropdown-item>
          </b-dropdown>
        </li>  
          <b-dropdown id="dropdown-right" class="nav-item text-nowrap langBtn pull-right" right variant="dark" toggle-class="text-decoration-none">
            <template v-slot:button-content>
              <i class="fas fa-user-cog"></i>
            </template>
            <b-dropdown-group :header="name">
              <b-dropdown-item  v-b-modal.modifyPassword>{{$t('navbar.CHANGE_PWD')}}</b-dropdown-item>
              <b-dropdown-item  @click.prevent="logout()">{{$t('navbar.LOGOUT')}}</b-dropdown-item>
            </b-dropdown-group>
          </b-dropdown>
      </ul>
    </nav>
    <b-modal class="modal fade" id="modifyPassword" :title="$t('navbar.CHANGE_PWD')" ref="modal" @show="resetModal" @hidden="resetModal">
      <form ref="form">
        <div class="form-group">
          <label for="userName">{{$t('navbar.OLD_PWD')}}</label>
          <b-form-input type="password" id="oldPassword" :placeholder="$t('navbar.HINT_INPUT_OLD_PWD')" v-model="oldPassword" required></b-form-input>
        </div>
        <div class="form-group">
          <label for="password">{{$t('navbar.NEW_PWD')}}</label>
          <b-form-input type="password" id="newPassword" v-model="newPassword" :placeholder="$t('navbar.HINT_INPUT_NEW_PWD')" required></b-form-input>
        </div>
        <div class="form-group">
          <label for="password">{{$t('navbar.CONFIRM_PWD')}}</label>
          <b-form-input type="password" id="newPasswordAgain" v-model="newPasswordAgain" :placeholder="$t('navbar.HINT_CONFIRM_PWD')" required></b-form-input>
        </div>
      </form>
      <template v-slot:modal-footer="{ok, cancel}">
        <b-button size="sm" variant="success" @click="okk()">{{$t("service.ACNT_DIAL_NACNT_BTN_SUBMIT")}}</b-button>
        <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.ACNT_DIAL_NACNT_BTN_CANCEL")}}</b-button>
      </template>
    </b-modal>
  </div>  
</template>


<script>
import swal from 'sweetalert2'
export default {
  name: 'App',
  data(){
      return{
        name:"",
        oldPassword:'',
        newPassword:'',
        newPasswordAgain:'',
        languages: [
          {
            "value": "tw",
            "text": "繁體中文"
          },
          {
            "value": "ch",
            "text": "简体中文"
          },
          {
            "value": "en",
            "text": "English"
          }
        ],
        selectedLanguage:  "繁體中文"
    };
  },
  mounted(){
    this.name = localStorage.getItem('name')
    var curLocale = null;
    if(localStorage.getItem('language')){
      curLocale = localStorage.getItem('language');
    }else{
      // For logout situation, localStorage would be cleared
      curLocale = this.$i18n.locale;
    }
    console.log("Navbar locale", curLocale);
    this.changeLanguage(curLocale);

  },
  methods: {
    changeLanguage(language) {
      if(language == "tw"){
         localStorage.setItem("language", 'tw');
        this.$i18n.locale = 'tw'
        this.selectedLanguage = "繁體中文";
        if (document.getElementById('section-container') != null) {
          document.getElementById('section-container').style.marginLeft = "";
          document.getElementById('section-container').style.removeProperty('marginLeft');
          document.querySelector('.aside-inner').style.width = "222px";
        }
      }else if(language == "ch"){
        localStorage.setItem("language", 'ch');
        this.$i18n.locale = 'ch'
        this.selectedLanguage = "简体中文";
        if (document.getElementById('section-container') != null) {
          document.getElementById('section-container').style.marginLeft = "";
          document.getElementById('section-container').style.removeProperty('margin-left');
          document.querySelector('.aside-inner').style.width = "222px";
        }
      }else if(language == "en"){
        localStorage.setItem("language", 'en');
        this.$i18n.locale = 'en';
        this.selectedLanguage = "English";
        if (document.getElementById('section-container') != null) {
          document.getElementById('section-container').style.marginLeft = "262px";
          document.querySelector('.aside-inner').style.width = "262px";
        }
      }else{
        localStorage.setItem("language", 'tw');
        this.$i18n.locale = 'tw'
        this.selectedLanguage = "繁體中文";
        if (document.getElementById('section-container') != null) {
          document.getElementById('section-container').style.marginLeft = "";
          document.getElementById('section-container').style.removeProperty('margin-left');
          document.querySelector('.aside-inner').style.width = "222px";
        }
      }
    },
    toCenter() {
          localStorage.setItem("current-type", '');
          this.$router.push({
              path: "/center",
          });      
    },
    // 預設值 //
    resetModal() {
      this.oldPassword = ''
      this.newPassword = ''
      this.newPasswordAgain = ''
    },
    okk(e){
      this.oldPassword.trim();
      this.newPassword.trim();
      this.newPasswordAgain.trim();
      if(this.oldPassword==''){
        alert(this.$t('navbar.ALERT_NO_OLD_PWD'))
      }else if(this.newPassword==''){
        alert(this.$t('navbar.ALERT_NO_NEW_PWD'))
      }else if(this.newPasswordAgain==''){
        alert(this.$t('navbar.ALERT_NO_CONFIRM_PWD'))
      }else if(this.newPassword!==this.newPasswordAgain){
        alert(this.$t('navbar.ALERT_CONFIRM_PWD_FAIL'))
      }else{
        this.submit()
      }
    },
    submit(){
      backendapi.put(`/user`,{
        current_password: this.oldPassword, 
        new_password: this.newPasswordAgain
      }).then((response) => {
        this.$nextTick(() => {
          this.$refs.modal.hide()
          swal({
              title: this.$t('navbar.PWD_MODIFY_SUCCESS'),
              confirmButtonText: this.$t('common.BTN_OK'),
              type: "success",
              heightAuto: false
          });
        });
      })
      .catch(error => {
        this.$nextTick(() => {
          this.$refs.modal.hide()
          swal({
              title: this.$t('common.PWD_MODIFY_FAIL'),
              confirmButtonText: this.$t('common.BTN_OK'),
              type: "error",
              heightAuto: false
          });
        });
      }); 
    },
    logout(){
      console.log("=====", this.$i18n.locale);
      localStorage.removeItem('token');
      localStorage.removeItem('clusterid');
      localStorage.clear()

      this.$router.push('/login');
    },

  }
};
</script>
<style>
#navbar1.navbar-nav {
  flex-direction: row;
}
.langBtn > button{
  box-shadow:none;
  -webkit-box-shadow:none;
}
.dropdown-header {
  padding: .5rem 1.2rem;
}
</style>


