      },

      nfsObject:{
        name:'',
        mountPath:'',
        nfs:{
          path:'',
          server:''
        }
      },
      strategy:"",
      scale: 1,
      max_replicas: 5,
      min_replicas: 1,
      target_cpu: 30,

      isPort: false,

      loadbalancer: [
        "LEAST_CONN",
        "ROUND_ROBIN",
        "RANDOM"
      ],
      iscircuitbreaker:true,
      project: "",
      namespace: "",
      openPort:0,
      demo: {
        name: '',
        image: '',
        ports:{
        },
        environment:{
        },
        environmentFrom: [
        ],
        labels:{
        },
        volumeMounts:[
        ],
        volumes:[
        ],
        limitsCpu: "200",
        limitsMemory: "128",
        requestsCpu: "200",
        requestsMemory: "128",
        loadbalancer:"",
        circuitbreaker:"",
        port_list:[],
        connecttimeout: 10000,
	      maxconnections: 100
      },
    };
  },

  mounted(){
    backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/service/${this.strservicename}`).then((response) => {
      this.demo.name = response.data.name + "-copy"
      this.demo.image = response.data.image

      if (response.data.ports !== null){
        var ports = Object.entries(response.data.ports);
        for(var i = 0; i < ports.length; i++) { 
          let obj = {
            key:'',
            value:''
          };
          obj['key'] = "port" + ports[i][0]
          obj['value'] = ports[i][1].containerPort
          this.ports.push(obj)
        }
      }  

      if (response.data.environment !== null){
        var en = Object.entries(response.data.environment);
        for(var i = 0; i < en.length; i++) { 
          let obj = {
            key:'',
            value:''
          };
          obj['key'] = en[i][0]
          obj['value'] = en[i][1]
          this.environment.push(obj)
        }
      }

      if (response.data.environmentFrom !== null){
        this.demo.environmentFrom = response.data.environmentFrom
      }
      
      if (response.data.labels !== null||response.data.hasOwnProperty('labels')){
        // delete response.data.labels['app']
        // delete response.data.labels['version']
        // delete response.data.labels['workload.user.cattle.io/workloadselector']
        var lb = Object.entries(response.data.labels);
        for(var i = 0; i < lb.length; i++) { 
          let obj = {
            key:'',
            value:''
          };
          obj['key'] = lb[i][0]
          obj['value'] = lb[i][1]
          this.labels.push(obj)     
        }
      } 

      if (response.data.volumes !== null){
        this.demo.volumes = response.data.volumes
        for(var i = 0; i < this.demo.volumes.length; i++) {
          delete this.demo.volumes[i]['type']
          if (response.data.volumes[i].hasOwnProperty('configMap')){
            delete this.demo.volumes[i]['configMap']['defaultMode']
            delete this.demo.volumes[i]['configMap']['type']
          }else if (response.data.volumes[i].hasOwnProperty('nfs')){
            delete this.demo.volumes[i]['nfs']['readOnly']
            delete this.demo.volumes[i]['nfs']['type']
          }else if (response.data.volumes[i].hasOwnProperty('persistentVolumeClaim')){
            delete this.demo.volumes[i]['persistentVolumeClaim']['type']
          }  
        }
      }

      if (response.data.volumeMounts !== null){
        this.demo.volumeMounts = response.data.volumeMounts
        for(var i = 0; i < this.demo.volumeMounts.length; i++) {
          delete this.demo.volumeMounts[i]['readOnly']
          delete this.demo.volumeMounts[i]['type'] 
        }
      }
      this.demo.volumes =  this.demo.volumes.map((item, i) => Object.assign({}, item, response.data.volumeMounts[i]));
      console.log(this.demo.volumes)

      if (response.data.scale !== null){
        this.scale = response.data.scale
        this.strategy = "1"
      }
      if (response.data.target_cpu !== null && response.data.hasOwnProperty('target_cpu')){
        this.target_cpu = response.data.target_cpu
        this.strategy = "2"
      }
      if (response.data.min_replicas !== null && response.data.hasOwnProperty('min_replicas')){
        this.min_replicas = response.data.min_replicas
        this.strategy = "2"
      }
      if (response.data.max_replicas !== null && response.data.hasOwnProperty('max_replicas')){
        this.max_replicas = response.data.max_replicas
        this.strategy = "2"
      }

      if (response.data.resources.hasOwnProperty('limits')){
        var a = response.data.resources.limits.cpu.replace("m","");
        this.demo.limitsCpu = a
      }
      if (response.data.resources.hasOwnProperty('requests')){
        var b = response.data.resources.requests.cpu.replace("m","");
        this.demo.requestsCpu = b
      }
      if (response.data.resources.hasOwnProperty('limits')){
        var c = response.data.resources.limits.memory.replace(/Mi|M/,"");
        this.demo.limitsMemory = c
      }
      if (response.data.resources.hasOwnProperty('requests')){
        var d = response.data.resources.requests.memory.replace(/Mi|M/,"");
        this.demo.requestsMemory = d
      }

      if (response.data.hasOwnProperty('port_list')){
        this.demo.port_list = response.data.port_list.map(item => [...item].slice(1));
        this.isPort = true;
      }

      this.demo.loadbalancer = response.data.loadbalancer;

      if(response.data.circuitbreaker=="enabled"){
        this.iscircuitbreaker = true;
        this.demo.circuitbreaker="enabled"
      }else if(response.data.circuitbreaker=="disabled"){
        this.iscircuitbreaker = false;
        this.demo.circuitbreaker="disabled"
      }
    
      this.isLoading = false;
    })
    .catch(error => {
      alert(error.message);
      this.isLoading = false;
    });
    backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap`).then((response) => {
      response.data.map((item, index) => {
        this.configList.push(item['name'])
      });
      this.configSelect = []
      response.data.map((item, index) => {
        this.configSelect.push(item)
      });

      this.isLoading = false;
    })
    .catch(error => {
      alert(error.message);
      this.isLoading = false;
    });
    backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/persistentvolumeclaim`).then((response) => {
      this.pvcSelect = []
      response.data.map((item, index) => {
        this.pvcSelect.push(item)
      });
    })
    .catch(error => {
      alert(error.message);
    });
  },

  created(){
    backendapi.get("/project").then((response) => {
      this.projectlist2 = response.data;
    })
    .catch(error => {
       console.log(error)
    });
  },

  methods:{
    selectedproject() {
      var projectlist2 = document.getElementById("projectlist2");
      this.strProject2 = projectlist2.options[projectlist2.selectedIndex].value;
      this.project = this.strProject2
      this.isLoading = true;
      backendapi.get(`/project/${this.strProject2}/namespace`).then((response) => {
          this.namespacelist2 = response.data
          this.selected = true
          this.showNamespace2 = false,
          this.showProject2 = true,
          this.isLoading = false;
      })
      .catch(error => {
        console.log(error)
      });
    },

    selectednamespace() {
      var namespacelist2 = document.getElementById("namespacelist2");
      this.strNamespace2 = namespacelist2.options[namespacelist2.selectedIndex].value;
      this.namespace = this.strNamespace2
      backendapi.get(`/project/${this.strProject2}/namespace/${this.strNamespace2}/service`).then((response) => {
        this.showProject2 = false
        this.showNamespace2 = true
      })
      .catch(error => {
        console.log(error)
      });
    },


    // 基本訊息必填驗證 //
    beforeTabSwitch(){
      if(this.demo.name.match(/[A-Z]|[\u4e00-\u9fa5]/) || this.demo.image.match(/[A-Z]|[\u4e00-\u9fa5]/)){
        return false;
      };
      if(this.demo.name.match(/[a-z]|\d|"|_/g) && this.demo.image.match(/[a-z]|\d|"|_/g)){
        return true;
      };
    },
    // 基本訊息增加端口 //
    addport(){
      const newPort = {
        key:"port",
        value: ""
      }
      this.ports.push(newPort);
    },
    // 基本訊息刪除端口 //
    removePorts (index) {
      this.ports.map((item, index) => {
        item['id'] = index
        return item
      });  
      if (this.ports.length <= 1) {
        return
      }else{
        var outIndex = this.ports.filter(item => (this.ports[index].id === item.id));
        this.ports.splice(outIndex[0].id, 1)
      }  
    },    
    // 環境變數增加變量 //
    addrule(){
      const newRule = {
        key:"",
        value: ""
      }
      this.environment.push(newRule);
    },
    // 基本訊息刪除變量 //
    removeEnvironment (index) {
      this.environment.map((item, index) => {
        item['id'] = index
        return item
      });  
      if (this.environment.length <= 1) {
        return
      }else{
        var outIndex = this.environment.filter(item => (this.environment[index].id === item.id));
        this.environment.splice(outIndex[0].id, 1)
      }  
    },
    // 環境變數匯入配置項 //
    addconfig(){
      const newConfig = {
        source: "configMap",
        sourceName: ""
      }
      this.demo.environmentFrom.push(newConfig);
    },
    // 環境變數刪除配置項 //
    removeConfig(index){
      this.demo.environmentFrom.map((item, index) => {
        item['id'] = index
        return item
      });  
      if (this.demo.environmentFrom.length <= 1) {
        return
      }else{
        var outIndex = this.demo.environmentFrom.filter(item => (this.demo.environmentFrom[index].id === item.id));
        this.demo.environmentFrom.splice(outIndex[0].id, 1)
      }
    },
    // 標籤變量 //
    addlabel(){
      const newLabel = {
        key:"",
        value: ""
      }
      this.labels.push(newLabel);
    },
    // 移除標籤變量 //
    removelabel(index){
      this.labels.map((item, index) => {
        item['id'] = index
        return item
      });  
      if (this.labels.length <= 1) {
        return
      }else{
        var outIndex = this.labels.filter(item => (this.labels[index].id === item.id));
        this.labels.splice(outIndex[0].id, 1)
      }
    },
    // 數據卷 新增雲硬碟 //
    pushvolpvc(){
      var a = document.getElementById("pvcpvc");
      const pvcVol = {
        name: this.pvcObject.name,
        mountPath: this.pvcObject.mountPath,
        persistentVolumeClaim: {
          persistentVolumeClaimId: a.options[a.selectedIndex].value,
          readOnly: false
        }
      }
      const pvcMounts ={
        name: this.pvcObject.name,
        mountPath: this.pvcObject.mountPath
      }
      this.demo.volumes.push(pvcVol)
      this.demo.volumeMounts.push(pvcMounts)
      this.pvcObject = {
        name:'',
        mountPath:'',
        persistentVolumeClaim:{
          persistentVolumeClaimId:'',
          readOnly:false
        }
      }
    },
    // 數據卷 新增配置項 //
    pushvolconfig(){
      var b = document.getElementById("configconfig");
      const configVol = {
        name: this.configObject.name,
        mountPath: this.configObject.mountPath,
        configMap: {
          name: b.options[b.selectedIndex].value,
        }
      }
      const configMounts = {
        name: this.configObject.name,
        mountPath: this.configObject.mountPath
      }


      this.demo.volumes.push(configVol)
      this.demo.volumeMounts.push(configMounts)

      this.configObject = {
        name:'',
        mountPath:'',
        configMap:{
          name:'',
        }
      }

    },

    // 數據卷 新增nfs //
    pushvolnfs(){
      const nfsVol = {
        name: this.nfsObject.name,
        mountPath: this.nfsObject.mountPath,
        nfs: {
          path:this.nfsObject.nfs.path,
          server:this.nfsObject.nfs.server
        }
      }
      const nfsMounts = {
        name: this.nfsObject.name,
        mountPath: this.nfsObject.mountPath
      }


      this.demo.volumes.push(nfsVol)
      this.demo.volumeMounts.push(nfsMounts)

      this.nfsObject = {
        name:'',
        mountPath:'',
        nfs:{
          path:'',
          server:''
        }
      }
    },
    // 刪除 數據卷 //
    removeVolumes(index){
      this.demo.volumes.map((item, index) => {
        item['id'] = index
        return item
      });
      this.demo.volumeMounts.map((item, index) => {
        item['id'] = index
        return item
      });
      if (this.demo.volumes <= 1) {
        return
      }else{
        var outIndex = this.demo.volumes.filter(item => (this.demo.volumes[index].id === item.id));
        this.demo.volumes.splice(outIndex[0].id, 1)
        var outIndex2 = this.demo.volumeMounts.filter(item => (this.demo.volumeMounts[index].id === item.id));
        this.demo.volumeMounts.splice(outIndex2[0].id, 1)
      }
    },
    // 新增開放端口 //
    pushPort(){
      var aaa = [parseInt(this.openPort)]
      this.demo.port_list.push(aaa)
    },
    // 刪除開放端口 //
    removePort (index) {
      var outIndex = this.demo.port_list.filter((item,index) => (this.demo.port_list[index] === item[index]));
      this.demo.port_list.splice(outIndex, 1)
    },
    // 負載均衡Select //
    loadbalanceChange() {
      console.log(this.demo.loadbalancer)
    },
    // 熔斷機制 //
    breakerChange(){
      if(document.querySelector('input[name="break"]:checked')){
        this.demo.circuitbreaker="enabled"
      }else{
        this.demo.circuitbreaker="disabled"
      }
    },
    submitEnvironment(){
      for (var i = 0; i < this.environment.length; i++) {
        this.demo.environment[this.environment[i].key] = this.environment[i].value;
      }
      for(var x = 0; x < this.demo.environmentFrom; x++) { 
        delete this.demo.environmentFrom[x].optional   
        delete this.demo.environmentFrom[x].prefix
        delete this.demo.environmentFrom[x].type
      }      
      for (var i = 0; i < this.labels.length; i++) {
        this.demo.labels[this.labels[i].key] = this.labels[i].value;
      }
      // this.demo.labels["workload.user.cattle.io/workloadselector"] = "deployment-" + this.strNamespace + "-" + this.strservicename  

      for (var i = 0; i < this.demo.volumes.length; i++) {
        delete this.demo.volumes[i]["mountPath"];
      }      
      if(this.strategy == "1"){
          this.demo.scale = parseInt(this.scale)
      }
      if(this.strategy == "2"){
        this.demo.scale = 1
        this.demo.max_replicas= parseInt(this.max_replicas)
        this.demo.min_replicas= parseInt(this.min_replicas)
        this.demo.target_cpu = parseInt(this.target_cpu)
      }

      this.demo.limitsCpu = this.demo.limitsCpu + "m"
      this.demo.limitsMemory = this.demo.limitsMemory + "Mi"
      this.demo.requestsCpu = this.demo.requestsCpu + "m"
      this.demo.requestsMemory = this.demo.requestsMemory + "Mi"

      if(document.querySelector('input[name="switchPort"]').checked){
        this.demo.port_list = this.demo.port_list.map(item => [...item, ...item]);
      }else{
        this.demo.port_list = []
      }
      this.go();
    },
    go(){
      backendapi.post(`/project/${this.project}/namespace/${this.namespace}/service/`,this.demo).then((response) => {
        this.$emit('childMethod');
      })
      .catch(error => {
        alert(error.message)
        this.$emit('childMethod');
      });
      console.log(this.demo)
    }
  }
}
</script>
    
<style src="vue-form-wizard/dist/vue-form-wizard.min.css"></style>

<style>
.vue-form-wizard .wizard-nav-pills>li>a>span {
    margin-left: 0rem;
    text-align: center;
    width: 100%;
}
.vue-form-wizard .wizard-nav-pills>li>a {
    padding: .5rem !important;
    border-radius: 7px;
}
.vue-form-wizard.md .wizard-icon-circle {
    font-size: 15px;
}
.vue-form-wizard.md .wizard-nav-pills>li.active>a .wizard-icon {
    font-size: 15px;
}
.vue-form-wizard .wizard-nav-pills>li>a>span>div {
    font-weight:bold;
}
.cloudDrive, .NFS, .config, .cloudDrive:last-child, .NFS:last-child, .config:last-child{
  padding: 20px;
  border: 1px dashed #eee;
  margin-bottom: 20px;
}
.icon-new_service{
  border:none;
  box-shadow:none;
}
.tab-content{
  border-width: 0 0 0 1px;
}

.form-group h4{
  text-align: center;
}
.deployform .form-inline{
  margin: 10px 0;
}
.deployform input{
  margin: 0px 5px;
}
.deployform fieldset{
  padding: 5px 0;
  margin: 0;
}
fieldset:last-child{
  margin-bottom: 0;
}

span.s_comment{
  display: block;
  font-size: 13px;
  margin-top: 10px;
}

.must_write{
  color:red;
  font-size: 13px;
  margin-left: 5px;
}

.input_tooltip{
    padding: 0px;
    box-shadow: none;
    border: none;
}
.input_tooltip i{
    font-size: 24px;
    color:red;
}
</style>



